<template>
  <div id="app">
    <el-table
      :data="tableData" size="mini"
      style="width: 100%">
      <el-table-column
        prop="id"
        label=""
        align="center">
      </el-table-column>
      <el-table-column prop="countryName" label="" align="center">
      </el-table-column>
      <el-table-column label="/" align="center">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.autoFlag==1?'':''" placement="top">
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.autoFlag == 1 ? 1 : 0"
              :switchData=1
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="0"
              @changeSwitch="changeStatus(scope.row)"
              perms="type-settlementSettingList-on/off"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.autoFlag == 0 && scope.row.settlementTime">{{scope.row.settlementTime}}</span>
          <span v-else>---</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="300" align="center">
        <template slot-scope="scope">
          <one-button v-if="scope.row.autoFlag == 0" size="mini" @click="openSettlement(scope.row)" perms="type-settlementSettingList-settlement"></one-button>
          <span v-else>---</span>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="settlementVisible"
      width="68%">
      <el-form :model="settlementForm" :rules="settlementFormRules" ref="settlementForm" label-width="100px" label-position="top" class="manual-settle-cls">
        <el-form-item prop="settlementTime">
          <template slot="label">
            
            <span style="color: #e9994b; font-size: 10px">  </span>
          </template>
          <el-date-picker
            v-model="settlementForm.settlementTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            :picker-options="pickerBeginHandle"
            type="datetime"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-for="item in cpInfoOptions" :key="item.appId" v-model="item.checked">{{item.label}}</el-checkbox>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="settlementVisible = false"> </el-button>
        <el-button type="primary" @click="handleSettlement"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getAll,
  changeAutoStatus,
  submitSettlement
} from "../../api/settlementSetting"
import {
  queryCpInfoByCountrySelectList
} from "../../api/cpinfo"
import OneButton from "../../components/cButton";
import OneSwitch from "../../components/cSwitch";

export default {
  name: "settlementSetting",
  components: {OneSwitch, OneButton},
  data() {
    return {
      tableData: [],
      settlementVisible: false,
      settlementForm: {
        checkedCpInfo: []
      },
      settlementFormRules: {
        settlementTime: [
          {required: true, message: '', trigger: 'blur'},
        ]
      },
      pickerBeginHandle: {
        disabledDate(date) {
          return date.getTime() > Date.now() - 2.16e7;
        }
      },
      cpInfoOptions: []
    }
  },
  async created() {
    this.queryAllList()
  },
  methods: {
    queryAllList() {
      let that = this
      getAll({}).then(function (response) {
        that.fullscreenLoading = false
        if (response.data.code === 0) {
          response.data.data.forEach(item => {
            item.autoFlag = item.autoFlag == true ? 1 : 0
          })
          that.tableData = response.data.data
        }
      })
    },
    changeStatus(row) {
      var that = this
      this.$confirm("" + row.countryName + "" + (row.autoFlag == 1 ? "" : "") + "", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let that = this
        changeAutoStatus({
          id: row.id,
          status: row.autoFlag === 1 ? false : true
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.queryAllList()
        })
      }).catch(() => {
        that.queryAllList()
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    openSettlement(row) {
      this.settlementForm = {}
      this.settlementForm.id = row.id
      this.settlementForm.autoFlag = row.autoFlag
      this.settlementVisible = true
      this.queryCpInfoByCountrySelectList(row.country)
    },
    handleSettlement() {
      if (this.settlementForm.autoFlag === 0) {
        let that = this
        this.$refs['settlementForm'].validate((valid) => {
          if (valid) {
            this.settlementForm.checkedCpInfo = []
            this.cpInfoOptions.map((item) => {
              if (item.checked) {
                this.settlementForm.checkedCpInfo.push(item.value)
              }
            })
            submitSettlement(this.settlementForm).then(function (response) {
              if (response.data.code === 0) {
                that.$message({
                  message: '',
                  type: 'success'
                })
                that.settlementVisible = false
              } else {
                that.$message.error('');
              }
              that.queryAllList()
            })
          }
        })
      } else {
        that.queryAllList()
        this.$message({
          message: row.countryName + '',
          type: 'warning'
        });
      }
    },
    queryCpInfoByCountrySelectList(country) {
      let that = this
      queryCpInfoByCountrySelectList({country: country, status: 1}).then(function (res) {
        that.cpInfoOptions = []
        if (res.data.code === 0) {
          res.data.data.forEach((item, index) => {
            that.cpInfoOptions.push({
              label: item.appId + ' ' + item.userName,
              value: item.appId,
              checked: false
            })
          })
        }
      })
    },
  }
}
</script>

<style>
.manual-settle-cls .el-checkbox__label {
  width: 120px;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
  position: relative;
  top: 5px;
}
</style>



