<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="id">
        <el-input v-model="searchForm.id" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker
            v-model="createTime"
            type="daterange"
            :clearable="true"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd"
            placeholder=""
            :picker-options="pickerOptionsStartCreate">
          </el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
        <el-button @click="resetForm('searchForm')"></el-button>
        <el-button type="primary" @click="openCommand()"></el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="">
      </el-table-column>
      <el-table-column
        prop="operator"
        label="">
      </el-table-column>
      <el-table-column
        prop="command"
        label="">
      </el-table-column>
      <el-table-column
        prop="descr"
        label="" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
        prop="response"
        label="" :show-overflow-tooltip="true">
      </el-table-column>

      <el-table-column
        prop="createTime"
        label=""
        :formatter="dateFormat"
      >
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="page.cur"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
    <!-- Form -->
    <el-dialog :title="dialog.title" :modal="false" :visible.sync="dialog.visible" width="40%" @closed="closeDialog">
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules">
        <el-form-item label=":" label-width="90px" prop="command">
          <el-input v-model="dialog.form.command" placeholder=" ,,, movie"></el-input>
        </el-form-item>
        <el-form-item label=":" label-width="90px" prop="descr">
          <el-input v-model="dialog.form.descr" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" label-width="90px" prop="response">
          <el-input v-model="dialog.form.response" type="textarea" :rows="3" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {addUser, changeStatus, deleteUser, queryCommandPageList, updateUser} from "../../api/user";
  import {queryRoleSelectList} from "../../api/role";
  import {getAllCommand, addCommand, updateCommand, deleteCommand} from "../../api/massMsg";

  export default {
    name: "commandList",
    data() {
      return {
        page: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        createTime: [],
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.pickerOptionsStartCreate) {
              // 
              var startTime = new Date(this.pickerOptionsStartCreate).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        fullscreenLoading: false,
        searchForm: {
          id: '',
          limit: 10,
          startCreateTime: '',
          endCreateTime: ''
        },
        total: 0,
        dialog: {
          title: '',
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {
            id: '',
            command: '',
            descr: '',
            response: ''
          },
          rules: {
            command: [
              {required: true, message: '', trigger: 'blur'},
            ],
            descr: [
              {required: true, message: '', trigger: 'blur'},
            ],
            response: [
              {required: true, message: '', trigger: 'blur'},
            ]
          }
        },
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          appId: 0,
          payFix: 0,
          payRate: 0,
          withdrawFix: 0,
          withdrawRate: 0,
          balanceForm: {
            password: '',
            Verifycode: '',
            remain: 0,
            Notes: ''
          },
          creditForm: {
            password: '',
            Verifycode: '',
            remain: '0',
          },
          agentForm: {
            UserName: ''
          },
          typeForm: {
            payType: 0,
            withdrawType: 0,
          },

          rules: {},
          payTypeOptions: [],
          withdrawTypeOptions: [],
        },
        tableData: []
      }
    },
    created() {
      this.queryCommandPageList()
    },
    methods: {
      dateFormat(row, column) {
        let date = row[column.property];
        if (date === undefined || date == null) {
          return ''
        }
        return this.dateFormate(date)
      },
      dateFormate(date) {
        if (date) {
          date = new Date(date)
        } else {
          return ''
        }
        var Y = date.getFullYear();
        var M = date.getMonth() + 1;
        M = M < 10 ? '0' + M : M;// 0
        var D = date.getDate();
        D = D < 10 ? '0' + D : D;
        var H = date.getHours();
        H = H < 10 ? '0' + H : H;
        var Mi = date.getMinutes();
        Mi = Mi < 10 ? '0' + Mi : Mi;
        var S = date.getSeconds();
        S = S < 10 ? '0' + S : S;
        // return Y + '-' + M + '-' + D + ' ' + H + ':' + Mi + ':' + S;
        return Y + '-' + M + '-' + D;
      },
      queryCommandPageList() {
        let that = this
        that.searchForm.startCreateTime = this.createTime === "" ? "" : this.createTime[0]
        that.searchForm.endCreateTime = this.createTime === "" ? "" : this.createTime[1]
        getAllCommand(
          {
            'page': that.page.cur,
            'limit': that.page.pageSize,
            'id': that.searchForm.id,
            'startDate': that.searchForm.startCreateTime,
            'endDate': that.searchForm.endCreateTime
          }
        ).then(function (response) {
          if (response.data.code === 0) {
            that.tableData = response.data.data
            that.page.total = response.data.count
          }
        })
      },
      refresh() {
        this.queryCommandPageList();
      },
      pageChange(cur) {
        this.page.cur = cur
        this.queryCommandPageList()
      },
      search() {
        this.queryCommandPageList()
      },
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (formName === 'ruleForm') {
              that.saveCommand()
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveCommand() {
        let str = this.dialog.isInsert ? '' : ''
        let that = this
        this.$confirm('' + str + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          (this.dialog.isInsert ? addCommand(that.dialog.form) : updateCommand(that.dialog.form)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: str + '',
                type: 'success'
              })
              that.queryCommandPageList()
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.closeDialog()
          })
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.createTime = []
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      commonCloseDialog(obj, formName) {
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        this.resetForm(formName)
      },
      handleEdit(index, row) {
        let that = this
        Object.keys(row).forEach(key => {
          if (that.dialog.form.hasOwnProperty(key)) {
            that.dialog.form[key] = row[key]
          }
        })
        this.dialog.title = ''
        this.dialog.form.id = row.id
        this.dialog.form.command = row.command
        this.dialog.form.descr = row.descr
        this.dialog.form.response = row.response
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteCommand({
            ids: row.id
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.queryCommandPageList()
          })
        })

      },
      changeStatus(row) {
        let that = this
        changeStatus({
          id: row.userId,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeSettings(row) {
        this.settings.visible = true
        if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
          let DefaultPayTypeId = row.DefaultPayTypeId.split("")
          let payTypes = [];
          for (let i = 0; i < DefaultPayTypeId.length; i++) {
            payTypes.push(DefaultPayTypeId[i] + "")
          }
          this.settings.typeForm.payType = payTypes
        } else {
          this.settings.typeForm.payType = []
        }

        if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
          let defaultWithdrawTypeId = row.DefaultWithdrawTypeId.split("")
          let withdrawTypes = [];
          for (let i = 0; i < defaultWithdrawTypeId.length; i++) {
            withdrawTypes.push(defaultWithdrawTypeId[i] + "")
          }
          this.settings.typeForm.withdrawType = withdrawTypes
        } else {
          this.settings.typeForm.withdrawType = []
        }
        this.settings.userName = row.UserName
        this.settings.appId = row.appId
        this.settings.payFeeFix = row.payFeeFix
        this.settings.payFeeRate = row.payFeeRate
        this.settings.withdrawFeeFix = row.withdrawFeeFix
        this.settings.withdrawFeeRate = row.withdrawFeeRate
      },
      closeSettingsDialog() {
        this.settings.activeName = 'first'
        this.settings.visible = false
        this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
        this.commonCloseDialog(this.settings.creditForm, 'creditForm')
        this.commonCloseDialog(this.settings.agentForm, 'agentForm')
        this.commonCloseDialog(this.settings.typeForm, 'typeForm')
      },
      queryRoleSelectList() {
        let that = this
        return new Promise(resolve => {
          queryRoleSelectList().then(function (res) {
            if (res.data.code === 0) {
              that.roleOptions = []
              res.data.data.forEach((item, index) => {
                that.roleOptions.push(
                  {
                    "label": item.rolename,
                    "value": item.id
                  }
                )

              })
            }
          })
        })
      },
      openCommand() {
        this.dialog.title = ''
        this.dialog.visible = true
      },
      handleSizeChange(val) {
        this.page.pageSize = val
        this.refresh()
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
    }
  }
</script>

<style scoped>

</style>



