import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function addChannelPayType(datas) {
  return Axios({
    url: "/channelPayType/addChannelPayType",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}



