<template>
  <div id="app" style="">
    <!--            form-->
    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="UTR" prop="transaction_id">
            <el-input v-model="searchForm.transaction_id" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="/" prop="type">
            <el-select v-model="searchForm.type" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="sync_status">
            <el-select v-model="searchForm.sync_status" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="payTypeId">
            <el-select v-model="searchForm.payTypeId" placeholder="" filterable>
              <el-option label="" value=""></el-option>
              <el-option
                v-for="(item) in this.payTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="12" :lg="12" :md="12" :sm="24" :xs="24" class="form-search">
          <el-form-item label="">
            <el-col :span="11">
              <el-date-picker
                v-model="grabTime"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder=""
                :picker-options="pickerOptionsStartCreate">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item class="search-button">
        <el-button size="medium" icon="el-icon-search" type="primary" @click="search"></el-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <!--            -->
    <div id="grid" style="width: 100%">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="small">
        <el-table-column
          label=""
          align="center"
          width="120"
        >
          <template slot-scope="scope">
            <span >{{scope.row.grab_time.split(' ')[0]}}</span>
            <p>
              <span >{{scope.row.grab_time.split(' ')[1]}}</span>
            </p>
          </template>
        </el-table-column>
        <el-table-column
          prop="payTypeName"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          width="100"
        >
          <template slot-scope="scope">
            <span v-if="scope.row.type === 1" class="settlement-status settlement-status-success"></span>
            <span v-if="scope.row.type === 0" class="settlement-status settlement-status-reject"></span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
        >
          <template slot-scope="scope">
            {{ scope.row.amount }}
          </template>
        </el-table-column>
        <el-table-column
          prop="transaction_id"
          label="UTR"
          align="center"
          width="160"

        >
        </el-table-column>
        <el-table-column
          prop="desc"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          align="center"
          width="110"

        >
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          width="120"

        >
          <template slot-scope="scope">
            <span v-if="scope.row.sync_status === ''"
                  class="settlement-status settlement-status-success"
            >{{ scope.row.sync_status }}</span>
            <span v-else class="settlement-status settlement-status-initial">{{ scope.row.sync_status }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="balance"
          label=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          width="110"
        >
          <template slot-scope="scope" v-if="scope.row.type === 0">
            <span v-if="scope.row.notify_flag === 0" class="settlement-status settlement-status-initial"></span>
            <span v-if="scope.row.notify_flag === 1" class="settlement-status settlement-status-success"></span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="text-align: center;margin-top:5px;height: 100%">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="searchForm.page"
        :page-size="searchForm.limit"
        :page-sizes="[10, 20, 50, 100, 200]"
        layout="total, sizes, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import {matchPayLog, queryBankRecordPageList} from "../../api/bankRecord";
import {toDecimalString} from "../../statics/custom";
import {queryPayTypeSelectList} from "../../api/paytype";

export default {
  name: "BankRecordList",
  data() {
    return {
      fullscreenLoading: false,
      centerDialogVisible: false,
      innerVisible: false,
      direction: 'rtl',
      grabTime: [],
      payTypeOptions: [],
      searchForm: {
        id: null,
        type: null,
        payTypeId: null,
        transaction_id: '',
        payment_type: '',
        sync_status: null,
        bank_type: null,
        startGrabTime: '',
        endGrabTime: '',
        page: 1,
        limit: 10,
        rowIndex: 0
      },
      tableData: [],
      total: 0,
      pickerOptionsStartCreate: {},
      pickerOptionsEndCreate: {
        disabledDate(time) {
          if (this.searchForm.startGrabTime) {
            // 
            var startGrabTime = new Date(this.searchForm.startGrabTime).valueOf() - 8.64e7 + 1;
          } else {
            var startGrabTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startGrabTime
        }
      },
      formLabelWidth: 120,
      matching: false,
      searchStatistics: {
        needPayAmount: 0,
        payAmount: 0,
        payFee: 0,
        payNum: 0
      }
    }
  },
  created() {
    this.queryBankRecordPageList()
    this.searchFormOri = this.searchForm
    this.queryAllPaytypeList()

  },
  methods: {
    queryBankRecordPageList() {
      this.searchForm.startGrabTime = this.grabTime === "" ? "" : this.grabTime[0]
      this.searchForm.endGrabTime = this.grabTime === "" ? "" : this.grabTime[1]

      const that = this;
      that.fullscreenLoading = true

      return new Promise(resolve => {
        queryBankRecordPageList(that.searchForm).then(function (response) {
          if (response.data.code === 0) {
            const tableData = [];

            const statistics = response.data.data.statistics;
            const list = response.data.data.list;

            for (const bankRecord of list) {
              bankRecord.sync_status = bankRecord.sync_status === 1 ? '' : ''
              bankRecord.amount = toDecimalString(Number(bankRecord.amount) * 100)
              bankRecord.balance = toDecimalString(Number(bankRecord.balance) * 100)

              tableData.push(bankRecord)
            }
            that.tableData = tableData
            that.total = statistics.count
            that.fullscreenLoading = false
          }
        })
      });
    },
    search() {
      this.queryBankRecordPageList()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.grabTime = []
    },
    pageChange(cur) {
      this.searchForm.page = cur
      this.queryBankRecordPageList()
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true
      this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    handleClose(done) {
      done();
    },
    handleSizeChange(val) {
      this.searchForm.limit = val
      this.queryAllPayLog()
    },
    queryAllPaytypeList() {
      var that = this
      queryPayTypeSelectList().then(function (res) {
        if (res.data.code === 0) {
          that.payTypeOptions = []
          var data = res.data.data
          for (var paytype of data) {
            that.payTypeOptions.push({
              label: paytype.name,
              value: paytype.id
            })
          }
        }
      })
    },
  }
}
</script>

<style scoped>

</style>



