import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function updateRolePermission(datas) {
  return Axios({
    url: "/rolepermission/updateRolePermission",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}




