<template>
  <div id="app" style="">
    <!--            form-->
    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row >
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="id">
            <el-input v-model="searchForm.id" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="orderId">
            <el-input v-model="searchForm.orderId" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="transactionNum">
            <el-input v-model="searchForm.transactionNum" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="status">
            <el-select v-model="searchForm.status" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
              <el-option label="" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="status1">
            <el-select v-model="searchForm.status1" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
              <el-option label="" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="appId">
            <el-input v-model="searchForm.appId" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="payTypeId">
            <el-select v-model="searchForm.payTypeId" placeholder="" filterable>
              <el-option label="" value=""></el-option>
              <el-option
                v-for="(item) in this.payTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="country">
            <el-select v-model="searchForm.country" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option
                v-for="(item) in this.countryOptions"
                :key="item.label"
                :label="item.label"
                :value="item.value">
                <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="syncCnt">
            <el-select v-model="searchForm.syncCnt" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
              <el-option label="" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
<!--        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search" :prop="false">-->
<!--          <el-form-item label="clabe" prop="clabe"  >-->
<!--            <el-input v-model="searchForm.clabe" placeholder="" type="textarea"  :rows="3"></el-input>-->
<!--          </el-form-item>-->
<!--        </el-col>-->
<!--        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">-->
<!--          <el-form-item label="Rastreo" prop="rastreo">-->
<!--            <el-input v-model="searchForm.rastreo" placeholder=""></el-input>-->
<!--          </el-form-item>-->
<!--        </el-col>-->
        <el-col :xl="3" :lg="5" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="amount">
            <el-input v-model="searchForm.amount" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="12" :lg="10" :md="12" :sm="24" :xs="24" class="form-search">
          <el-form-item label="">
            <el-col :span="11">
              <el-date-picker
                v-model="createTime"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder=""
                :picker-options="pickerOptionsStartCreate">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :xl="12" :lg="12" :md="12" :sm="24" :xs="24" class="form-search">
          <el-form-item label="">
            <el-col :span="11">
              <el-date-picker
                v-model="payTime"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder=""
                :picker-options="pickerOptionsStartPay">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item class="search-button">
        <one-button size="medium" icon="el-icon-search" type="primary" @click="search" perms="log-payLogList-search"></one-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <!--            -->
    <div style="margin-left: 30px">
      <el-row :gutter="20">
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statistics-span">
                            {{searchStatistics.needPayAmount}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statistics-span">
                            {{searchStatistics.payAmount}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statistics-span">
                            {{searchStatistics.payFee}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statistics-span">
                            {{searchStatistics.payNum}}
                        </span>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" style="margin-top: 10px">
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statistics-span">
                            {{searchStatistics.rate}}
                        </span>
          </div>
        </el-col>
      </el-row>
    </div>
    <!--            -->
    <div id="grid" style="width: 100%">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="small">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.validStatus }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.validDay }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.callbackUrl }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.notifyUrl }}</span>
              </el-form-item>
              <el-form-item label="Ip">
                <span>{{ props.row.ip }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.description }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="id"
          label="">
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="">
        </el-table-column>
        <el-table-column
          prop="transactionNum"
          label="">
        </el-table-column>
        <el-table-column
          prop="appId"
          label="Id">
        </el-table-column>
        <el-table-column
          prop="amount"
          label="">
        </el-table-column>
        <el-table-column
          prop="platformFee"
          label="">
        </el-table-column>
        <el-table-column
          prop="thirdFee"
          label="">
        </el-table-column>
        <el-table-column
          prop="payTypeName"
          label="">
        </el-table-column>
        <el-table-column
          prop="type"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
                    <span v-if="scope.row.status  === ''"
                          style="color:red;font-weight: bolder">{{scope.row.status}}</span>
            <span v-else style="color:#606972">{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
                    <span v-if="scope.row.status1  === ''"
                          style="color:red;font-weight: bolder">{{scope.row.status1}}</span>
            <span v-else style="color:#606972">{{scope.row.status1}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="syncCnt"
          label="">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="">
        </el-table-column>
        <el-table-column
          prop="payTime"
          label="">
        </el-table-column>

<!--        <el-table-column  -->
<!--          prop="clabe"-->
<!--          label="clabe" :row-style="{ height: '100px' }">-->
<!--        </el-table-column>-->
<!--        <el-table-column-->
<!--          prop="rastreo"-->
<!--          label="rastreo">-->

<!--        </el-table-column>-->
        <el-table-column
          prop="ip"
          label="ip">
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <one-button v-if="(scope.row.status === '' )" type="danger"
                       round
                       size="mini"
                       @click="changeStatus(scope)"
                       perms="log-payLogList-receive"
            >
            </one-button>
            <el-button  v-if="(scope.row.status === '' && (scope.row.payTypeId === 101 || scope.row.payTypeId === 102))" type="danger"
                       round
                       size="mini"
                       @click="openMatchUtrDrawer(scope)">
            </el-button>
            <one-button :disabled="(scope.row.status === '' )"
                       type="success"
                       round
                       size="mini"
                       @click="openSendNotifyMsgDrawer(scope.row)"
                       @change=""
                        perms="log-payLogList-notify"
            >
            </one-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--            -->
    <!--            <div style="text-align: center;margin-top:5px;">-->
    <!--                <el-pagination-->
    <!--                        background-->
    <!--                        layout="total, prev, pager, next"-->
    <!--                        :total="total" :current-page="searchForm.page" @current-change="pageChange">-->
    <!--                </el-pagination>-->
    <!--            </div>-->
    <div style="text-align: center;margin-top:5px;height: 100%">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="searchForm.page"
        :page-size="searchForm.limit"
        :page-sizes="[10, 20, 50, 100, 200]"
        layout="total, sizes, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <!--            -->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <el-dialog
        width="30%"
        title=""
        :visible.sync="innerVisible"
        append-to-body>
        <el-input v-model="verifyCode" placeholder=""></el-input>
        <el-button @click="innerVisible = false"> </el-button>
        <el-button type="primary" @click="checkVerifyCode()"> </el-button>
      </el-dialog>
      <el-input v-model="password" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false"> </el-button>
    <el-button type="primary" @click="checkPassword()"> </el-button>
  </span>
    </el-dialog>
    <el-drawer
      :visible.sync="sendNotifyDrawerVisable"
      :direction="direction"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :show-close="false"
      custom-class="demo-drawer"
      style="overflow-y: auto"
      size="50%">
      <div class="" style="padding: 20px">
        <el-form>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.id" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.url" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.notifyData" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" style="resize: none" readonly :rows="12"
                      v-model="sendNotifyMsgForm.notifyResp"
                      autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input type="textarea" style="resize: none" readonly :rows="3"
                      v-model="sendNotifyMsgForm.notifyAnalyze"
                      autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button :disable="sending" type="success" @click="sendNotifyMsg(sendNotifyMsgForm.id)">{{sending
            ?
            '...' : ''}}
          </el-button>
          <el-button @click="cancelNotifyMsgDrawer()"> </el-button>
        </div>
      </div>
    </el-drawer>
    <el-drawer
      :visible.sync="matchUtrDrawerVisible"
      :direction="direction"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :show-close="false"
      custom-class="demo-drawer"
      style="overflow-y: auto"
      size="50%">
      <div class="" style="padding: 20px">
        <el-form>
          <el-form-item label="UTR" :label-width="formLabelWidth">
            <el-input v-model="matchUtrForm.rastreo" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button :disable="matching" type="success" @click="matchUtr()">{{matching
            ?
            '...' : ''}}
          </el-button>
          <el-button @click="closeMatchUtrDrawer()"> </el-button>
        </div>
      </div>
    </el-drawer>

  </div>
</template>

<script>
  import {changePayLogStatus, matchUtr, queryPayLogPageList, sendNotifyMsg} from "../../api/paylog";
  import {queryCountryPageList} from "../../api/dictionary";
  import {queryPayTypeSelectList} from "../../api/paytype";
  import {checkPassword, checkVerifyCode} from "../../api/user";
  import {formatPayType, formatAmount, dateFormate, toDecimalString} from "../../statics/custom"
  import OneButton from "../../components/cButton";


  export default {
    name: "payLogList",
    components: {OneButton},
    data() {
      return {
        fullscreenLoading: false,
        centerDialogVisible: false,
        innerVisible: false,
        sendNotifyDrawerVisable: false,
        matchUtrDrawerVisible: false,
        direction: 'rtl',
        password: '',
        verifyCode: '',
        notifyId: '',
        createTime: [],
        payTime: [],
        countryOptions: [],
        payTypeOptions: [],
        searchFormOri: {},
        searchForm: {
          id: '',
          appId: '',
          orderId: '',
          transactionNum: '',
          payTypeId: '',
          type: '',
          status: '1',
          status1: '',
          syncCnt: '',
          startCreateTime: '',
          endCreateTime: '',
          startPayTime: '',
          endPayTime: '',
          country: '',
          clabe: '',
          rastreo: '',
          amount: '',
          page: 1,
          limit: 10,
          rowindex: 0
        },
        matchUtrForm: {
          id: '',
          rastreo: ''
        },
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {},
          settings: {
            visible: false,
            activeName: 'first',
            userName: '',
            appId: 0,
            payFix: 0,
            payRate: 0,
            withdrawFix: 0,
            withdrawRate: 0,
            balanceForm: {
              password: '',
              Verifycode: '',
              remain: 0,
              Notes: ''
            }
          },
          rules: {
            UserName: [
              {required: true, message: '', trigger: 'blur'},
              {min: 3, max: 50, message: ' 3  50 ', trigger: 'blur'}
            ]
          },
        },
        tableData: [],
        total: 0,
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.searchForm.startCreateTime) {
              // 
              var startCreateTime = new Date(this.searchForm.startCreateTime).valueOf() - 8.64e7 + 1;
            } else {
              var startCreateTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startCreateTime
          }
        },
        pickerOptionsStartPay: {},
        pickerOptionsEndPay: {
          disabledDate(time) {
            if (this.searchForm.startPayTime) {
              // 
              var startPayTime = new Date(this.searchForm.startPayTime).valueOf() - 8.64e7 + 1;
            } else {
              var startPayTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startPayTime
          }
        },
        notifyBtnSwitch: 'disabled',
        sendNotifyMsgForm: {
          id: '',
          url: '',
          notifyData: '',
          notifyResp: '',
          notifyAnalyze: ''
        },
        formLabelWidth: 120,
        sending: false,
        searchStatistics: {
          needPayAmount: 0,
          payAmount: 0,
          payFee: 0,
          payNum: 0
        },
        matching: false
      }
    },
    created() {
      this.searchFormOri = this.searchForm
      this.queryAllPayLog()
      this.queryCountryPageList()
      this.queryAllPaytypeList()
    },
    methods: {
      queryAllPayLog() {
        const that = this;
        that.fullscreenLoading = true
        this.searchForm.startCreateTime = this.createTime === "" ? "" : this.createTime[0]
        this.searchForm.endCreateTime = this.createTime === "" ? "" : this.createTime[1]
        this.searchForm.startPayTime = this.payTime === "" ? "" : this.payTime[0]
        this.searchForm.endPayTime = this.payTime === "" ? "" : this.payTime[1]

        return new Promise(resolve => {

          queryPayLogPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {
              const tableData = [];

              const statistics = response.data.data.statistics;
              const list = response.data.data.list;

              for (const paylog of list) {

                paylog.type = formatPayType(paylog)

                paylog.amount = toDecimalString(paylog.amount)
                paylog.platformFee = toDecimalString(paylog.platformFee)
                paylog.thirdFee = toDecimalString(paylog.thirdFee)

                let status = paylog.status;
                status = status === 1 ? '' : status === 0 ? '' : ''
                paylog.status = status
                let status1 = paylog.status1;
                status1 = status1 === 1 ? '' : status1 === 0 ? '' : ''
                paylog.status1 = status1

                let syncCnt = paylog.syncCnt;
                syncCnt = syncCnt === 1 ? '' : syncCnt === 0 ? '' : '' + (syncCnt * -1) + ''
                paylog.syncCnt = syncCnt

                let validStatus = paylog.validStatus;
                validStatus = validStatus === 1 ? '' : ''
                paylog.validStatus = validStatus

                paylog.createTime = dateFormate(paylog.createTime)
                paylog.payTime = dateFormate(paylog.payTime)

                tableData.push(paylog)
              }

              var needPayAmount = statistics.needPayAmount;
              var payAmount = statistics.payAmount;
              var payFee = statistics.payFee;
              var payNum = statistics.payNum;
              var allPayNum = statistics.allPayNum;
              if ( needPayAmount !== null){
                that.searchStatistics.needPayAmount = toDecimalString(needPayAmount)
              }

              if ( payAmount !== null){
                that.searchStatistics.payAmount = toDecimalString(payAmount)
              }

              if ( payFee !== null){
                that.searchStatistics.payFee = toDecimalString(payFee)
              }

              that.searchStatistics.payNum = payNum
              if (Number(allPayNum) !== 0){
                that.searchStatistics.rate = Math.floor(Number(payNum)/Number(allPayNum)*100).toFixed(2) + '%'
              }else {
                that.searchStatistics.rate = '0'
              }

              that.tableData = tableData
              that.total = allPayNum


                that.fullscreenLoading = false
            }
          })

        });
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      queryAllPaytypeList() {
        var that = this
        queryPayTypeSelectList().then(function (res) {
          if (res.data.code === 0) {
            that.payTypeOptions = []
            var data = res.data.data
            for (var paytype of data) {
              that.payTypeOptions.push({
                label: paytype.name,
                value: paytype.id
              })
            }
          }
        })
      },
      search() {
        this.queryAllPayLog()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.createTime = []
        this.payTime = []
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryAllPayLog()
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      changeStatus(data) {
        var that = this
        this.centerDialogVisible = true
        that.notifyId = data.row.id
      },
      checkPassword() {
        var that = this
        checkPassword({password: that.password}).then(function (response) {
          if (response.data.code === 0) {
            that.innerVisible = true
          } else {
            that.$message("CHECK PSSWORD FAIL")
          }
        })
      },
      checkVerifyCode() {
        var that = this
        checkVerifyCode({verifyCode: that.verifyCode}).then(function (response) {
          if (response.data.code === 0) {
            changePayLogStatus({id: that.notifyId}).then(function (response) {
              console.log(response.data)
              if (response.data.code === 0) {
                that.$message(response.data.msg)
                that.notifyId = ''
                that.centerDialogVisible = false
                that.innerVisible = false
                that.queryAllPayLog()
              }
            })
          } else {
            that.$message("VERIFY FAIL")
          }
        })
      },
      handleClose(done) {
        done();
      },
      handleSizeChange(val) {
        this.searchForm.limit = val
        this.queryAllPayLog()
      },
      openSendNotifyMsgDrawer(data) {
        const that = this;
        sendNotifyMsg({id: data.id}).then(function (response) {
          that.$message(response.data.msg)
          if (response.data.code === 0 || response.data.code === -1) {
            that.queryAllPayLog()
          }
          that.sending = false
        })
      },
      openMatchUtrDrawer(scope) {
        const currency = scope.row.currency
        if (currency === 'INR') {
          this.matchUtrForm.id=scope.row.id

          this.matchUtrDrawerVisible = true
        }
      },
      closeMatchUtrDrawer() {
        this.matchUtrForm.id = ''
        this.matchUtrForm.utr = ''
        this.matchUtrDrawerVisible = false
      },
      matchUtr() {
        const that = this
        matchUtr(this.matchUtrForm).then((response) => {
          const data = response.data
          const code = data.code
          if (code === 0) {
            that.closeMatchUtrDrawer()
            that.queryAllPayLog()
          }
          that.$message(data.msg)
        })

      }
    }
  }
</script>

<style >
.show-statistics-span{
  color: #909399;
  font-size: 15px;
  font-weight: bold;
}
</style>



