import Axios from "@/utils/request"  // axios

export function getMenuList(datas) {
    return Axios({
        url: "/permission/getMenuList",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //form
        },
        data: datas
    })
}





