import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function settlementPayLog(datas) {
  return Axios({
    url: "/paylog/settlementPayLog",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

