import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function getUndealOrder(datas) {
  return Axios({
    url: "/workOrder/getUndealOrder",
    method: "post",
    data: datas
  })
}

export function getOrderDetailByGroupId(datas) {
  return Axios({
    url: "/workOrder/getOrderDetailByGroupId",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function replyOrder(datas) {
  return Axios({
    url: "/workOrder/replyOrder",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(datas)
  })
}

export function replyImgOrder(datas) {
  return Axios({
    url: "/workOrder/replyImgOrder",
    method: "post",
    data: datas
  })
}

export function getImg(datas) {
  return Axios({
    url: "/telegrambot/getImg",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function getFilePath(fileId) {
  return Axios({
    url: "https://api.telegram.org/bot5302362125:AAE7TkE4Yr-cDJBI004OMviQvTU0y71NNhM/getFile?file_id=" + fileId,
    method: "get",
  })
}





