import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function addRole(datas) {
  return Axios({
    url: "/role/addRole",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function deleteRole(datas) {
  return Axios({
    url: "/role/deleteRole",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

