import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function getAll() {
  return Axios({
    url: "/settlementSetting/queryAllList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    }
  })
}

export function changeAutoStatus(datas) {
  return Axios({
    url: "/settlementSetting/changeStatus",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function submitSettlement(datas) {
  return Axios({
    url: "/settlementSetting/settlement",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}



