<template>
  <div id="app" ref="app" :style="heightClass">
    <transition name="el-fade-in-linear">
      <el-container>
        <el-aside :width="asideWidth" ref="elaside">
          <el-scrollbar style="width: 100%;height: 100%;overflow-x: hidden">
            <div class="top-info" v-if="!isCollapse">
              <el-row :span="24">
                <el-col :span="24">
                  <div class="top-info__image">
                    <img src="@/assets/top1.png" style="width: 5rem;" alt="">
                  </div>
                </el-col>
              </el-row>
            </div>
            <el-menu :collapse-transition="false" default-active="1" :collapse="isCollapse">
              <el-menu-item index="0" @click="getIframeSrc('./homeNew.html')">
                <i class="el-icon-menu"></i>
                <span slot="title" @click="addTabs({ id: 1, permissName: '', pValue: 'home' })"></span>
              </el-menu-item>

              <el-submenu class="submenu-hover" v-for="item in iframe" :index="item.permissName" :key="item.id">
                <template slot="title">
                  <i slot="title" :class="item.pIcon"></i>
                  <span slot="title">{{item.permissName}}</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item v-for="itemson in item.children" :index="itemson.permissName"
                                :key="itemson.id" @click="addTabs(itemson)">
                    {{itemson.permissName}}
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
            </el-menu>
          </el-scrollbar>
        </el-aside>

        <el-container>
          <el-header>
            <div class="page-nav-header">
              <div class="header__left" @click="ShowNavMenu">
                <i v-if="isCollapse" class="el-icon-s-unfold" @click="asideWidth = '12rem'"></i>
                <i v-else class="el-icon-s-fold" @click="asideWidth = '4rem'"></i>
              </div>
              <div class="toolbar">
                <el-dropdown>
                  <el-icon style="margin-right: 8px; margin-top: 1px"
                  >
                    <setting
                    />
                  </el-icon>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item>View</el-dropdown-item>
                      <el-dropdown-item>Add</el-dropdown-item>
                      <el-dropdown-item>Delete</el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
                <!--                <span style="color:#f03e41;">{{ .username }}</span>-->
                <el-link style="margin-left: 10px" @click="checkoutLogin"></el-link>
              </div>
            </div>
          </el-header>
          <el-main class="el-main" ref="elmain" style="padding-bottom: 40px">
            <el-row>
              <el-tabs v-model="tabsValues" type="card" editable @edit="handleTabsEdit">
                <el-tab-pane
                  :key="item.name"
                  v-for="(item) in editableTabs"
                  :label="item.title"
                  :name="item.name"
                >
                  <component :is="item.content"></component>
                </el-tab-pane>
                <!--              <el-tab-pane label="" name="1">-->
                <!--                <Home></Home>-->
                <!--              </el-tab-pane>-->
                <!--              <el-tab-pane label="" name="2">-->
                <!--                <PayLogList></PayLogList>-->
                <!--              </el-tab-pane>-->

              </el-tabs>
            </el-row>
            <!--                    <iframe id="iframe" :src="iframeSrc" ref="iframe"-->
            <!--                            style="width: 101%;height: 100%;overflow-x: hidden;overflow-y: hidden;border-style: none"></iframe>-->
          </el-main>
        </el-container>
      </el-container>
    </transition>
  </div>
</template>

<script>
  import {getMenuList} from '@/api'
  import Home from './home'
  import PayLogList from "./logmanage/payLogList";
  import WithdrawLogList from "./logmanage/withdrawLogList";
  import OperaLogList from "./logmanage/operaLogList.vue";
  import MoneyLogList from "./logmanage/moneyLogList.vue";
  import CpInfoList from "./cpmanage/cpInfoList.vue";
  import AgentList from "./agentmanage/agentList.vue";
  import PayTypeList from "./typemanage/payTypeList.vue";
  import WithdrawTypeList from "./typemanage/withdrawTypeList.vue";
  import CpInfoTotalStatistics from "./statisticsmanage/cpInfoTotalStatistics.vue";
  import CpInfoDailyStatistics from "./statisticsmanage/cpInfoDailyStatistics.vue";
  import UserList from "./usermanage/userList.vue";
  import RoleList from "./rolemanage/roleList.vue";
  import ChannelTotalStatistics from "./statisticsmanage/channelTotalStatistics.vue";
  import ChannelDailyStatistics from "./statisticsmanage/channelDailyStatistics.vue";
  import bankRecordList from "./logmanage/bankRecordList.vue";
  import ChannelList from "./typemanage/channelList";
  import SettlementSettingList from "./typemanage/settlementSettingList";
  import blackList from "./typemanage/blackList";
  import AlquimiaPayLogList from "./logmanage/AlquimiaPayLog";
  import settlementLogList from "./logmanage/settlementLogList";
  import rechargeList from "./logmanage/rechargeList";
  import {getSettings} from "../api/settings";
  import {logout} from "../api/user";
  import massMsg from "./telegramManage/massMsg.vue";
  import massMsgTwo from "./telegramManage/massMsgTwo.vue";
  import massMsgWithOutUser from "./telegramManage/massMsgWithOutUser.vue";
  import questionRecord from "./questionrecord/questionRecord.vue";
  import commandList from "./telegramManage/commandList.vue";
  import keywordsList from "./telegramManage/keywordsList.vue";
  import permissionList from "./permissionmanage/permissionList";
  import workorderChat from "./workorder/workorderChat.vue";
  import cButton from "../components/cButton";
  import cSwitch from "../components/cSwitch";

  export default {
    el: "#app",
    name: "index",
    components: {
      Home,
      PayLogList,
      WithdrawLogList,
      OperaLogList,
      MoneyLogList,
      CpInfoList,
      AgentList,
      PayTypeList,
      WithdrawTypeList,
      CpInfoTotalStatistics,
      CpInfoDailyStatistics,
      UserList,
      RoleList,
      ChannelTotalStatistics,
      ChannelDailyStatistics,
      ChannelList,
      SettlementSettingList,
      blackList,
      AlquimiaPayLogList,
      settlementLogList,
      rechargeList,
      massMsg,
      massMsgTwo,
      massMsgWithOutUser,
      questionRecord,
      bankRecordList,
      commandList,
      keywordsList,
      permissionList,
      cButton,
      workorderChat,
      cSwitch
    },
    created() {
      this.editableTabs = []
      this.addTabs({id: 1, permissName: '', pValue: 'home'})
      this.getPlatformSettings()
    },
    data() {
      return {
        fullscreenLoading: true,
        PlatformName: '',
        platformImg: require('@/assets/top1.png'),
        iframeSrc: './homeNew.html',
        isCollapse: false,
        iframe: [],
        clientHeight: 0,
        asideWidth: '12rem',
        tabsValues: '1',
        editableTabs: [],
        tabIndex: 2,
        heightClass: {'height': '937px'}
      }
    },
    watch: {
      //  `clientHeight` 
      clientHeight: function () {
        this.changeFixed(this.clientHeight)
      },
      editableTabs: {
        handler: function (newVal, oldVal) {
          if (newVal.length === 1 && newVal[0].name === 'home') {
            this.tabsValues = 'home'
          }
          // TO DO
        },
        deep: true,
        immediate: true
      }
    },
    mounted() {
      let that = this
      getMenuList().then(response => {
        var data = response.data.data;
        if (response.data.code === 0) {
          window.sessionStorage.setItem("permissionList", JSON.stringify(data))
          that.iframe = data
        }
      })
      // 
      that.clientHeight = `${document.documentElement.clientHeight}`          //document.body.clientWidth;
      that.$refs.elmain.$el.style.height = that.clientHeight + 'px';
      that.$refs.elaside.$el.style.height = that.clientHeight + 'px';
      window.onresize = function temp() {
        that.clientHeight = `${document.documentElement.clientHeight}`;
      }
    },
    methods: {

      changeFixed(clientHeight) {                        //
        this.$refs['elaside'].$el.style.height = clientHeight + 'px';
        this.heightClass = {'height': clientHeight + 'px'}
      },
      ShowNavMenu() {
        this.isCollapse = !this.isCollapse
      },
      getPlatformImg() {
        this.PlatformName = ''
      },
      getIframeSrc(e) {
        this.iframeSrc = e
      },
      checkoutLogin() {
        var that = this
        logout().then((resp) => {
          const code = resp.data.code
          if (code === 0) {
            localStorage.removeItem('Authorization')
            that.$router.push('/')
          }
        })
      },
      handleTabsEdit(targetName, action) {
        if (action === 'add') {
          let newTabName = ++this.tabIndex + '';
          this.editableTabs.push({
            title: 'New Tab',
            name: newTabName,
            content: ''
          });
          this.tabsValues = newTabName;
        }
        if (action === 'remove') {
          let tabs = this.editableTabs;
          let activeName = this.tabsValues;
          if (activeName === targetName) {
            tabs.forEach((tab, index) => {
              if (tab.name === targetName) {
                let nextTab = tabs[index + 1] || tabs[index - 1];
                if (nextTab) {
                  activeName = nextTab.name;
                }
              }
            });
          }
          this.tabsValues = activeName;
          this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        }
      },
      addTabs(itemson) {
        const pIcon = itemson.pIcon
        const permissName = itemson.permissName
        const pValue = itemson.pValue

        let show = false;
        if (pValue != null) {
          var newTab = (title, name, content, show) => {
            return {
              title: title,
              name: name,
              content: content,
              show: show
            }
          }

          for (const tab of this.editableTabs) {
            if (tab.name === pValue) {
              this.tabsValues = pValue
              return
            }
          }
          this.editableTabs.push(newTab(permissName, pValue, pValue, true))
        }

        this.$nextTick(() => {
          this.tabsValues = pValue
        })
      },
      isClosable(item) {
        return item.name !== 'home';
      },
      getPlatformSettings() {
        var that = this
        getSettings().then((response) => {
          that.PlatformName = response.data.data.platformName
        })
      }
    }
  }
</script>

<style scoped>

  html, body, #app, .el-container {
    border-style: none;
    /*0*/
    padding: 0;
    /**/
    margin: 0;
    /*100%*/
    height: 100%;

  }

</style>
<style>
  * {
    margin: 0;
    padding: 0;
  }

  html {
    height: 100%;
  }

  body {
    height: 100%;
  }

  #app {
    height: 100%;
  }

  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  /*  */
  .el-row {
    max-width: 100%;
    /*height: 100%;*/
  }

  .el-col-12 {
    width: 100%;
    height: 100%;
  }

  .el-menu {
    width: 180px;
    border: none;
    background-color: #333;
    color: #ffffff;
  }

  .el-menu-item {
    color: #ffffff;
  }

  /*  */
  .top-info {
    display: flex;
    padding: 10px;
    height: 100px;
    box-sizing: border-box;
  }

  .top-info .top-info__image {

  }

  .top-info .top-info-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .top-info .top-info__image .el-image {
    width: 64px;
    height: 64px;
  }

  .el-submenu__title {
    color: #ffffff;
  }

  .el-menu-item.is-active {
    background-color: rgb(240, 62, 65) !important;
  }

  .shou {
    width: 64px !important;
  }

  /*  */
  .page-nav-header {
    font-size: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header__right {
    font-size: 14px;
    display: flex;
  }


  .el-menu-item:hover {
    color: #1787FB !important;
    /* text-decoration: underline; */
  }

  .el-submenu__title:hover {
    color: #1787FB !important;
  }

  .el-tabs__nav .el-tabs__item:nth-child(1) span {
    display: none;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .demo-table-expand {
    font-size: 0;
    margin-left: 60px;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



