<template>
  <div id="app">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="channelNo">
        <el-input v-model="searchForm.channelNo" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <el-button plain @click="dialog.visible=true"></el-button>
    </el-row>
    <el-table
      :row-class-name="tableRowClassName"
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.params }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="channelNo"
        label=""
        align="center"
      >
      </el-table-column>
      <el-table-column label="" style="text-align: center"
                       align="center"
      >
        <template slot-scope="scope">
          <input v-model="scope.row.sort"
                 style="border: 1px solid rgb(141,141,141);width: 40px;height: 30px;line-height: 30px;border-radius: 8px;text-align: center"
                 @change="changeSort(scope.row)"></input>
        </template>
      </el-table-column>
      <el-table-column label="" prop="createTime" :formatter="dateFormat" align="center">
      </el-table-column>
      <el-table-column label="/" align="center">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status === 1?'':''" placement="top">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @change="changeStatus(scope.row)">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" width="300" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="openAddChannelPayTypeDrawer(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!--  -->
    <el-drawer
      :title="dialog.isInsert?'':''"
      :visible.sync="dialog.visible" @closed="closeDialog"
      direction="rtl"
    >
    </el-drawer>
    <el-drawer
      :visible.sync="channelPayTypeVisible"
      direction="rtl"
    >
      <el-form :model="channelPayTypeForm"  ref="channelPayTypeForm" label-width="150px" class="demo-ruleForm">
        <el-form-item label="">
          <el-input readonly v-model="channelPayTypeForm.channelNo"></el-input>
        </el-form-item>

          <el-form-item>
            <el-button size="medium" icon="el-icon-search" type="primary" @click="addChannelPayType('channelPayTypeForm')"></el-button>
            <!--          <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('ruleForm')"></el-button>-->
          </el-form-item>
      </el-form>
    </el-drawer>

  </div>
</template>

<script>
  import {
    addPayType,
    changePayTypeStatus,
    deletePayType,
    queryPayTypePageList,
    testPay,
    updatePayType
  } from "../../api/paytype"
  import {dateFormate} from "../../statics/custom";
  import {
    addChannel,
    deleteChannel,
    queryChannelPageList, updateChannel,
    updateIsShow,
    updateSort,
    updateStatus
  } from "../../api/channel";
  import {queryCountryPageList} from "../../api/dictionary";
  import {addChannelPayType} from "../../api/channelPayType";

  export default {
    name: "channelList",
    data() {
      return {
        fullscreenLoading: true,
        tableData: [],
        types: [],
        testPayVisible: false,
        successDialog: false,
        isDisabled: false,
        testPayUrl: '',
        testPayNumber: '',
        page: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        type: [],
        dialog: {
          isInsert: true,
          visible: false,
          form: {
            channelNo: '',
            sort: 1,
            country: ''
          }
        },
        dialogTest: {
          isInsert: true,
          visible: false,
          form: {
            appId: '',
            amount: '',
            type: '',
            payTypeId: ''
          }
        },
        searchForm: {
          channelNo: '',
          status: '',
        },
        rules: {
          out_trade_no: [
            {required: true, message: '', trigger: 'blur'},
          ],
          payUrl: [
            {required: true, message: '', trigger: 'blur'},
          ]
        },
        countryOptions: [],
        channelPayTypeVisible: false,
        channelPayTypeForm: {
          channelId: 0,
          channelNo: '',
          payTypeIds: []
        }
      }
    },
    async created() {
      this.refresh()
      this.queryCountryPageList()

    },
    methods: {
      refresh() {
        let that = this
        queryChannelPageList({
          page: this.page.cur,
          limit: this.page.pageSize,
          status: this.searchForm.status,
          channelNo: this.searchForm.channelNo,
        }).then(function (response) {
          that.fullscreenLoading = false
          if (response.data.code === 0) {
            that.tableData = response.data.data
            that.page.total = response.data.count
          }
        })
      },
      dateFormat(row, column) {
        let date = row[column.property];
        if (date === undefined || date == null) {
          return ''
        }
        return dateFormate(date)
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
      tableRowClassName({row, rowIndex}) {
        if (row.status === 1) {
          return 'success-row'
        } else {
          return 'warning-row'
        }
      },
      handleEdit(index, row) {
        this.dialog.form.id = row.id
        this.dialog.form.channelNo = row.channelNo
        this.dialog.form.sort = row.sort
        this.dialog.form.country = row.country
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          updateIsShow({
            id: row.id,
            isShow: -1
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.refresh()
          })
        })
      },
      submitForm(formName) {
        let str = this.dialog.isInsert ? '' : ''
        if (this.dialog.isInsert) {
          delete this.dialog.form.id
        }
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('' + str + '?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              (that.dialog.isInsert ? addChannel(that.dialog.form) : updateChannel(that.dialog.form)).then((response) => {
                console.log(response)

                if (response.data.code === 0) {
                  that.$message({
                    message: str + '',
                    type: 'success'
                  })
                } else {
                  that.$message.error(str + '');
                }
                that.type = []
                that.refresh()
                that.closeDialog()
              })
            }).catch(() => {
            })
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      closeDialog() {
        let obj = this.dialog.form
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        obj.stauts = -1
        this.type = []
        this.dialog.visible = false
        this.dialog.isInsert = true
        // this.resetForm('ruleForm')
      },
      search() {
        this.refresh()
      },
      sendMsg() {
        window.close()
      },
      returnPayUrl() {
        window.open(this.testPayUrl)
      },
      changeStatus(row) {
        let that = this
        console.log(row.status)

        updateStatus({
          id: row.id,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeSort(row) {
        let that = this
        updateSort({
          id: row.id,
          sort: row.sort
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )

              })
            }
          })
        })
      },
      openAddChannelPayTypeDrawer(index,row){
        this.channelPayTypeForm.channelId = row.id
        this.channelPayTypeForm.channelNo = row.channelNo
        this.channelPayTypeForm.payTypeIds = [ 1,2,3]

        this.channelPayTypeVisible = true
      },
      addChannelPayType(formName) {
        const that = this

        addChannelPayType(that.channelPayTypeForm).then((response) => {


        })
      }

    },
  }
</script>

<style scoped>

</style>



