<template>
  <div id="app">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="country">
        <el-select v-model="searchForm.country" placeholder="">
          <el-option
            v-for="(item) in this.countryOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="searchForm.type" placeholder="">
          <el-option
            v-for="(item) in this.typeOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="searchForm.txt" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <one-button size="medium" icon="el-icon-search" type="primary" @click="search" perms="user-agentList-search"></one-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <one-button plain @click="addBlack()" perms="type-blackList-add"></one-button>
    </el-row>
    <el-table
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column
        prop="countryName"
        label="">
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span class="black_type" v-if="scope.row.type === 0"></span>
          <span class="black_type" v-if="scope.row.type === 1"></span>
          <span class="black_type" v-if="scope.row.type === 2"></span>
          <span class="black_type" v-if="scope.row.type === 3">IP</span>
          <span class="black_type" v-if="scope.row.type === 4">IFSC</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="txt"
        label="">
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <!--<one-button size="mini" @click="handleEdit(scope.$index, scope.row)" perms="user-agentList-edit"></one-button>-->
          <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" perms="user-agentList-del"></one-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!-- Form -->
    <!--<el-dialog title="" :modal="false" :visible.sync="dialog.visible" width="30%" @closed="closeDialog">-->
      <!--<el-form ref="ruleForm" :model="dialog.form" status-icon :rules="settings.rules" size="mini" label-width="100px">-->
        <!--<el-form-item label="" prop="country">-->
          <!--<el-select v-model="dialog.form.country" placeholder="" filterable>-->
            <!--<el-option-->
              <!--v-for="(item) in this.countryOptions"-->
              <!--:key="item.label"-->
              <!--:label="item.label"-->
              <!--:value="item.value">-->
              <!--<span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>-->
            <!--</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" prop="type">-->
          <!--<el-select v-model="dialog.form.type" placeholder="">-->
            <!--<el-option-->
              <!--v-for="(item) in this.typeOptions"-->
              <!--:key="item.label"-->
              <!--:label="item.label"-->
              <!--:value="item.value">-->
              <!--<span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>-->
            <!--</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" prop="txt">-->
          <!--<el-input v-model="dialog.form.txt" autocomplete="new-password"></el-input>-->
        <!--</el-form-item>-->
      <!--</el-form>-->
      <!--<div slot="footer" class="dialog-footer">-->
        <!--<el-button @click="dialog.visible = false"> </el-button>-->
        <!--<el-button type="primary" @click="submitForm('ruleForm')"> </el-button>-->
      <!--</div>-->
    <!--</el-dialog>-->
    <el-drawer
      :before-close="blockCloseHandler"
      :visible.sync="dialog.visible"
      :show-close="false"
      direction="rtl"
      custom-class="black-drawer"
      ref="drawer"
      size="50%"
    >
      <template slot="title">
        <p></p>
      </template>
      <div class="drawer-container">
        <div style="height: 100%; padding: 20px;">
          <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="settings.rules" label-position="top" label-width="80px">
            <el-row>
              <el-col :span="12" style="padding: 0 10px">
                <el-form-item label="" prop="country">
                  <el-select v-model="dialog.form.country" placeholder="" filterable style="width: 100%;">
                    <el-option
                      v-for="(item) in this.countryOptions"
                      :key="item.label"
                      :label="item.label"
                      :value="item.value">
                      <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12" style="padding: 0 10px">
                <el-form-item label="" prop="type">
                  <el-select v-model="dialog.form.type" placeholder="" style="width: 100%;">
                    <el-option
                      v-for="(item) in this.typeOptions"
                      :key="item.label"
                      :label="item.label"
                      :value="item.value">
                      <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24" style="padding: 0 10px">
                <el-form-item label="(100)" prop="txt">
                  <el-input type="textarea" :rows="20" v-model="dialog.form.txt"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </div>
      <section class="drawer-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">
           
        </el-button>
      </section>
    </el-drawer>
  </div>
</template>

<script>
  import {
    addBlack,
    addAgentRemain,
    changeAgentStatus, deleteBlack,
    queryBlackPageList,
    subtractAgentRemain,
    updateBlack
  } from "../../api/black";
  import {queryCountryPageList} from "../../api/dictionary";
  import OneButton from "../../components/cButton";
  import OneSwitch from "../../components/cSwitch";

  export default {
    name: "blackList",
    components: {OneSwitch, OneButton},
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.dialog.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.dialog.form.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      let isNumber = (rule, value, callback) => {
        if (typeof value == 'number') {
          callback();
          return;
        }
        if (value == null || value.indexOf(' ') != -1) {
          callback(new Error('!'));
          return;
        }
        if (!isNaN(value)) {
          callback();
        } else {
          callback(new Error('!'));
        }
      }
      let country = (rule, value, callback) => {
        if (this.dialog.form.country !== '') {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      return {
        fullscreenLoading: true,
        searchForm: {
          country: '',
          txt: '',
          type: '',
          page: 0,
          limit: 10
        },
        tableData: [],
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {
            id: '',
            type: '',
            txt: '',
            country: ''
          }
        },
        page: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        countryOptions: [],
        typeOptions: [{
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }, {
          label: 'IP',
          value: 3
        }, {
          label: 'IFSC',
          value: 4
        }],
        agentOptions: [],
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          id: 0,
          balanceForm: {
            id: 0,
            password: '',
            verifyCode: '',
            remain: 0,
            notes: ''
          },
          rules: {
            txt: [
              {required: true, message: '', trigger: 'blur'},
            ],
            country: [
              {required: true, message: '', trigger: 'change'},
            ],
            type: [
              {required: true, message: '', trigger: 'change'}
            ]
          },
          payTypeOptions: [],
          withdrawTypeOptions: [],
        },
      }
    },
    created() {
      this.refresh()
      this.queryCountryPageList()
    },
    methods: {
      refresh() {
        var that = this
        that.searchForm.page = this.page.cur
        queryBlackPageList(that.searchForm).then(function (response) {
          that.fullscreenLoading = false
          if (response.data.code === 0) {
            that.tableData = response.data.data

            that.page.total = response.data.count
          }
        })
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
      search() {
        this.page.cur = 1
        this.refresh()
      },
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            that.saveBlack()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveBlack() {
        let str = ''
        let insert = false
        if (!this.dialog.form.id) {
          insert = true
          str = ''
        }
        let that = this
        this.$confirm('' + str + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          (insert ? addBlack(that.dialog.form) : updateBlack(that.dialog.form)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: str + '',
                type: 'success'
              })
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.refresh()
            that.closeDialog()
          })
        })
      },
      saveBalance() {
        let that = this
        that.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.balanceForm
          let add = true;
          if (data.remain < 0) {
            add = false
            console.log(data.remain)
            data.remain = Math.abs(data.remain)
          }
          data.id = that.settings.id;
          console.log(add );
          (add ? addAgentRemain(data) : subtractAgentRemain(data)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.searchForm.txt = ''
      },
      closeDialog() {
        this.dialog.visible = false
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      commonCloseDialog(obj, formName) {
        this.$nextTick(() => {
          this.$refs[formName].clearValidate()
          this.$refs[formName].resetFields()
        })
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
      },
      handleEdit(index, row) {
        this.dialog.form.id = row.id
        this.dialog.form.country = row.country
        this.dialog.form.type = row.type
        this.dialog.form.txt = row.txt
        this.dialog.visible = true
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteBlack({
            id: row.id
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.refresh()
          })
        })

      },
      changeStatus(row) {
        let that = this
        changeAgentStatus({
          id: row.id,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      addBlack() {
        this.dialog.visible = true
        this.dialog.form.id = undefined
        this.dialog.form.country = ''
        this.dialog.form.type = undefined
        this.dialog.form.txt = ''
      },
      beforeCloseDialog() {
        this.$refs.ruleForm.resetFields()
      },
      blockCloseHandler() {
        return false
      },
    }
  }
</script>

<style>
  .black-drawer #el-drawer__title {
    padding: 20px !important;
    margin-bottom: 0px;
    border-bottom: 1px solid #eee;
  }

  .black-drawer .el-col-12 {
    width: 50%;
  }

  .black-drawer .el-form-item__label {
    padding-bottom: 0 !important;
  }
</style>

<style scoped>
  .black_type {
    background-color: #409eff;
    border-color: #409eff;
    color: #ffffff;
    padding: 2px 5px;
  }

  .black-drawer .el-col-12 {
    width: 50%;
  }

  .black-drawer .el-form-item__label {
    padding-bottom: 0 !important;
  }

  .drawer-footer {
    position: absolute;
    bottom: 0;

    width: 100%;
    height: 60px;
    line-height: 60px;
    border-top: 1px solid #dedede;
    padding: 0 30px;
    text-align: right;
    overflow: hidden;
    z-index: 100;
    background: #fff;
  }
</style>



