<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="roleName">
              <el-input v-model="searchForm.roleName" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="Level" prop="roleNo">
              <el-input v-model="searchForm.roleNo" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-form-item>
            <one-button type="primary" @click="search" perms="system-roleList-search"></one-button>
            <el-button @click="resetForm('searchForm')"></el-button>
          </el-form-item>
        </el-row>
      </el-form>
      <div>
        <el-row>
          <one-button plain @click="dialog.visible=true" perms="system-roleList-add"></one-button>
        </el-row>
      </div>
      <div id="grid" style="width: 100%">
        <el-table
          :data="tableData"
          style="width: 100%"
        size="mini">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="roleName"
            label="">
          </el-table-column>
          <el-table-column
            prop="roleNo"
            label="Level">
          </el-table-column>
          <el-table-column
            prop="details"
            label="">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="">
          </el-table-column>
          <el-table-column
            prop="updateTime"
            label="">
          </el-table-column>
          <el-table-column
            label=""
            min-width="120px">
            <template slot-scope="scope">
              <one-button size="mini" @click="handleEdit(scope.$index, scope.row)" perms="system-roleList-edit"></one-button>
              <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" perms="system-roleList-del">
              </one-button>
              <one-button  size="mini"
                         @click="openRoleMenuDrawer(scope.$index, scope.row)" perms="system-roleList-roleMenu">
              </one-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total" :current-page="searchForm.page" @current-change="pageChange">
        </el-pagination>
      </div>
      <div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialog.visible" @closed="closeDialog">
          <el-dialog
            title=""
            :visible.sync="successDialog"
            :show-close=false
            :close-on-click-modal=true
            :close-on-press-escape=false
            width="30%"
            center>
        <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="successDialog = false"></el-button>
      </span>
          </el-dialog>
          <el-form :model="dialog.form" :rules="rules" ref="ruleForm" width="24rem" label-width="6rem">
            <el-form-item label="" prop="roleName">
              <el-input v-model="dialog.form.roleName"></el-input>
            </el-form-item>
            <el-form-item label="Level" prop="roleNo">
              <el-input v-model="dialog.form.roleNo"></el-input>
            </el-form-item>
            <el-form-item label="" prop="details">
              <el-input type="textarea" autosize v-model="dialog.form.details"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button size="medium" icon="el-icon-circle-plus-outline" type="primary"
                         @click="submitForm('ruleForm')">
                
              </el-button>
              <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('ruleForm')">
              </el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <div>
        <!--            -->
        <el-drawer
          title=""
          :visible.sync="showRoleMenuDrawer"
          :direction="direction"
          size="40%"
        >
          <div style="width: 100%;height: 100%;padding: 20px;">
            <template>
              <div style="width: 100%;height: 100%;">
                <el-tree
                  ref="tree"
                  :data="roleMenuData"
                  show-checkbox
                  node-key="id"
                  :props="defaultProps"
                  :default-expand-all="true"
                  :check-strictly="true"
                >
                </el-tree>
                <el-form ref="form" label-width="80px">
                  <el-form-item>
                    <el-button type="primary" @click="updateRoleMenu"></el-button>
                    <el-button></el-button>
                  </el-form-item>
                </el-form>

              </div>
            </template>
          </div>
        </el-drawer>
      </div>
    </template>
  </div>
</template>

<script>
  import {addRole, deleteRole, queryRolePageList, updateRole} from "../../api/role";
  import {getMenuListSelectByRoleId, queryPermissionTreeList} from "../../api/permission";
  import {updateRolePermission} from "../../api/rolepermission";
  import OneButton from "../../components/cButton";

  export default {
    name: "roleList",
    components: {OneButton},
    data() {
      return {
        fullscreenLoading: false,
        direction: 'rtl',
        searchForm: {
          id: null,
          roleNo: '',
          roleName: '',
          page: 1,
          limit: 10,
          rowIndex: 0
        },
        dialog: {
          isInsert: true,
          visible: false,
          form: {
            id: null,
            roleNo: '',
            roleName: '',
            details: ''
          }
        },
        showRoleMenuDrawer: false,
        roleMenuDrawerData: {
          roleId: ''
        },
        selectedMenu: [],
        successDialog: false,
        tableData: [],
        total: 0,
        dateStart: '', // 
        dateEnd: '', // 
        pickerOptionsStart: {},
        pickerOptionsEnd: {
          disabledDate(time) {
            if (this.dateStart) {
              // 
              var startTime = new Date(this.dateStart).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        notifyBtnSwitch: 'disabled',
        sendNotifyMsgForm: {
          id: '',
          url: '',
          notifyData: '',
          notifyResp: '',
          notifyAnalyze: ''
        },
        formLabelWidth: 120,
        sending: false,
        rules: {
          roleName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          roleNo: [
            {required: true, message: 'Level', trigger: 'blur'},
          ],
        },
        roleMenuData: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        roleMenuRoleNo: '',
        roleLevel: ''

      }
    },
    created() {
      this.queryRolePageList()
      this.queryRoleMenuTreeList()
    },
    methods: {
      queryRolePageList() {
        var that = this
        this.fullscreenLoading = true
        return new Promise(resolve => {
          queryRolePageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {

              var list = response.data.data
              that.total = response.data.count

              var tableData = []
              for (var role of list) {

                tableData.push(role)
              }
              that.tableData = tableData
              that.fullscreenLoading = false
            }
          })

        });
      },
      queryRoleMenuTreeList() {
        var that = this
        this.fullscreenLoading = true
        return new Promise(resolve => {
          queryPermissionTreeList().then(function (response) {
            if (response.data.code === 0) {
              var data = response.data.data

              that.$nextTick(() => {
                that.roleMenuData = data
              })
            }
          })

        });
      },
      search() {
        this.searchForm.page = 1
        this.searchForm.limit = 10
        this.queryRolePageList()
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryRolePageList()
      },
      handleEdit(index, row) {
        this.dialog.form.id = row.id
        this.dialog.form.roleNo = row.roleNo
        this.dialog.form.roleName = row.roleName
        this.dialog.form.details = row.details
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      submitForm(formName) {
        let str = this.dialog.isInsert ? '' : ''
        let urlStr = this.dialog.isInsert ? '../role/addRole' : '../role/updateRole'
        if (this.dialog.isInsert) {
          delete this.dialog.form.id
        }
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('' + str + '?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              (this.dialog.isInsert ? addRole(that.dialog.form) : updateRole(that.dialog.form)).then(function (response) {
                if (response.data.code === 0) {
                  that.$message({
                    message: str + '',
                    type: 'success'
                  })
                } else {
                  that.$message.error(str + '');
                }
                that.type = []
                that.queryRolePageList()
                that.closeDialog()
              })
            }).catch(() => {
            })
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      closeDialog() {
        let obj = this.dialog.form
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        this.dialog.visible = false
        this.dialog.isInsert = true
        // this.resetForm('ruleForm')
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteRole({
            id: row.id
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.queryRolePageList()
          })
        })
      },
      openRoleMenuDrawer(index, row) {
        this.roleMenuRoleNo = row.roleNo
        this.selectedMenu = []
        this.queryRoleMenuSelectByRoleId(row.roleNo)
        this.showRoleMenuDrawer = true
      },
      closeRoleMenuDrawer() {
        this.showRoleMenuDrawer = false
      },
      queryRoleMenuSelectByRoleId() {
        var that = this
        getMenuListSelectByRoleId({
          role: that.roleMenuRoleNo
        }).then(function (response) {
          if (response.data.code === 0) {
            var selectedMenu = []
            var data = response.data.data
            that.$nextTick(() => {
              for (const menu of data) {
                selectedMenu.push({id: menu})
              }

              that.$refs.tree.setCheckedNodes(selectedMenu);
            })

          }
        })
      },
      updateRoleMenu() {
        var checkNodes = this.$refs.tree.getCheckedKeys()
        var that = this
        updateRolePermission({
          roleNo: that.roleMenuRoleNo,
          permissionIds: checkNodes.join(',')
        }).then(function (response) {
          if (response.data.code === 0) {
            that.closeRoleMenuDrawer()
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



