import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function queryBankRecordPageList(datas) {
  return Axios({
    url: "/bankRecord/queryBankRecordPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function matchPayLog(datas) {
  return Axios({
    url: "/bankRecord/matchPayLog",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}






