<template>
  <div id="app" style="width: 100%;height: 100%;" v-bind:style="{backgroundImage:'url(' + bg + ')',
        backgroundRepeat:'no-repeat',
        backgroundSize:'100% 100%'}">
    <transition name="el-fade-in-linear">
    <el-row  class="row-bg" justify="center">
      <el-col :span="6">
        <div class="grid-content bg-purple-light login-panel">
          <el-form ref="loginForm" :model="loginForm" style="padding-top: 15%;padding-left: 20%;padding-right: 20%">
            <el-form-item label="" style="">
              <el-input v-model="loginForm.userName" style="width: 100%" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="password" v-model="loginForm.password" style="" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="loginForm.googleCode" style="" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="login" style="width: 36%;height: 32px;line-height: 32px;padding: 0"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
    </transition>
  </div>
</template>

<script>
  import {login} from '../api/user'

  export default {
    el: "#app",
    name: "login",
    created() {
    },
    data() {
      return {
        loginForm: {
          userName: '',
          password: '',
          googleCode: '',
        },
        bg: require('@/assets/sea_sky.png')

      }
    },
    methods: {
      login() {
        const that = this;

        login(that.loginForm).then((resp) => {
          const msg = resp.data.msg
          const data = resp.data.data
          that.$message(msg)

          if (resp.data.code === 0) {
            localStorage.setItem('User_Authorization',data.token)
            that.$router.push('/index')
          }
        })
      }
    }

  }
</script>

<style scoped>




  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #d3dce6;
  }

  .bg-purple-light {
  }

  .grid-content {
    min-height: 36px;
  }


  .el-form-item{
    margin-top: 5%;
  }
  .el-input__inner {
    border-radius: 11px;
  }

</style>



