import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function getSettings() {
  return Axios({
    url: "/platformSettings/getSettings",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    }
  })
}



