import Axios from "@/utils/request"  // axios


export function getAllRecord(datas) {
  return Axios({
    url: "/questionRecord/getQuestionRecordByPage",
    method: "post",
    headers: {
      'Content-Type': 'application/json' //form
    },
    data: JSON.stringify(datas)
  })
}

export function updateQuestionRecord(datas) {
  return Axios({
    url: "/questionRecord/updateQuestionRecord",
    method: "post",
    headers: {
      'Content-Type': 'application/json' //form
    },
    data: JSON.stringify(datas)
  })
}




