<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <template>
      <el-tabs v-model="active" @tab-click="handleClick">
        <el-tab-pane v-for="(item,index) in countryOptions" :key="item.value" :label="item.label" :name="item.label"
                     @click="changeList(item)">{{ item.name }}
        </el-tab-pane>
      </el-tabs>
    </template>

    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row>
        <el-col :xl="4" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="Id" prop="appId">
            <el-input v-model="searchForm.appId"></el-input>
          </el-form-item>
        </el-col>

        <el-col :xl="4" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="status">
            <el-select v-model="searchForm.status" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
              <el-option label="" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="financeStatus">
            <el-select v-model="searchForm.financeStatus" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="8" :md="8" :sm="12" :xs="24" class="form-search">
          <el-form-item label="">
            <el-col :span="11">
              <el-date-picker
                v-model="early"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder=""
                :picker-options="pickerOptionsStart">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <one-button type="primary" @click="search" perms="log-rechargeList-search"></one-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-row>

    </el-form>
    <div id="grid" style="width: 100%;margin-top: 20px">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="mini"
      >
        <!--        <el-table-column type="expand">-->
        <!--          <template #default="props">-->
        <!--            <el-form label-position="left" inline class="demo-table-expand">-->

        <!--            </el-form>-->
        <!--          </template>-->
        <!--        </el-table-column>-->
        <el-table-column
          prop="id"
          label=""
          align="center"
          min-width="90"
        >
        </el-table-column>
        <el-table-column
          prop="appId"
          label="Id"
          align="center">
        </el-table-column>
        <el-table-column
          prop="country"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="faitAmount"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="rate"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="uAmount"
          label="USDT"
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="120px"
        >
          <template #default="scope">
            <span v-if="scope.row.status  === 1" class="settlement-status settlement-status-success"></span>
            <span v-if="scope.row.status  === 0" class="settlement-status settlement-status-initial"></span>
            <span v-if="scope.row.status  === -1" class="settlement-status settlement-status-reject"></span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="120px"
        >
          <template #default="scope">
            <span v-if="scope.row.financeStatus  === 1"
                  class="settlement-status settlement-financeStatus-success"></span>
            <span v-if="scope.row.financeStatus  === 0"
                  class="settlement-status settlement-financeStatus-initial"></span>
          </template>
        </el-table-column>
        <!--        <el-table-column-->
        <!--          prop="proof"-->
        <!--          label=""-->
        <!--          align="center">-->
        <!--        </el-table-column>-->
        <el-table-column
          prop="createTime"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="finishTime"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          min-width="360px">
          <template #default=scope>
            <one-button type="primary" size="mini" @click="openRechargeLogDialog(scope.row, 1)"
                       v-show="scope.row.status === 0" perms="log-rechargeList-receive">
            </one-button>
            <one-button type="primary" size="mini" @click="openRechargeLogDialog(scope.row, -1)"
                       v-show="scope.row.status === 0" perms="log-rechargeList-reject">
            </one-button>
            <one-button type="Warning" size="mini" @click="confirmFinanceStatus(scope.row)"
                       v-show="scope.row.financeStatus === 0 && scope.row.status === 1" perms="log-rechargeList-financialConfirm">
            </one-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="total" :current-page="searchForm.page" @current-change="pageChange">
      </el-pagination>
    </div>
    <el-dialog width="68%" class="settlementLogDialog" :visible="rechargeDialogVisiable" title=""
               @close="closeRechargeLogDialog">
      <div style="padding: 10px">
        <el-form ref="rechargeForm" :model="rechargeForm" :rules="rechargeFormRules" size="small">
          <el-row>
            <el-col style="width: 40%">
              <el-form-item label="ID:" class="no-boder-input">
                <el-input readonly v-model="rechargeForm.appId"></el-input>
              </el-form-item>
            </el-col>
            <el-col :offset="1" style="width: 40%">
              <el-form-item label=":" class="no-boder-input">
                <el-input readonly v-model="rechargeForm.country"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="" prop="rechargeAmount" v-if="rechargeForm.status === 1">
            <el-input disabled v-model="rechargeForm.rechargeAmount"></el-input>
          </el-form-item>
          <el-form-item label="" prop="rate" v-if="rechargeForm.status === 1">
            <el-input v-model="rechargeForm.rate" @change="changeUSDTLabel"></el-input>
          </el-form-item>
          <el-form-item label="USDT" prop="uAmount" v-if="rechargeForm.status === 1">
            <el-input type="number" v-model="rechargeForm.uAmount"></el-input>
          </el-form-item>

          <el-form-item :label="actualAmountLabel" prop="actualAmount" v-if="rechargeForm.status === 1">
            <el-input readonly v-model="rechargeForm.actualAmount"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="rechargeForm.comment"></el-input>
          </el-form-item>
          <el-upload
            v-if="false"
            class="upload-demo"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :file-list="fileList"
            list-type="picture"
            :auto-upload="false"
            :http-request="upload"
          >
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
          </el-upload>
        </el-form>
      </div>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeRechargeLogDialog"></el-button>
        <el-button type="primary" @click="confirmRecharge(1)"
        ></el-button
        >
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {confirmRecharge, pageList, uploadProof} from "../../api/recharge";
import {toDecimalString} from "../../statics/custom";
import {queryCountryPageList} from "../../api/dictionary";
import {financeSuccess} from "../../api/recharge";
import OneButton from "../../components/cButton";

export default {
  name: "settlementLogList",
  components: {OneButton},
  data() {
    return {
      fullscreenLoading: false,
      early: [],
      searchForm: {
        status: '',
        startCreateTime: '',
        endCreateTime: '',
        startFinishTime: '',
        endFinishTime: '',
        country: '',
        page: 0,
        limit: 10
      },
      tableData: [],
      total: 0,
      rechargeDialogVisiable: false,
      cpInfoRemain: {
        remain: 0,
        frozenRemain: 0,
        actualRemain: 0,
        country: ''
      },
      rechargeForm: {
        id: '',
        appId: '',
        status: null,
        rechargeAmount: '',
        actualAmount: '',
        rate: '',
        uAmount: '',
        comment: '',
        country: ''
      },
      rechargeFormRules: {},
      formLabelWidth: '140px',
      pickerOptionsStart: {},
      pickerOptionsEnd: {
        disabledDate(time) {
          let startCreateTime = null
          if (this.dateStart) {
            // 
            startCreateTime = new Date(this.dateStart).valueOf() - 8.64e7 + 1;
          } else {
            startCreateTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startCreateTime
        }
      },
      fileList: [{
        name: 'food.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }, {
        name: 'food2.jpeg',
        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      }],
      countryOptions: [],
      country: '',
      active: ''
    }
  },
  // watch: {
  //   rechargeForm: function (form) {
  //     console.log(form)
  //
  //     let rechargeAmount = this.rechargeForm.rechargeAmount
  //     let uAmount = this.rechargeForm.uAmount
  //     this.rechargeForm.uAmount = this.rechargeForm.rechargeAmount / this.rechargeForm.rate
  //     this.rechargeForm.actualAmount = this.rechargeForm.uAmount * this.rechargeForm.rate
  //
  //   }
  // uSDTLabel: function (usdrt) {
  //   let actualAmount = this.rechargeForm.actualAmount
  //   const rate = this.rechargeForm.rate
  //   if (actualAmount === '' || rate === '') {
  //     this.rechargeForm.uAmount = 0
  //     return 'USDT'
  //   } else if (actualAmount !== '' && rate !== '') {
  //     actualAmount = actualAmount.replaceAll(",", "")
  //     this.rechargeForm.actualAmount.replaceAll(",", "")
  //
  //     this.rechargeForm.uAmount = Math.floor(actualAmount / rate)
  //     return 'USDT ( ' + this.rechargeForm.actualAmount + ' / ' + this.rechargeForm.rate + ' = ' + Math.floor(actualAmount / rate) + ')'
  //   }
  // }
  // },
  computed: {
    actualAmountLabel: function () {
      if (this.rechargeForm.rate === '' || this.rechargeForm.uAmount === '') {
        return ''
      }
      this.rechargeForm.actualAmount = Math.floor(this.rechargeForm.rate * this.rechargeForm.uAmount)
      return '(' + this.rechargeForm.uAmount + '*' + this.rechargeForm.rate + '=' + this.rechargeForm.actualAmount + ')'
    }
  },
  watch: {},
  created() {
    this.queryCountryPageList()
    this.pageList()
  },
  methods: {
    pageList() {
      const that = this
      pageList(this.searchForm).then((resp) => {
        const code = resp.data.code
        const msg = resp.data.msg
        const data = resp.data.data

        if (code === 0) {
          for (const listElement of data.list) {
            listElement.faitAmount = toDecimalString(listElement.faitAmount)
          }

          that.tableData = data.list
          that.total = data.statistics.totalAllNum
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    search() {
      this.pageList()
    },
    pageChange(cur) {
      this.searchForm.page = cur
      this.pageList()
    },
    openRechargeLogDialog(row, status) {
      this.rechargeForm.id = row.id
      this.rechargeForm.status = status
      this.rechargeForm.appId = row.appId
      this.rechargeForm.country = row.country

      if (status === 1) {
        this.rechargeForm.rechargeAmount = row.faitAmount

        this.rechargeFormRules = {
          rechargeAmount: [
            {required: true, message: '', trigger: 'blur'}
          ],
          actualAmount: [
            {required: true, message: '', trigger: 'blur'}
          ],
          uAmount: [
            {required: true, message: 'USDT', trigger: 'blur'}
          ],
          rate: [
            {required: true, message: '', trigger: 'blur'}
          ],
        }

      } else {
        this.rechargeFormRules = {}
      }
      this.$nextTick(() => {
        this.$refs.rechargeForm.clearValidate()

      })
      this.rechargeDialogVisiable = true
    },
    changeUSDTLabel() {
    },
    closeRechargeLogDialog() {
      this.$refs['rechargeForm'].resetFields()
      this.$nextTick(() => {
        this.$refs['rechargeForm'].clearValidate()
      })
      this.rechargeDialogVisiable = false
    },
    confirmRecharge() {

      this.$refs.rechargeForm.validate((valid) => {
        if (valid) {
          const that = this
          confirmRecharge(this.rechargeForm).then((resp) => {
            const code = resp.data.code
            const msg = resp.data.msg
            const data = resp.data.data

            if (code === 0) {
              that.closeRechargeLogDialog()
              that.pageList()
            }
          })
        }
      })
    },
    handleRemove(file, fileList) {
    },
    handlePreview(file) {
    },
    upload(params) {
      const files = params.files
    },
    handleClick(tab, event) {
      this.searchForm.country = this.countryOptions[tab.index].value
      this.pageList()
    },
    changeList(item) {
      this.country = item.value
    },
    queryCountryPageList() {
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    confirmFinanceStatus(row) {
      const that = this
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(()=>{
        financeSuccess({id: row.id}).then((resp) => {
          const code = resp.data.code
          const msg = resp.data.msg
          const data = resp.data.data

          if (code === 0 ){
            that.$message(msg)
            that.pageList()
          }
        })
      })
    },

  }
}
</script>

<style scoped>

.no-boder-input >>> .el-input__inner {
  border: 0;
  padding: 0;
}
</style>



