<template>
  <div id="app">
    <template>
      <el-tabs v-model="active" @tab-click="handleClick">
        <el-tab-pane v-for="(item,index) in countryOptions" :key="item.value" :label="item.label" :name="item.label"
        >{{ item.name }}
        </el-tab-pane>
      </el-tabs>
    </template>
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="name">
        <el-input v-model="searchForm.name" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="searchForm.type" multiple placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="UPI" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <one-button type="primary" @click="search" perms="type-withdrawTypeList-search"></one-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <one-button plain @click="dialog.visible=true" perms="type-withdrawTypeList-add"></one-button>
    </el-row>
    <el-table
      :row-class-name="tableRowClassName"
      :data="tableData" size="mini"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.params }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="outName" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag type="danger" size="small">{{ Number(scope.row.feeFix / 100).toFixed(2) }}+{{
              Number(scope.row.feeRate * 100).toFixed(2)
            }}%
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="sort" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.types.indexOf('1')!=-1" size="mini"></el-tag>
          <el-tag type="info" v-if="scope.row.types.indexOf('2')!=-1" size="mini"></el-tag>
          <el-tag type="success" v-if="scope.row.types.indexOf('3')!=-1" size="mini">UPI</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" prop="range">
      </el-table-column>
      <el-table-column label="" prop="worktime">
      </el-table-column>
      <el-table-column label="" prop="currency">
      </el-table-column>
      <el-table-column label="" prop="createTime" :formatter="dateFormat">
      </el-table-column>
      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'':''" placement="top">
            <!--            <el-switch-->
            <!--              v-model="scope.row.status"-->
            <!--              active-color="#13ce66"-->
            <!--              inactive-color="#ff4949"-->
            <!--              :active-value="1"-->
            <!--              :inactive-value="-1"-->
            <!--              @change="changeStatus(scope.row)">-->
            <!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.status"
              :switchData=JSON.stringify(scope.row)
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @changeSwitch="changeStatus(scope.row)"
              perms="type-withdrawTypeList-on/off"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <one-button size="mini" @click="handleEdit(scope.$index, scope.row)"
                      perms="type-withdrawTypeList-edit"></one-button>
          <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"
                      perms="type-withdrawTypeList-del"></one-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialog.visible" @closed="closeDialog">
      <el-form :model="dialog.form" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="dialog.form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="outName">
          <el-input v-model="dialog.form.outName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="params">
          <el-input type="textarea" autosize v-model="dialog.form.params"></el-input>
        </el-form-item>
        <el-form-item label="" prop="feeFix">
          <el-input v-model="dialog.form.feeFix"></el-input>
        </el-form-item>
        <el-form-item label="" prop="feeRate">
          <el-input v-model="dialog.form.feeRate"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input-number v-model="dialog.form.sort" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="type">
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="UPI" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="workTime">
          <el-input v-model="dialog.form.workTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="range">
          <el-tag type="info" v-if="!dialog.isInsert">{{ dialog.form.thirdRange }}</el-tag>
          <el-input v-model="dialog.form.range" placeholder="100-500000"></el-input>
        </el-form-item>
        <el-form-item label="" prop="thirdRange" v-if="dialog.isInsert">
          <el-input v-model="dialog.form.thirdRange" placeholder="100-500000"></el-input>
        </el-form-item>
        <el-form-item label="" prop="currency">
          <el-select v-model="dialog.form.currency" placeholder="">
            <el-option v-for="(item,index) in this.currencyOptions" :label="item.value" :value="item.value"
                       :key="item.value">{{ item.value }}
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {
  addWithdrawType, changeWithdrawTypeStatus,
  deleteWithdrawType,
  queryWithdrawTypePageList,
  updateWithdrawType
} from "../../api/withdrawtype";
import {dateFormate} from "../../statics/custom";
import {queryCountryPageList, queryCurrencyPageList} from "../../api/dictionary";
import OneButton from "../../components/cButton";
import OneSwitch from "../../components/cSwitch";

export default {
  name: "withdrawTypeList",
  components: {OneSwitch, OneButton},
  data() {
    let isNumber = (rule, value, callback) => {
      if (typeof value == 'number') {
        callback();
        return;
      }
      if (value == null || value.indexOf(' ') !== -1) {
        callback(new Error('!'));
        return;
      }
      if (!isNaN(value)) {
        callback();
      } else {
        callback(new Error('!'));
      }
    }
    return {
      active: '',
      fullscreenLoading: true,
      tableData: [],
      page: {
        pageSize: 10,
        total: 0,
        cur: 1
      },
      dialog: {
        isInsert: true,
        visible: false,
        form: {
          name: '',
          outName: '',
          params: '',
          feeFix: '',
          feeRate: '',
          sort: '',
          types: '',
          workTime: '',
          range: '',
          currency: '',
          thirdRange: ''
        }
      },
      type: [],
      searchForm: {
        name: '',
        stauts: '',
        type: [],
        types: '',
        country: ''
      },
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
        ],
        outName: [
          {required: true, message: '', trigger: 'blur'},
        ],
        params: [
          {required: true, message: '', trigger: 'blur'},
        ],
        feeFix: [
          {validator: isNumber, trigger: 'blur'},
        ],
        feeRate: [
          {validator: isNumber, trigger: 'blur'},
        ],
        // type: [
        //     {type: 'array', required: true, message: '', trigger: 'change'}
        // ],
        currency: [
          {required: true, message: '', trigger: 'change'}
        ],
        range: [
          {required: true, message: '', trigger: 'blur'},
        ],
        thirdRange: [
          {required: true, message: '', trigger: 'blur'},
        ]
      },
      countryOptions: [],
      currencyOptions: []

    }
  },
  async created() {
    this.searchForm.country = 'IND'
    this.queryCurrencyPageList()
    this.queryCountryPageList()
    await this.refresh()

  },
  methods: {
    refresh() {
      let that = this
      this.searchForm.types = this.searchForm.type.sort(function (a, b) {
        return a - b
      }).join(',')
      queryWithdrawTypePageList({
        page: this.page.cur,
        limit: this.page.pageSize,
        status: this.searchForm.status,
        name: this.searchForm.name,
        types: this.searchForm.types,
        country: this.searchForm.country
      }).then(function (response) {
        that.fullscreenLoading = false
        if (response.data.code === 0) {
          that.tableData = response.data.data
          that.page.total = response.data.count
        }
      })
    },
    dateFormat(row, column) {
      let date = row[column.property];
      if (date === undefined || date == null) {
        return ''
      }
      return dateFormate(date)
    },
    pageChange(cur) {
      this.page.cur = cur
      this.refresh()
    },
    tableRowClassName({row, rowIndex}) {
      if (row.status === 1) {
        return 'success-row'
      } else {
        return 'warning-row'
      }
    },
    handleEdit(index, row) {
      this.dialog.form.id = row.id
      this.dialog.form.name = row.name
      this.dialog.form.outName = row.outName
      this.dialog.form.params = row.params
      this.dialog.form.status = row.status
      this.dialog.form.feeRate = row.feeRate
      this.dialog.form.sort = row.sort
      this.dialog.form.workTime = row.workTime
      this.dialog.form.range = row.range
      this.dialog.form.currency = row.currency
      this.dialog.form.thirdRange = row.thirdRange
      this.dialog.form.types = row.types
      this.type = row.types.replace('1', '').replace('2', '').replace('3', 'UPI').split(',')
      this.dialog.form.feeFix = Number(row.feeFix / 100).toFixed(2)
      this.dialog.visible = true
      this.dialog.isInsert = false
    },
    handleDelete(index, row) {
      let that = this
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteWithdrawType({
          id: row.id
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      })
    },
    submitForm(formName) {
      let str = this.dialog.isInsert ? '' : ''
      if (this.dialog.isInsert) {
        delete this.dialog.form.id
      }

      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$confirm('' + str + '?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            that.dialog.form.types = that.type.toString().replace('', '1').replace('', '2').replace('UPI', '3')

            const resp = (this.dialog.isInsert ? addWithdrawType(that.dialog.form) : updateWithdrawType(that.dialog.form))
            resp.then(function (response) {
              if (response.data.code === 0) {
                that.$message({
                  message: str + '',
                  type: 'success'
                })
              } else {
                that.$message.error(str + '');
              }
              that.refresh()
              that.closeDialog()
            })
          }).catch(() => {
          })
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.type = []
    },
    closeDialog() {
      let obj = this.dialog.form
      Object.keys(obj).forEach(key => {
        let type = typeof obj[key]
        if (type === 'number') {
          obj[key] = 0
        }
        if (type === 'string') {
          obj[key] = ''
        }
      })
      obj.stauts = -1
      this.dialog.visible = false
      this.dialog.isInsert = true
      this.$refs.ruleForm.clearValidate()
      // this.resetForm('ruleForm')
    },
    search() {
      this.page.cur = 1
      this.refresh()
    },
    changeStatus(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let that = this
        changeWithdrawTypeStatus({
          id: row.id,
          status: row.status === 1 ? -1 : 1
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      }).catch(() => {
        this.refresh()
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    queryCountryPageList() {
      console.log('currency')
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    queryCurrencyPageList() {
      let that = this
      return new Promise(resolve => {
        queryCurrencyPageList({type: 'currency'}).then(function (res) {
          if (res.data.code === 0) {
            that.currencyOptions = []
            res.data.data.forEach((item, index) => {
              that.currencyOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    handleClick(tab, event) {
      this.searchForm.country = this.countryOptions[tab.index].value
      this.refresh()
    },
  }
}
</script>

<style scoped>

</style>



