<template>
  <div id="app">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="ID" prop="id">
        <el-input v-model="searchForm.id" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="country">
        <el-select v-model="searchForm.country" placeholder="">
          <el-option
            v-for="(item) in this.countryOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <one-button size="medium" icon="el-icon-search" type="primary" @click="search" perms="user-agentList-search"></one-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <one-button plain @click="addCpinfo()" perms="user-agentList-add"></one-button>
    </el-row>
    <el-table
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.userPass }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column
        prop="userName"
        label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <i class="el-icon-rs"></i>
          <span style="margin-left: 2px">{{ Number(scope.row.remain/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'':''" placement="top">
<!--            <el-switch-->
<!--              v-model="scope.row.status"-->
<!--              active-color="#13ce66"-->
<!--              inactive-color="#ff4949"-->
<!--              :active-value="1"-->
<!--              :inactive-value="-1"-->
<!--              @change="changeStatus(scope.row)">-->
<!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.status"
              :switchData=JSON.stringify(scope.row)
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @changeSwitch="changeStatus(scope.row)"
              perms="user-agentList-on/off"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column
        prop="country"
        label="">
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <one-button size="mini" @click="handleEdit(scope.$index, scope.row)" perms="user-agentList-edit"></one-button>
          <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" perms="user-agentList-del"></one-button>
          <one-button size="mini" @click="changeSettings(scope.row)" perms="user-agentList-setting"></one-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!-- Form -->
    <el-dialog title="" :modal="false" :visible.sync="dialog.visible" width="30%" @closed="closeDialog"
    >
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules" size="mini">
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="userName">
          <el-input v-model="dialog.form.userName" autocomplete="new-password"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="dialog.formLabelWidth" prop="password">
          <el-input type="password" v-model="dialog.form.password" autocomplete="new-password"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="dialog.formLabelWidth" prop="passwordNew">
          <el-input type="password" v-model="dialog.form.passwordNew" autocomplete="off"></el-input>
        </el-form-item>
        <!--            <el-form-item label="" :label-width="dialog.formLabelWidth">-->
        <!--                <el-select v-model="dialog.form.platformId" placeholder="" filterable>-->
        <!--                    <el-option-->
        <!--                            v-for="item in searchForm.platformOptions"-->
        <!--                            :key="item.id"-->
        <!--                            :label="item.platformName"-->
        <!--                            :value="item.id">-->
        <!--                        <span style="color: #8492a6; font-size: 13px">{{ item.platformName }}</span>-->
        <!--                    </el-option>-->
        <!--                </el-select>-->
        <!--            </el-form-item>-->
        <el-form-item v-if="this.dialog.isInsert" label="" :label-width="dialog.formLabelWidth"
                      prop="checkCountry">
          <el-select v-model="dialog.form.country" placeholder="" filterable>
            <el-option
              v-for="(item) in this.countryOptions"
              :key="item.label"
              :label="item.label"
              :value="item.value">
              <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :modal="false" :visible.sync="settings.visible" width="27rem" @closed="closeSettingsDialog">
      <el-tabs v-model="settings.activeName" type="card" @tab-click="">
        <el-tab-pane label="" name="first">
          <el-form :model="settings.balanceForm" :rules="settings.rules" ref="balanceForm" label-width="100px">
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="settings.balanceForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="verifyCode">
              <el-input type="password" v-model="settings.balanceForm.verifyCode"></el-input>
            </el-form-item>
            <el-form-item label="(-/+)" prop="remain">
              <el-col :span="10">
                <el-input v-model="settings.balanceForm.remain"></el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="notes">
              <el-input v-model="settings.balanceForm.notes"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('balanceForm')"></el-button>
              <el-button @click="resetForm('balanceForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
  import {
    addAgent,
    addAgentRemain,
    changeAgentStatus, deleteAgent,
    queryAgentPageList,
    subtractAgentRemain,
    updateAgent
  } from "../../api/agent";
  import {queryCountryPageList} from "../../api/dictionary";
  import OneButton from "../../components/cButton";
  import OneSwitch from "../../components/cSwitch";

  export default {
    name: "agentList",
    components: {OneSwitch, OneButton},
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.dialog.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.dialog.form.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      let isNumber = (rule, value, callback) => {
        if (typeof value == 'number') {
          callback();
          return;
        }
        if (value == null || value.indexOf(' ') != -1) {
          callback(new Error('!'));
          return;
        }
        if (!isNaN(value)) {
          callback();
        } else {
          callback(new Error('!'));
        }
      }
      let country = (rule, value, callback) => {
        if (this.dialog.form.country !== '') {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      return {
        fullscreenLoading: true,
        searchForm: {
          userName: '',
          status: '',
          canOver: '',
          country: '',
          page: 0,
          limit: 10
        },
        tableData: [],
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {
            id: '',
            userName: '',
            password: '',
            passwordNew: '',
            country: ''
          }
        },
        page: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        countryOptions: [],
        agentOptions: [],
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          id: 0,
          balanceForm: {
            id: 0,
            password: '',
            verifyCode: '',
            remain: 0,
            notes: ''
          },
          rules: {
            password: [
              {required: true, message: '', trigger: 'blur'},
            ],
            verifyCode: [
              {required: true, message: '', trigger: 'blur'},
            ],
            remain: [
              {required: true, message: '', trigger: 'blur'},
              {validator: isNumber, trigger: 'blur'}
            ]
          },
          payTypeOptions: [],
          withdrawTypeOptions: [],
        },

      }
    },
    created() {
      this.refresh()
      this.queryCountryPageList()
    },
    methods: {
      refresh() {
        const that = this;
        queryAgentPageList(that.searchForm).then(function (response) {
          that.fullscreenLoading = false
          if (response.data.code === 0) {

            that.tableData = response.data.data
            that.page.total = response.data.count
          }
        })
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
      search() {
        this.page.cur = 1
        this.refresh()
      },
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (formName === 'ruleForm') {
              that.saveAgent()
            } else if (formName === 'balanceForm') {
              that.saveBalance()
            } else if (formName === 'creditForm') {
              that.saveCredit()
            } else if (formName === 'agentForm') {
              that.saveAgent()
            } else if (formName === 'typeForm') {
              that.saveType()
            } else if (formName === 'confirmForm') {
              that.saveConfirmMin()
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveAgent() {
        let str = this.dialog.isInsert ? '' : ''
        let that = this
        this.$confirm('' + str + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          (this.dialog.isInsert ? addAgent(that.dialog.form) : updateAgent(that.dialog.form)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: str + '',
                type: 'success'
              })
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.refresh()
            that.closeDialog()
          })
        })
      },
      saveBalance() {
        let that = this
        that.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.balanceForm
          let add = true;
          if (data.remain < 0) {
            add = false
            console.log(data.remain)
            data.remain = Math.abs(data.remain)
          }
          data.id = that.settings.id;
          console.log(add );
          (add ? addAgentRemain(data) : subtractAgentRemain(data)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      commonCloseDialog(obj, formName) {
        this.$nextTick(() => {
          this.$refs[formName].clearValidate()
          this.$refs[formName].resetFields()
        })
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
      },
      handleEdit(index, row) {
        let that = this
        Object.keys(row).forEach(key => {
          if (that.dialog.form.hasOwnProperty(key)) {
            that.dialog.form[key] = row[key]
          }
        })
        this.dialog.form.id = row.id
        this.dialog.form.userName = row.userName
        this.dialog.form.password = row.password
        this.dialog.form.passwordNew = row.passwordNew
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteAgent({
            id: row.id
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.refresh()
          })
        })

      },
      changeStatus(row) {
        let that = this
        changeAgentStatus({
          id: row.id,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeSettings(row) {
        this.settings.visible = true
        this.settings.userName = row.userName
        this.settings.id = row.id
      },
      closeSettingsDialog() {
        this.settings.activeName = 'first'
        this.settings.visible = false
        this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      addCpinfo() {
        this.dialog.visible = true
      },
      beforeCloseDialog() {
        this.$refs.ruleForm.resetFields()
      }
    }
  }
</script>

<style scoped>

</style>



