/* 1. */
import axios from 'axios'
import router from "@/router";

/* 2. */
let baseURL
// 
if (process.env.NODE_ENV === 'development') { // 
    baseURL = '/api'    // 
} else {                                      // 
    if (process.env.type === 'test') {        // 
        baseURL = 'http://13.200.37.118:8080'
    } else {                                  // 
        baseURL = '/manage'
    }
}
// axios
axios.defaults.timeout = 60000;    // 1
axios.defaults.baseURL =baseURL; // 
//axios.defaults.responseType="json";
axios.defaults.withCredentials=false;  //cookie

axios.interceptors.request.use(
  config => {
    if (localStorage.getItem('User_Authorization')) {
      config.headers.Authorization = localStorage.getItem('User_Authorization');
    }

    return config;
  },
  error => {
    return Promise.reject(error);
  });

axios.interceptors.response.use(
  response => {
    if (response.data.code === 444){
      router.push("/login")
    }
    return response
  }
)
export default axios





