import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function confirmSettle(datas) {
    console.log(datas)
    return Axios({
        url: "/settlementLog/confirmSettle",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //form
        },
        data: qs.stringify(datas)
    })
}

export function confirmAgentSettle(datas) {
  console.log(datas)
  return Axios({
    url: "/settlementLog/confirmAgentSettle",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function financeSuccess(datas) {
  console.log(datas)
  return Axios({
    url: "/settlementLog/financeSuccess",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function uploadProof(datas) {
  return Axios({
    url: "/settlementLog/uploadProof",
    method: "post",
    data: datas,
    headers: {
      "Content-Type": "multipart/form-data"
    }
  })
}

export function pageList(datas) {
    console.log(datas)
    return Axios({
        url: "/settlementLog/pageList",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //form
        },
        data: qs.stringify(datas)
    })
}




