<template>
  <div id="app">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="payTypeIds">
              <el-select v-model="searchForm.payTypeIds" placeholder="" filterable multiple>
                <el-option
                  v-for="(item) in this.payTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="statDay">
              <el-date-picker
                v-model="searchForm.statDays"
                type="dates"
                placeholder=""
                value-format="yyyyMMdd"
                multiple
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <el-button type="primary" @click="search"></el-button>
          <el-button @click="resetForm('searchForm')"></el-button>
        </el-form-item>
      </el-form>
      <div id="grid" style="width: 100%">
        <el-table
          :data="payTypeStaticticsTableData "
          style="width: 100%"
          size="mini"
          show-summary
          :summary-method="getSummaries"

        >
          <!--                <el-table-column type="expand">-->
          <!--                    <template slot-scope="props">-->
          <!--                        <el-form label-position="left" inline class="demo-table-expand">-->
          <!--                            <el-form-item label="">-->
          <!--                                <span>{{ props.row.callbackUrl }}</span>-->
          <!--                            </el-form-item>-->
          <!--                        </el-form>-->
          <!--                    </template>-->
          <!--                </el-table-column>-->
          <el-table-column
            prop="channelId"
            label="id"
            align="center">
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="statDay"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="payAmount"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="payFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="withdrawAmount"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="withdrawFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
          >
            <template
              slot-scope="scope"
            >{{Number(scope.row.payAmount-scope.row.payFee-scope.row.withdrawAmount-scope.row.withdrawFee).toFixed(2)}}
            </template>

          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;height: 100%">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="pageChange"
          :current-page.sync="searchForm.page"
          :page-size="searchForm.limit"
          :page-sizes="[10, 20, 50, 100, 200]"
          layout="total, sizes, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
  import {getChannelDailyStatistics, queryPayTypeSelectList} from "../../api/paytype";

  export default {
    name: "channelDailyStatistics",
    data() {
      return {
        fullscreenLoading: true,
        searchForm: {
          payTypeIds: [],
          statDays: [],
          page: 1,
          limit: 10
        },
        payTypeStaticticsTableData: [],
        total: 0,
        payTypeOptions: []
      }
    },
    watch: {
      tableData: {
        handler: function (newName, oldName) {
          this.$nextTick(() => {
            // 
            const s_table = document.getElementsByClassName('el-table__footer-wrapper')[0]
            s_table.setAttribute('style', '')
            const child_tr = s_table.getElementsByTagName('tr')[0]
            // console.log(child_tr)
            child_tr.childNodes.forEach(item => {
              item.setAttribute('style', ';font-size:0.8rem;font-weight: bold')
            })
          })
        },
        immediate: true,
        deep: true
      }
    },
    async created() {
      this.getChannelDailyStatistics()
      this.queryAllPaytypeList()
    },
    methods: {
      getChannelDailyStatistics() {
        var that = this
        getChannelDailyStatistics(that.searchForm).then(function (response) {
          if (response.data.code === 0) {
            var data = response.data.data
            that.total = response.data.count

            that.payTypeStaticticsTableData = []
            //that.payTypeStaticticsTableData = data
            for (const channelData of data) {
              channelData.payAmount = Number(channelData.payAmount * 0.01).toFixed(2)
              channelData.payFee = Number(channelData.payFee * 0.01).toFixed(2)
              channelData.withdrawAmount = Number(channelData.withdrawAmount * 0.01).toFixed(2)
              channelData.withdrawFee = Number(channelData.withdrawFee * 0.01).toFixed(2)
            }
            that.payTypeStaticticsTableData = data
          }
        })

      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.getChannelDailyStatistics()
      },
      queryAllPaytypeList() {
        const that = this;
        queryPayTypeSelectList().then(function (res) {
          if (res.data.code === 0) {
            that.payTypeOptions = []
            const data = res.data.data;
            for (var paytype of data) {
              that.payTypeOptions.push({
                label: paytype.name,
                value: paytype.id
              })
            }
          }
        })
      },
      tableStatistics(row) {
        return row.payAmount-row.payFee-row.withdrawAmount-row.withdrawFee
      },
      search() {
        this.getChannelDailyStatistics()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.searchForm.payTypeIds = []
        this.searchForm.statDays = []
      },
      handleSizeChange(val) {
        this.searchForm.limit = val
        this.getChannelDailyStatistics()
      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          if (index === 1 || index === 2) {
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += ' ';
          } else {
            sums[index] = 'N/A';
          }
        });
        sums[3] = Number(sums[3]).toFixed(2)
        sums[4] = Number(sums[4]).toFixed(2)
        sums[5] = Number(sums[5]).toFixed(2)
        sums[6] = Number(sums[6]).toFixed(2)
        sums[7] = Number(sums[3]-sums[4]-sums[5]-sums[6]).toFixed(2)

        return sums;
      }
    }
  }
</script>

<style scoped>

</style>



