<template>
  <div id="app">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="id">
              <el-input v-model="searchForm.id" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="userName">
              <el-input v-model="searchForm.userName" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="operation">
              <el-input v-model="searchForm.operation" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :xl="4" :lg="8" :md="8" :sm="12" :xs="24" class="form-search">
            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker
                  v-model="createTime"
                  type="datetimerange"
                  :clearable="true"
                  :default-time="['00:00:00', '23:59:59']"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder=""
                  :picker-options="pickerOptionsStartCreate">
                </el-date-picker>
              </el-col>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <one-button icon="el-icon-search" type="primary" @click="search" perms="log-operaLogList-search"></one-button>
          <el-button icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
        </el-form-item>

      </el-form>
      <div id="grid" style="width: 100%">
        <el-table
          :data="tableData"
          style="width: 100%"
          size="mini"
        >
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <!--                            <el-form-item label="">-->
                <!--                                <span>{{ props.row.callbackUrl }}</span>-->
                <!--                            </el-form-item>-->
                <!--                            <el-form-item label="">-->
                <!--                                <span>{{ props.row.notifyUrl }}</span>-->
                <!--                            </el-form-item>-->
                <!--                            <el-form-item label="">-->
                <!--                                <span>{{ props.row.description }}</span>-->
                <!--                            </el-form-item>-->
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="id"
            label=""
            align="center">
          </el-table-column>

          <el-table-column
            prop="userName"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="functions"
            label=""
            min-width="200"
            align="left"
          >
          </el-table-column>
          <el-table-column
            prop="operation"
            label=""
            min-width="500"
            align="left"
          >
          </el-table-column>
          <el-table-column
            prop="result"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="useTime"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="ip"
            label="ip"
            align="center">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label=""
            align="center">
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total" :current-page="searchForm.page" @current-change="pageChange">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
  import {queryOperateLogPageList} from "../../api/operatelog";
  import OneButton from "../../components/cButton";

  export default {
    name: "operaLogList",
    components: {OneButton},
    data() {
      return {
        fullscreenLoading: false,
        createTime: [],
        searchForm: {
          id: null,
          userName: '',
          operation: '',
          module: '',
          function: '',
          method: '',
          startCreateTime: '',
          endCreateTime: '',
          page: 1,
          limit: 10,
          rowIndex: 0
        },
        tableData: [],
        total: 0,
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.pickerOptionsStartCreate) {
              // 
              var startTime = new Date(this.pickerOptionsStartCreate).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        notifyBtnSwitch: 'disabled',
        formLabelWidth: 120,
        sending: false
      }
    },
    created() {
      this.queryOperateLogPageList()
    },
    methods: {
      queryOperateLogPageList() {
        var that = this
        this.fullscreenLoading = true
        this.searchForm.startCreateTime = this.createTime === "" ? "" : this.createTime[0]
        this.searchForm.endCreateTime = this.createTime === "" ? "" : this.createTime[1]
        return new Promise(resolve => {
          queryOperateLogPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {

              var list = response.data.data
              that.total = response.data.count

              var tableData = []
              for (var operateLog of list) {

                tableData.push(operateLog)
              }
              that.tableData = tableData
              that.fullscreenLoading = false
            }
          })

        });
      },
      search() {
        this.searchForm.page = 1
        this.queryOperateLogPageList()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.createTime = []
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryOperateLogPageList()
      }
    }
  }
</script>

<style scoped>

</style>



