                    <div class="cell">{{ scope.row.content }}</div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
            <section class="form-footer">
              <el-button @click="manualUploadCallbackDrawerCloseHandle"> </el-button>
            </section>
          </el-drawer>
          <!--yesbank --->
          <el-drawer
            title="YESBANK-"
            :show-close="false"
            :visible.sync="yesmanualUploadCallbackDrawerVisible"
            direction="rtl"
            size="70%"
            :before-close="yesmanualUploadCallbackCloseHandle">
            <div style="width: 100%; padding-left: 10px; padding-right: 10px; margin-bottom: 60px" v-loading="yesloading">
              <el-upload
                ref='uploadExcel'
                class="upload-callback"
                action="#"
                :limit="1"
                :show-file-list="true"
                :auto-upload="false"
                :on-change="yesmanualUploadHandle"
                :on-exceed="yesfileSizeErrorHandle"
              >
                <el-tooltip class="item" effect="dark" content="excel" placement="right-start">
                  <el-button size="mini" type="primary" style="float: left"></el-button>
                </el-tooltip>
              </el-upload>
              <div v-if="yesuploadCallbackFlag" style="padding: 10px 0; color: green; font-size: 14px; font-weight: bold">
                YESBANK,
                <el-button v-if="yesuploadCallbackFlag" size="mini" style="float: right; margin-right: 30px;margin-bottom: 10px" type="primary" @click="submitYesBank"></el-button>
              </div>
              <el-table
                v-if="yesuploadCallbackFlag"
                :key="yesuploadCallbackTableKey"
                :data="yesuploadCallbackList"
                @selection-change="handleSelectionChange"
                row-key="id"
                border
                fit
                highlight-current-row
                style="width: 100%"
                max-height="350px"
                :header-cell-style="{background:'#F3F4F7',color:'#555'}"
              >
                <el-table-column
                  type="index"
                  width="50">
                </el-table-column>
                <el-table-column
                  type="selection"
                  width="55">
                </el-table-column>
                <el-table-column label="Reference No" width="120px" prop="referenceNo">
                </el-table-column>
                <el-table-column label="Transaction Date" width="170px" prop="transactionDate" sortable>
                </el-table-column>
                <el-table-column label="Debited Amount" width="130px" prop="debitedAmount">
                </el-table-column>
                <el-table-column label="Description" prop="description">
                </el-table-column>
              </el-table>
              <div v-if="submitCallbackFlag" style="padding: 10px 0; color: green; font-size: 14px; font-weight: bold;">YESBANK</div>
              <el-table
                v-if="submitCallbackFlag"
                :key="submitCallbackTableKey"
                :data="submitCallbackList"
                row-key="id"
                border
                fit
                highlight-current-row
                max-height="350px"
                style="width: 100%"
                :header-cell-style="{background:'#F3F4F7',color:'#555'}"
              >
                <el-table-column
                  type="index"
                  width="50">
                </el-table-column>
                <el-table-column label="" align="center" width="100">
                  <template slot-scope="scope">
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium" :type="scope.row.status == 'success' ? 'success': 'error' ">{{ scope.row.status == 'success' ? '' :
                        '' }}
                      </el-tag>
                    </div>
<!--                    <span v-if="scope.row.status === 'success'" data-v-3d7c3fb9="" class="settlement-status settlement-status-success"></span>-->
<!--                    <span v-else data-v-3d7c3fb9="" class="settlement-status settlement-status-initial"></span>-->
                  </template>
                </el-table-column>
                <el-table-column label="" align="center" width="180" prop="msg">
                </el-table-column>
                <el-table-column label="" align="left" prop="content">
                </el-table-column>
              </el-table>

            </div>
            <section class="form-footer">
              <el-button @click="yesmanualUploadCallbackDrawerCloseHandle"> </el-button>
            </section>
          </el-drawer>
        </el-row>
      </div>
    </template>
  </div>
</template>

<script>
import {
  manualUploadCallback,
  addAdminWithdrawLogColombia,
  changeWithdrawLogStatus, getWithdrawLogToken,
  queryWithdrawLogPageList, updateWithdrawLogConfirmStatusPass,
  updateWithdrawLogConfirmStatusReject, yesmanualUploadCallback, yesmanualSubmit
} from "../../api/withdrawlog";
import {queryCountryPageList} from "../../api/dictionary";
import {formatWithdrawType, formatAmount, dateFormate, toDecimalString} from "../../statics/custom"
import {queryWithdrawTypeSelectList} from "../../api/withdrawtype";
import {sendNotifyMsg} from "../../api/withdrawlog";
import {confirmSettle} from "../../api/settlement";
import OneButton from "../../components/cButton";

export default {
  name: "withdrawLogList",
  components: {OneButton},
  data() {
    var validateAmount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        const reg = /^[0-9]+(.[0-9]{1,2})?$/
        if (!reg.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 1000);
    };

    return {
      yesloading: false,
      multipleSelection: [],
      fullscreenLoading: false,
      sendNotifyDrawerVisable: false,
      direction: 'rtl',
      createTime: [],
      withdrawTime: [],
      countryOptions: [],
      searchForm: {
        id: '',
        appId: '',
        orderId: '',
        bankCardNo: '',
        pixKey: '',
        cpf: '',
        transactionNum: '',
        withdrawTypeId: '',
        type: '',
        status: '',
        syncCnt: '',
        confirmStatus: '',
        startCreateTime: '',
        endCreateTime: '',
        startWithdrawTime: '',
        endWithdrawTime: '',
        country: '',
        page: 1,
        limit: 10,
        rowIndex: 0
      },
      searchStatictics: {
        totalWithdrawAmount: 0,
        totalWithdrawNum: 0,
        totalWithdrawSuccessAmount: 0,
        totalWithdrawSuccessFee: 0,
        totalWithdrawSuccessNum: 0,
        totalWithdrawProcessAmount: 0,
        totalWithdrawFailAmount: 0,
      },
      dialog: {
        isInsert: true,
        visible: false,
        formLabelWidth: '150px',
        form: {
          appId: 0,
          UserName: '',
          UserPass: '',
          checkPass: '',
          PhoneNo: '',
          payRange: '',
          withdrawRange: '',
          SubRatio: 0,
          RemainDelayDay: '0',
          payFeeFix: '0',
          payFeeRate: '0',
          withdrawFeeFix: '0',
          withdrawFeeRate: '0',
          AgentpayFeeFix: '0',
          AgentpayFeeRate: '0',
          AgentwithdrawFeeFix: '0',
          AgentwithdrawFeeRate: '0',
          platformId: '',
          country: ''
        },
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          appId: 0,
          payFix: 0,
          payRate: 0,
          withdrawFix: 0,
          withdrawRate: 0,
          balanceForm: {
            password: '',
            Verifycode: '',
            remain: 0,
            Notes: ''
          }
        },
        rules: {
          UserName: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 50, message: ' 3  50 ', trigger: 'blur'}
          ]
        }
      },
      tableData: [],
      total: 0,
      dateStart: '', // 
      dateEnd: '', // 
      pickerOptionsStartCreate: {},
      pickerOptionsEndCreate: {
        disabledDate(time) {
          if (this.searchForm.startCreateTime) {
            // 
            var startCreateTime = new Date(this.searchForm.startCreateTime).valueOf() - 8.64e7 + 1;
          } else {
            var startCreateTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startCreateTime
        }
      },
      pickerOptionsStartPay: {},
      pickerOptionsEndPay: {
        disabledDate(time) {
          if (this.searchForm.startWithdrawTime) {
            // 
            var startWithdrawTime = new Date(this.searchForm.startWithdrawTime).valueOf() - 8.64e7 + 1;
          } else {
            var startWithdrawTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startWithdrawTime
        }
      },

      withdrawTypeOptions: [],
      sendNotifyMsgForm: {
        id: '',
        url: '',
        notifyData: '',
        notifyResp: '',
        notifyAnalyze: ''
      },
      formLabelWidth: 120,
      sending: false,
      showCheckBox: true,
      showExtra: false,
      createAdminWithdrawDrawerVisible: false,
      adminWithdrawForm: {
        country: 'COL',
        type: '1',
        appId: 10000,
        amount: 0,
        bankAccountName: '',
        bankCardNo: '',
        bankCode: '',
        branchCode: '',
        documentId: '',
        documentType: 'CC',
        accountType: '',
        verifyCode: ''
      },
      adminWithdrawRule: {
        country: [
          {required: true, message: '', trigger: 'change'},
        ],
        type: [
          {required: true, message: '', trigger: 'change'},
        ],
        appId: [
          {required: true, message: 'id', trigger: 'change'},
        ],
        bankCode: [
          {required: true, message: '', trigger: 'blur'},
        ],
        amount: [
          {required: true, validator: validateAmount, trigger: 'blur'}
        ],
        bankCardNo: [
          {required: true, message: '', trigger: 'blur'},
        ],
        documentId: [
          {required: true, message: '', trigger: 'blur'},
        ],
      },
      creatingAdminWithdraw: false,
      // -
      manualUploadCallbackDrawerVisible: false,
      uploadCallbackTableKey: 999,
      uploadCallbackFlag: false,
      uploadCallbackList: [],

      // yesbank -
      yesmanualUploadCallbackDrawerVisible: false,
      yesuploadCallbackTableKey: 888,
      yesuploadCallbackFlag: false,
      yesuploadCallbackList: [],

      // yesbank 
      submitCallbackTableKey: 777,
      submitCallbackFlag: false,
      submitCallbackList: [],

    }
  },
  created() {
    this.queryAllWithdrawLog()
    this.queryCountryPageList()
    this.queryAllWithdrawTypeList()
  },
  methods: {
    submitYesBank() {
      const that = this
      if (that.multipleSelection.length < 1) {
        that.$message({
          message: '',
          type: 'warning'
        })
        return
      }
      this.$confirm(' ', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        that.yesloading = true
        yesmanualSubmit(this.multipleSelection).then((response) => {
          if (response !== null) {
            const code = response.data.code
            const msg = response.data.msg
            that.$message(msg)
            if (code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
              that.submitCallbackFlag = true
              that.submitCallbackList = response.data.data.list
            } else {
              that.$message.error(msg);
            }
          }
          that.yesloading = false
        })
      }).catch(_ => {
      });
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    queryAllWithdrawLog() {
      var that = this
      that.fullscreenLoading = true
      this.searchForm.startCreateTime = this.createTime === "" || this.createTime === null ? "" : this.createTime[0]
      this.searchForm.endCreateTime = this.createTime === "" || this.createTime === null ? "" : this.createTime[1]
      this.searchForm.startWithdrawTime = this.withdrawTime === "" || this.withdrawTime === null ? "" : this.withdrawTime[0]
      this.searchForm.endWithdrawTime = this.withdrawTime === "" || this.withdrawTime === null ? "" : this.withdrawTime[1]

      queryWithdrawLogPageList(that.searchForm).then(function (response) {
        if (response.data.code === 0) {
          var tableData = []

          var list = response.data.data.list
          var statictics = response.data.data.statictics

          for (var withdrawlog of list) {
            withdrawlog.type = formatWithdrawType(withdrawlog)

            withdrawlog.amount = toDecimalString(withdrawlog.amount)
            withdrawlog.platformFee = toDecimalString(withdrawlog.platformFee)
            withdrawlog.thirdFee = toDecimalString(withdrawlog.thirdFee)

            var status = withdrawlog.status
            status = status === 2 ? '' : status === -2 ? '' : status === -1 ? '' : status === 0 ? '' : ''
            withdrawlog.status = status

            var syncCnt = withdrawlog.syncCnt
            syncCnt = syncCnt === 1 ? '' : syncCnt === 0 ? '' : '' + (syncCnt * -1) + ''
            withdrawlog.syncCnt = syncCnt

            withdrawlog.createTime = dateFormate(withdrawlog.createTime)
            withdrawlog.withdrawTime = dateFormate(withdrawlog.withdrawTime)

            tableData.push(withdrawlog)
          }
          that.tableData = tableData

          var totalWithdrawAmount = statictics.totalWithdrawAmount;
          that.searchStatictics.totalWithdrawAmount = toDecimalString(totalWithdrawAmount)

          var totalWithdrawSuccessAmount = statictics.totalWithdrawSuccessAmount;
          that.searchStatictics.totalWithdrawSuccessAmount = toDecimalString(totalWithdrawSuccessAmount)

          var totalWithdrawSuccessFee = statictics.totalWithdrawSuccessFee;
          that.searchStatictics.totalWithdrawSuccessFee = toDecimalString(totalWithdrawSuccessFee)

          var totalWithdrawSuccessNum = statictics.totalWithdrawSuccessNum;
          that.searchStatictics.totalWithdrawSuccessNum = totalWithdrawSuccessNum

          var totalWithdrawProcessAmount = statictics.totalWithdrawProcessAmount;
          that.searchStatictics.totalWithdrawProcessAmount = toDecimalString(totalWithdrawProcessAmount)

          var totalWithdrawFailAmount = statictics.totalWithdrawFailAmount;
          that.searchStatictics.totalWithdrawFailAmount = toDecimalString(totalWithdrawFailAmount)

          var totalWithdrawNum = statictics.totalWithdrawNum;
          if (totalWithdrawNum !== 0) {
            that.searchStatictics.rate = Math.floor(Number(totalWithdrawSuccessNum) / Number(totalWithdrawNum) * 100).toFixed(2) + '%'
          } else {
            that.searchStatictics.rate = '0'
          }

          that.total = totalWithdrawNum
          that.fullscreenLoading = false
        }
      })
    },
    queryCountryPageList() {
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    search() {
      this.queryAllWithdrawLog()
    },
    resetForm(name) {
      this.$refs.searchForm.resetFields()
      this.createTime = []
      this.withdrawTime = []
    },
    pageChange(cur) {
      this.searchForm.page = cur
      this.queryAllWithdrawLog()
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true
      // this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    queryAllWithdrawTypeList() {
      var that = this
      queryWithdrawTypeSelectList().then(function (res) {
        if (res.data.code === 0) {
          that.withdrawTypeOptions = []
          console.log(res.data.data)
          var data = res.data.data
          for (var withdrawType of data) {
            that.withdrawTypeOptions.push({
              label: withdrawType.name,
              value: withdrawType.id
            })
          }
        }
      })
    },
    changeStatus(data) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(()=>{
        var that = this
        changeWithdrawLogStatus({id: data.id}).then(function (response) {
          if (response.data.code === 0) {
            that.queryAllWithdrawLog()
          }
        })
      })
    },
    openSendNotifyMsgDrawer(data) {
      var that = this
      sendNotifyMsg({id: data.id}).then(function (response) {
        if (response.data.code === 0 || response.data.code === -1) {
          that.queryAllWithdrawLog()
        }
        that.sending = false
      })
    },
    sendNotifyMsg(id) {
      var that = this
      that.sending = true
      return new Promise(resolve => {
        axios({
          method: 'post',
          url: '../withdrawlog/sendNotifyMsg',
          data: 'id=' + id
        }).then(function (response) {
          if (response.data.code === 0 || response.data.code === -1) {
            var data = response.data.data
            that.sendNotifyMsgForm.notifyData = JSON.stringify(data.notifyData)
            that.sendNotifyMsgForm.notifyResp = data.notifyResp

            if (data.notifyResp + '' == 'SUCCESS') {
              that.sendNotifyMsgForm.notifyAnalyze = ''
              that.queryAllWithdrawLog()
            } else {
              that.sendNotifyMsgForm.notifyAnalyze = '' +
                '1."SUCCESS"' +
                '2.' +
                '' +
                '1.' +
                '2.' +
                '3.JSON' +
                '4.' +
                '5.'
            }
          }
          that.sending = false
        })

      });
    },
    handleClose(done) {
      done();
    },
    cancelNotifyMsgDrawer() {
      this.sendNotifyDrawerVisable = false
      this.sendNotifyMsgForm.notifyData = ''
      this.sendNotifyMsgForm.notifyResp = ''
      this.sendNotifyMsgForm.notifyAnalyze = ''
    },
    batchPassWithdrawLog() {
      var ids = []
      ids = Array.from(this.$refs.multipleTable.selection, ({id}) => id)
      this.pass(ids.toString())
    },
    singlePassWithdrawLog(id) {
      this.pass(id)
    },
    batchRejectWithdrawLog() {
      var ids = []
      ids = Array.from(this.$refs.multipleTable.selection, ({id}) => id)
      this.reject(ids.toString())
    },
    singleRejectWithdrawLog(id) {
      this.reject(id)
    },
    pass(ids) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(()=>{
        var that = this
        this.fullscreenLoading = true
        updateWithdrawLogConfirmStatusPass({ids: ids}).then(function (res) {
          if (res.data.code === 0) {
            that.$message(res.data.msg)
          } else {
            that.$message(res.data.msg)
          }
          that.queryAllWithdrawLog()
        })
      })
    },
    reject(ids) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(()=>{
        var that = this;
        this.fullscreenLoading = true
        updateWithdrawLogConfirmStatusReject({ids: ids}).then(function (res) {
          if (res.data.code === 0) {
            that.$message(res.data.msg)
          } else {
            that.$message(res.data.msg)
          }
          that.queryAllWithdrawLog()
        })
      })
    },
    handleSizeChange(val) {
      this.searchForm.limit = val
      this.queryAllWithdrawLog()
    },
    createAdminWithdrawDrawerHandleClose() {
      this.createAdminWithdrawDrawerVisible = false
    },
    createAdminWithdrawLog() {
      const that = this

      that.$refs['adminWithdrawForm'].validate((valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            that.creatingAdminWithdraw = true

            getWithdrawLogToken().then((resp) => {
              const code = resp.data.code
              if (code === 0) {
                const token = resp.data.data

                addAdminWithdrawLogColombia(token, that.adminWithdrawForm).then((response) => {
                  if (response !== null) {
                    const code = response.data.code
                    const msg = response.data.msg
                    that.$message(msg)
                    if (code === 0) {
                      that.queryAllWithdrawLog()
                      that.adminWithdrawForm.amount = 0
                      that.adminWithdrawForm.bankAccountName = ''
                      that.adminWithdrawForm.bankCardNo = ''
                      that.adminWithdrawForm.bankCode = ''
                      that.adminWithdrawForm.branchCode = ''
                      that.adminWithdrawForm.documentId = ''
                      that.adminWithdrawForm.documentType = ''
                    } else {
                    }
                    that.creatingAdminWithdraw = false
                  }
                })
              }
            })


          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else return false;
      })
    },
    manualUploadCallbackDrawerOpenHandle() {
      this.manualUploadCallbackDrawerVisible = true
    },
    manualUploadCallbackCloseHandle() {
      return false
    },
    manualUploadCallbackDrawerCloseHandle() {
      this.manualUploadCallbackDrawerVisible = false
      var that = this
      this.uploadCallbackFlag = false
      this.uploadCallbackList = []
      that.$nextTick(()=>{
        that.$refs['uploadImgs'].clearFiles()
      })
    },
    manualUploadHandle(file, fileList) {

      const that = this

      this.$confirm(' ' + file.name + ' ', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        var formData = new FormData()
        formData.append('file', file.raw)

        manualUploadCallback(formData).then((response) => {
          if (response !== null) {
            const code = response.data.code
            const msg = response.data.msg
            that.$message(msg)

            if (code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
              that.uploadCallbackFlag = true
              that.uploadCallbackList = response.data.data.list
            } else {
              that.$message.error('');
            }
          }
        })
      }).catch(_ => {
        that.$nextTick(()=>{
          that.$refs['uploadImgs'].clearFiles()
        })
      });

    },
    fileSizeErrorHandle(files, fileList) {
      this.$message({
        type: 'warn',
        message: ''
      });
    },

    // YESBANK 
    yesmanualUploadCallbackDrawerOpenHandle() {
      this.yesmanualUploadCallbackDrawerVisible = true
    },
    yesmanualUploadCallbackCloseHandle() {
      return false
    },
    yesmanualUploadCallbackDrawerCloseHandle() {
      this.yesmanualUploadCallbackDrawerVisible = false
      var that = this
      that.yesuploadCallbackFlag = false
      that.yesuploadCallbackList = []
      that.multipleSelection = []
      that.submitCallbackFlag = false
      that.submitCallbackList = []
      that.yesloading = false
      that.$nextTick(()=>{
        that.$refs['uploadExcel'].clearFiles()
      })
    },
    yesmanualUploadHandle(file, fileList) {
      const that = this
      this.$confirm(' ' + file.name + ' ', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        that.yesloading = true
        var formData = new FormData()
        formData.append('file', file.raw)
        yesmanualUploadCallback(formData).then((response) => {
          if (response !== null) {
            const code = response.data.code
            const msg = response.data.msg
            that.$message(msg)
            if (code === 0) {
              that.yesuploadCallbackFlag = true
              that.yesuploadCallbackList = response.data.data.list
            } else {
              that.$message.error(msg);
            }
          }
          that.yesloading = false
        })
      }).catch(_ => {
        that.$nextTick(()=>{
          that.$refs['uploadExcel'].clearFiles()
        })
      });
    },
    yesfileSizeErrorHandle(files, fileList) {
      this.$message({
        type: 'warn',
        message: ''
      });
    },
  }
}
</script>

<style scoped>
  .form-footer {
    position: absolute;
    bottom: 0;

    width: 100%;
    height: 60px;
    line-height: 60px;
    border-top: 1px solid #dedede;
    padding: 0 30px;
    text-align: right;
    overflow: hidden;
    z-index: 100;
    background: #fff;
  }
</style>



