      homeStatistics() {
        var that = this
        getHomeStatistics({country: that.countrySelect, appId: that.cpInfoSelect}).then(function (response) {
          if (response.data.code === 0) {
            that.showData = response.data.data
          }

        })
      },
      homeWarningInfo() {
        let that = this
        return new Promise(resolve => {
          homeWarningInfo().then(function (response) {
            if (response.data.code === 0) {
              that.warnData = response.data.data
            }
          })
        });
      },
      cpinfoDailyStatistics() {
        let that = this
        return new Promise(resolve => {
          cpinfoDailyStatistics({country: that.countrySelect, appId: that.cpInfoSelect}).then(function (response) {
            if (response.data.code === 0) {

              var dailyStatistics = response.data.data

              const cpInfoAppIds = []
              const cpInfoPays = []
              const cpInfoWithdraws = []

              for (const cpInfoDailyStatistics of dailyStatistics) {
                cpInfoAppIds.push(cpInfoDailyStatistics.appId)
                cpInfoPays.push(cpInfoDailyStatistics.payAmount)
                cpInfoWithdraws.push(cpInfoDailyStatistics.withdrawAmount)
              }

              that.polar.legend.data = response.data.data.legend
              that.polar.xAxis[0].data = []
              that.polar.series[0].data = []
              that.polar.series[1].data = []
              that.polar.xAxis[0].data = cpInfoAppIds
              that.polar.series[0].data = cpInfoPays
              that.polar.series[1].data = cpInfoWithdraws
              that.myCpinfoHour24Chart.setOption(that.polar);
            }

