          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.options = []
              res.data.data.forEach((item, index) => {

                that.options.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      queryCpInfoByCountrySelectList() {
        let that = this
        return new Promise(resolve => {
          queryCpInfoByCountrySelectList({country: that.countrySelect}).then(function (res) {
            that.cpInfoOptions = []
            if (res.data.code === 0) {
              res.data.data.forEach((item, index) => {
                that.cpInfoOptions.push({
                  "label": item.appId + ' ' + item.userName,
                  "value": item.appId
                })
              })
            }
          })
        })
      },
      changeCountry() {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
        this.queryCpInfoByCountrySelectList()
        this.cpinfoDailyAmountStatistics();
      },
      changeCpInfo() {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
        this.cpinfoDailyAmountStatistics();
      }
    },
    mounted() {
      this.timer = setInterval(() => {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
        this.cpinfoDailyAmountStatistics
      }, 20 * 1000)
      // this.timer = setInterval(this.cpinfoDailyStatistics(), 20*1000);
      // this.timer = setInterval(this.cpinfoHour24Statistics(), 20*1000);

    },
    beforeDestroy() {
      clearInterval(this.timer);
    }
  }
</script>

<style scoped>

</style>
<style>
  .header-title {
    text-align: center;
    font-size: 40px;
    margin: 30px 0;
  }

  .card-item {
    font-size: 22px;
  }

  .card-item > span:first-child {
    margin: 5px;
    display: inline-block;
  }

  .card-item > span:last-child {
    float: right;
    right: 10%;
    position: relative;
    font-weight: bold;
  }

  .country_select {
    text-align: right;
    margin-right: 1rem;
  }
</style>



