<template>
  <div id="app">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="Id" prop="appIds">
              <el-input v-model="appIdsStr" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="statDay">
              <el-date-picker
                v-model="searchForm.statDays"
                type="dates"
                placeholder=""
                value-format="yyyyMMdd"
                multiple
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <el-button size="medium" icon="el-icon-search" type="primary" @click="search"></el-button>
          <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
        </el-form-item>

      </el-form>
      <div id="grid" style="width: 100%">
        <el-table
          :data="tableData"
          style="width: 100%"
          size="mini"
          show-summary
          :summary-method="getSummaries"

        >
          <el-table-column
            prop="appId"
            label="Id"
            align="center">
          </el-table-column>

          <el-table-column
            prop="statDay"
            label=""
            align="center">
          </el-table-column>

          <el-table-column
            prop="payAmount"
            label=""
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="payFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="payNum"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="withdrawAmount"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="withdrawFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="withdrawNum"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            label=""
            align="center"
          >
            <template
              slot-scope="scope"
            >{{Number(scope.row.payAmount-scope.row.payFee-scope.row.withdrawAmount-scope.row.withdrawFee).toFixed(2)}}
            </template>

          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;height: 100%">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="pageChange"
          :current-page.sync="searchForm.page"
          :page-size="searchForm.limit"
          :page-sizes="[10, 20, 50, 100, 200]"
          layout="total, sizes, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
  import {queryCpStatPageList} from "../../api/cpstat";
  import {formatAmount} from "../../statics/custom";


  export default {
    name: "cpInfoDailyStatistics",
    data() {
      return {
        fullscreenLoading: false,
        createTime: [],
        appIdsStr: '',
        searchForm: {
          id: null,
          appIds: [],
          statDays: [],
          page: 1,
          limit: 10,
          rowIndex: 0
        },
        tableData: [],
        total: 0,
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.pickerOptionsStartCreate) {
              // 
              var startTime = new Date(this.pickerOptionsStartCreate).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        notifyBtnSwitch: 'disabled',
        formLabelWidth: 120,
        sending: false
      }
    },
    watch: {
      tableData: {
        handler: function (newName, oldName) {
          this.$nextTick(() => {
            // 
            const s_table = document.getElementsByClassName('el-table__footer-wrapper')[0]
            s_table.setAttribute('style', '')
            const child_tr = s_table.getElementsByTagName('tr')[0]
            // console.log(child_tr)
            child_tr.childNodes.forEach(item => {
              item.setAttribute('style', ';font-size:0.8rem;font-weight: bold')
            })
          })
        },
        immediate: true,
        deep: true
      }
    },
    created() {
      this.queryCpStatPageList()
    },
    methods: {
      queryCpStatPageList() {
        var that = this
        this.fullscreenLoading = true
        const re = /^,|| $/;

        const str = String(that.appIdsStr);
        if (str !== '') {
          if (re.test(str)) {
            that.searchForm.appIds = str.split(re)
          }else {
            that.searchForm.appIds = []
            that.searchForm.appIds.push(str)
          }
        } else {
          that.searchForm.appIds = null
        }


        return new Promise(resolve => {
          queryCpStatPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {

              var list = response.data.data
              that.total = response.data.count

              var tableData = []
              for (var cpStat of list) {
                cpStat.payAmount = formatAmount(cpStat.payAmount)
                cpStat.payFee = formatAmount(cpStat.payFee)
                cpStat.withdrawAmount = formatAmount(cpStat.withdrawAmount)
                cpStat.withdrawFee = formatAmount(cpStat.withdrawFee)

                tableData.push(cpStat)
              }
              that.tableData = tableData
              that.fullscreenLoading = false
            }
          })

        });
      },
      handleSizeChange(val) {
        this.searchForm.limit = val
        this.queryCpStatPageList()
      },
      search() {
        this.searchForm.page = 1
        this.queryCpStatPageList()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.createTime = []
        if (formName === 'searchForm') {
          this.appIdsStr = ''
          this.searchForm.appIds = []
          this.searchForm.statDays = []
        }
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryCpStatPageList()
      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          if (index === 1 ) {
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += ' ';
          } else {
            sums[index] = 'N/A';
          }
        });
        sums[2] = Number(sums[2]).toFixed(2)
        sums[3] = Number(sums[3]).toFixed(2)
        sums[5] = Number(sums[5]).toFixed(2)
        sums[6] = Number(sums[6]).toFixed(2)
        sums[8] = Number(sums[2]-sums[3]-sums[5]-sums[6]).toFixed(2)

        return sums;
      }

    }
  }
</script>

<style scoped>

</style>



