<template>
  <div id="app">
    <template>
      <el-tabs @tab-click="handleClick">
        <el-tab-pane v-for="(item,index) in countryOptions" :key="item.value" :label="item.label" :name="item.label"
                     @click="changeList(item)">{{ item.name }}
        </el-tab-pane>
      </el-tabs>
    </template>

    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="userName">
        <el-input v-model="searchForm.userName" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="" prop="agentId">
        <el-select v-model="searchForm.agentId" placeholder="" filterable>
          <el-option
            v-for="item in searchForm.agentOptions"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="canOver">
        <el-select v-model="searchForm.canOver" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="country">
        <el-select v-model="searchForm.country" placeholder="">
          <el-option
            v-for="(item) in this.countryOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="searchForm.type" placeholder="">
          <el-option
            v-for="(item) in this.userTypeOptions"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <one-button size="medium" icon="el-icon-search" type="primary" @click="search"
                    perms="user-cpInfoList-search"></one-button>
        <el-button size="medium"  icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <el-col :xl="4" :lg="4" :md="4" :sm="6" :xs="24">
        <div class="grid-content bg-purple">
          <one-button plain @click="addCpinfo()" perms="user-cpInfoList-add"></one-button>
        </div>
      </el-col>

      <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
        <div class="grid-content bg-purple">
          <span style="color: #909399; font-size: 15px; font-weight: bold">
            {{ sumRemain }}
          </span>
        </div>
      </el-col>
      <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
        <div class="grid-content bg-purple">
          <span style="color: #909399; font-size: 15px; font-weight: bold">
            {{ sumActualRemain }}
          </span>
        </div>
      </el-col>
      <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
        <div class="grid-content bg-purple">
          <span style="color: #909399; font-size: 15px; font-weight: bold">
            {{ sumToBeSettleRemain }}
          </span>
        </div>
      </el-col>
    </el-row>
    <el-table
      ref="table"
      :data="tableData" size="mini"
      style="width: 100%"
    >

      <!--      <el-table-column-->
      <!--        type="selection"-->
      <!--        width="55">-->
      <!--      </el-table-column>-->
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.key }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.googleSecret }}</span>
              <el-link :disabled="props.row.googleSecret === ''" :underline="false"
                       @click="resetGoogleSecret(props.row)">
              </el-link>

            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.userPass }}</span>
            </el-form-item>
            <el-form-item label="">
                            <span>
                                <i class="el-icon-s-finance"></i>
                                <span style="margin-left: 1px">{{
                                    Number((props.row.creditRemain) / 100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')
                                  }}</span>
                            </span>
            </el-form-item>
            <el-form-item label="">
                            <span>
                                <i class="el-icon-ice-cream-square"></i>
                                <span style="margin-left: 1px">{{
                                    props.row.frozenRemain
                                  }}</span>
                            </span>
            </el-form-item>
            <el-form-item label="Ip">

              <el-row>

                <el-col :span="24">
                  <el-input v-model="props.row.whiteIps"></el-input>

                  <el-link :underline="false"
                           @click="changeWhiteIps(props.row)">
                  </el-link>
                </el-col>

              </el-row>

            </el-form-item>
            <el-form-item label="">
                            <span>
                                <i class="el-icon-ice-cream-square"></i>
                                <span
                                  style="margin-left: 1px">{{ props.row.confirmMin }}-{{ props.row.confirmMax }}</span>
                            </span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.subRatio }}%</span>
            </el-form-item>
            <el-form-item label="T+n">
              <span>{{ props.row.remainDelayDay }}</span>
            </el-form-item>
            <el-form-item label="">
                            <span>
                                {{ props.row.agentUserName }}
                            </span>
            </el-form-item>
            <el-form-item label="">
                            <span>
                                <el-tag type="success" size="small"
                                        style="margin-bottom:2px;">{{
                                    Number(props.row.agentPayFeeFix / 100).toFixed(2)
                                  }}+{{ props.row.agentPayFeeRate }}%</el-tag>
                                <el-tag type="danger"
                                        size="small">{{
                                    Number(props.row.agentWithdrawFeeFix / 100).toFixed(2)
                                  }}+{{ props.row.agentWithdrawFeeRate }}%</el-tag>
                            </span>
            </el-form-item>
            <el-form-item label="">
                            <span>
                                <el-tag v-if="props.row.payRange&&props.row.payRange!=''" type="success" size="small"
                                        style="margin-bottom:2px;">{{ props.row.payRange }}</el-tag>
                                <el-tag v-if="props.row.withdrawRange&&props.row.withdrawRange!=''" type="danger"
                                        size="small">{{ props.row.withdrawRange }}</el-tag>
                            </span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="appId"
        label="ID"
        sortable
        column-key="appId"
        :filters="filterList"
        :filter-method="filterHandler"
        min-width="100"
      >
      </el-table-column>
      <el-table-column
        prop="userNo"
        label=""
        v-if="false"
      >
      </el-table-column>
      <el-table-column
        prop="userName"
        label="">
      </el-table-column>
      <!--        <el-table-column-->
      <!--                prop="platformName"-->
      <!--                label=""-->
      <!--                v-if="false">-->
      <!--        </el-table-column>-->
      <el-table-column
        prop="country"
        label="-">
        <template slot-scope="scope">
          {{ scope.row.country + "-" + getUserTypeLabel(scope.row.type) }}
        </template>
      </el-table-column>
      <!--      <el-table-column-->
      <!--        prop="type"-->
      <!--        label="">-->
      <!--        <template slot-scope="scope">-->
      <!--          {{getUserTypeLabel(scope.row.type)}}-->
      <!--        </template>-->
      <!--      </el-table-column>-->
      <el-table-column label="" prop="remain">
      </el-table-column>
      <el-table-column label="" prop="actualRemain">
      </el-table-column>
      <el-table-column label="" prop="frozenActualRemain">
      </el-table-column>
      <el-table-column label="" prop="toBeSettleRemain">
      </el-table-column>
      <el-table-column label="/" min-width="120">
        <template slot-scope="scope">
          <el-tag type="success" size="small" style="margin-bottom:2px;">
            {{ Number(scope.row.payFeeFix / 100).toFixed(2) }}+{{ Number(scope.row.payFeeRate * 100).toFixed(1) }}%
          </el-tag>
          <el-tag type="danger" size="small">{{ Number(scope.row.withdrawFeeFix / 100).toFixed(2) }}+{{
              Number(scope.row.withdrawFeeRate * 100).toFixed(1)
            }}%
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.DefaultPayTypeId == 0"></span>
          <template v-else>
            <span style="display: block"> {{ scope.row.defaultPayTypeId }}</span>
            <span>{{ scope.row.payTypeName }}</span>
          </template>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.defaultWithdrawTypeId == 0"></span>
          <template v-else>
            <span style="display: block"> {{ scope.row.defaultWithdrawTypeId }}</span>
            <span>{{ scope.row.withdrawTypeName }}</span>
          </template>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span v-if="scope.row.payDailyLimit === -1"></span>
          <span v-else>{{ scope.row.payDailyLimitDecimal }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <span v-if="scope.row.withdrawDailyLimit === -1"></span>
          <span v-else>{{ scope.row.withdrawDailyLimitDecimal }}</span>
        </template>
      </el-table-column>
      <el-table-column label="/" >
        <template slot-scope="scope" >
          <el-tooltip :content="scope.row.status === 1?'':''" placement="top" >
<!--            <el-switch-->
<!--              v-model="scope.row.status"-->
<!--              active-color="#13ce66"-->
<!--              inactive-color="#ff4949"-->
<!--              :active-value="1"-->
<!--              :inactive-value="-1"-->
<!--              @changeSwitch="changeStatus(scope.row)"-->
<!--            >-->
<!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @changeSwitch="changeStatus(scope.row)"
              perms="user-cpInfoList-on/off"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.canWithdraw === 1?'':''" placement="top">
<!--            <el-switch-->
<!--              v-model="scope.row.canWithdraw"-->
<!--              active-color="#13ce66"-->
<!--              inactive-color="#ff4949"-->
<!--              :active-value="1"-->
<!--              :inactive-value="0"-->
<!--              @change="changeCanWithdraw(scope.row)">-->
<!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.canWithdraw"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @changeSwitch="changeCanWithdraw(scope.row)"
              perms="user-cpInfoList-canWithdraw"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.canOver?'':''" placement="top">
<!--            <el-switch-->
<!--              v-model="scope.row.canOver"-->
<!--              active-color="#13ce66"-->
<!--              inactive-color="#ff4949"-->
<!--              :active-value="1"-->
<!--              :inactive-value="0"-->
<!--              @change="changeCanOver(scope.row)">-->
<!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.canOver"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="0"
              @changeSwitch="changeCanOver(scope.row)"
              perms="user-cpInfoList-canOver"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>

      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <one-button size="mini" @click="handleEdit(scope.$index, scope.row)"
                      perms="user-cpInfoList-edit"></one-button>
          <!--          <el-button v-if="false" size="mini" @click="openSettlementRemainDrawer(scope.$index, scope.row)"></el-button>-->
          <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"
                      perms="user-cpInfoList-del"></one-button>
          <one-button size="mini" @click="changeSettings(scope.row)" perms="user-cpInfoList-setting"></one-button>
        </template>
      </el-table-column>

    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="page.cur"
        :page-sizes="[10, 20, 50, 100, 200]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
    <!-- Form -->
    <el-dialog :title="this.dialog.isInsert ? '' : ''" :modal="false" :visible.sync="dialog.visible" width="40%" class="cpInfoAddWin"
               :before-close="preventClose"
               :show-close="false"
    >
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules" size="mini" label-position="top">
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="userName">
              <el-input v-model="dialog.form.userName" autocomplete="new-password"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="userPass">
              <el-input type="password" v-model="dialog.form.userPass" autocomplete="new-password"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="checkPass">
              <el-input type="password" v-model="dialog.form.checkPass" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item v-if="this.dialog.isInsert" label="" :label-width="dialog.formLabelWidth"
                          prop="checkCountry">
              <el-select v-model="dialog.form.country" placeholder="" filterable>
                <el-option
                  v-for="(item) in this.countryOptions"
                  :key="item.label"
                  :label="item.label"
                  :value="item.value">
                  <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth">
              <el-select v-model="dialog.form.type" placeholder="" filterable>
                <el-option
                  v-for="(item) in this.userTypeOptions"
                  :key="item.label"
                  :label="item.label"
                  :value="item.value">
                  <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="payRange">
              <el-input v-model="dialog.form.payRange" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="withdrawRange">
              <el-input v-model="dialog.form.withdrawRange" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="subRatio">
              <el-input v-model.number="dialog.form.subRatio" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="T+n" :label-width="dialog.formLabelWidth" prop="remainDelayDay">
              <el-input v-model.number="dialog.form.remainDelayDay" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!--<el-row style="display: flex; justify-content: space-between">-->
          <!--<el-col :span="11">-->
            <!--<el-form-item label="(); -1">-->
              <!--<el-input v-model.number="dialog.form.payDailyLimit" autocomplete="off"></el-input>-->
            <!--</el-form-item>-->
          <!--</el-col>-->
          <!--<el-col :span="11">-->
            <!--<el-form-item label="(); -1">-->
              <!--<el-input v-model.number="dialog.form.withdrawDailyLimit" autocomplete="off"></el-input>-->
            <!--</el-form-item>-->
          <!--</el-col>-->
        <!--</el-row>-->
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="payFeeFix">
              <el-input v-model="dialog.form.payFeeFix" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="payFeeRate">
              <el-input v-model="dialog.form.payFeeRate" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="withdrawFeeFix">
              <el-input v-model="dialog.form.withdrawFeeFix" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="withdrawFeeRate">
              <el-input v-model="dialog.form.withdrawFeeRate" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="agentPayFeeFix">
              <el-input v-model="dialog.form.agentPayFeeFix" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="agentPayFeeRate">
              <el-input v-model="dialog.form.agentPayFeeRate" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row style="display: flex; justify-content: space-between">
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="agentWithdrawFeeFix">
              <el-input v-model="dialog.form.agentWithdrawFeeFix" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="agentWithdrawFeeRate">
              <el-input v-model="dialog.form.agentWithdrawFeeRate" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <!--            <el-form-item label="" :label-width="dialog.formLabelWidth" prop="phoneNo">-->
        <!--                <el-input v-model="dialog.form.phoneNo" autocomplete="off"></el-input>-->
        <!--            </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
    <!-- settings -->
    <el-dialog title="" :modal="false" :visible.sync="settings.visible" width="60%" @closed="closeSettingsDialog"
               :close-on-click-modal="false">
      <el-tabs v-model="settings.activeName" type="card" @tab-click="">
        <el-tab-pane label="" name="first">
          <el-form :model="settings.balanceForm" :rules="settings.rules" ref="balanceForm" label-width="100px">
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="settings.balanceForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="verifyCode">
              <el-input type="password" v-model="settings.balanceForm.verifyCode"></el-input>
            </el-form-item>
            <el-form-item label="(-/+)" prop="remain">
              <el-col :span="10">
                <el-input v-model="settings.balanceForm.remain"></el-input>
              </el-col>
              <el-col class="line" :span="1">&nbsp;</el-col>
              <el-col :span="13">
                <el-tag type="success">
                  {{ Number(settings.payFeeFix / 100).toFixed(2) }}+{{ settings.payFeeRate }}%
                </el-tag>
                <el-tag type="danger">
                  {{ Number(settings.withdrawFeeFix / 100).toFixed(2) }}+{{ settings.withdrawFeeRate }}%
                </el-tag>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="notes">
              <el-input v-model="settings.balanceForm.notes"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('balanceForm')"></el-button>
              <el-button @click="resetForm('balanceForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <el-form :model="settings.creditForm" :rules="settings.rules" ref="creditForm" label-width="100px">
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="settings.creditForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="verifyCode">
              <el-input type="password" v-model="settings.creditForm.verifyCode"></el-input>
            </el-form-item>
            <el-form-item label="" prop="remain">
              <el-input v-model="settings.creditForm.remain"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('creditForm')"></el-button>
              <el-button @click="resetForm('creditForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <el-form :model="settings.confirmForm" :rules="settings.rules" ref="confirmForm" label-width="100px">

            <el-form-item label="">
              <el-select v-model="settings.confirmForm.confirmStatus" placeholder="">
                <el-option label="" :value="1"></el-option>
                <el-option label="" :value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" required>
              <el-col :span="11">
                <el-form-item>
                  <el-input v-model="settings.confirmForm.confirmMin" placeholder="/"
                  >
                    <el-button slot="append" @click="setConfirmMin()">Min</el-button>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col class="line" :span="2" style="text-align: center">-</el-col>
              <el-col :span="11">
                <el-form-item>
                  <el-input v-model="settings.confirmForm.confirmMax" placeholder="/"
                  >
                    <el-button slot="append" @click="setConfirmMax()">Max</el-button>
                  </el-input>
                </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item>
              
              {{ confirmRangeShow }}
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('confirmForm')"></el-button>
              <el-button @click="resetForm('confirmForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="fourth">
          <el-form :model="settings.agentForm" :rules="settings.rules" ref="agentForm" label-width="100px">
            <el-form-item label="" prop="agentId">
              <el-select v-model="settings.agentForm.agentId" placeholder="" filterable>
                <el-option
                  v-for="item in searchForm.agentOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('agentForm')"></el-button>
              <el-button @click="resetForm('agentForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="fifth">
          <el-form :model="settings.typeForm" :rules="settings.rules" ref="typeForm" label-width="100px">
            <el-form-item label="">
              <el-select v-model="settings.typeForm.defaultPayTypeId" placeholder="" multiple filterable>
                <el-option label="" value="0"></el-option>
                <el-option
                  v-for="item in settings.payTypeOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                  :disabled="item.status==0">
                  <span style="float: left">{{ item.id }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.name }}</span>
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="settings.typeForm.defaultWithdrawTypeId" placeholder="" multiple
                         filterable>
                <el-option label="" value="0"></el-option>
                <el-option
                  v-for="item in settings.withdrawTypeOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                  :disabled="item.status==0">
                  <span style="float: left">{{ item.id }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.name }}</span>
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('typeForm')"></el-button>
              <el-button @click="resetForm('typeForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="IP" name="sixth">
          <el-form :model="settings.ipForm" ref="ipForm" label-width="100px">
            <el-form-item label="Ip">
              <el-input v-model="settings.ipForm.ip"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ipForm')"></el-button>
              <el-button @click="resetForm('ipForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="seventh">
          <el-form :model="settings.dailyAmountForm" ref="dailyAmountForm" label-width="100px" label-position="top">
            <el-form-item label="(); -1">
              <el-input v-model="settings.dailyAmountForm.payAmount"></el-input>
            </el-form-item>
            <el-form-item label="(); -1">
              <el-input v-model="settings.dailyAmountForm.withdrawAmount"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('dailyAmountForm')"></el-button>
              <el-button @click="resetForm('dailyAmountForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="eighth">
          <el-form :model="settings.frozenForm" :rules="settings.rules" ref="frozenForm" label-width="100px" label-position="top">
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="settings.frozenForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="verifyCode">
              <el-input type="password" v-model="settings.frozenForm.verifyCode"></el-input>
            </el-form-item>
            <el-form-item label="(-/+)" prop="remain">
              <template slot="label">
                (-/+)
                <span style="color: #e9994b; font-size: 10px">  </span>
              </template>
              <el-col :span="10">
                <el-input v-model="settings.frozenForm.remain"></el-input>
              </el-col>
              <el-col class="line" :span="1">&nbsp;</el-col>
              <!--<el-col :span="13">-->
                <!--<el-tag type="success">-->
                  <!--{{ Number(settings.payFeeFix / 100).toFixed(2) }}+{{ settings.payFeeRate }}%-->
                <!--</el-tag>-->
                <!--<el-tag type="danger">-->
                  <!--{{ Number(settings.withdrawFeeFix / 100).toFixed(2) }}+{{ settings.withdrawFeeRate }}%-->
                <!--</el-tag>-->
              <!--</el-col>-->
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('frozenForm')"></el-button>
              <el-button @click="resetForm('frozenForm')"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>

    <el-drawer
      title=""
      size="40%"
      :visible.sync="settleRemainDrawer"
      :direction="direction"
    >
      <template>
        <div style="padding: 20px">
          <span></span>
          <p>
            <span>:{{ settlementForm.appId }}</span>
          </p>
          <el-table
            :data="validDayList"
            size="mini"
            style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <div style="padding-left: 50px">
                  <el-table
                    :data="props.row.list"
                    size="mini"
                    style="">
                    <el-table-column
                      label=""
                      align="center"
                    >
                      <template slot-scope="scope">
                        {{ scope.row.payTypeId }}
                        <p>
                          {{ scope.row.payTypeName }}
                        </p>
                      </template>
                    </el-table-column>
                    <el-table-column
                      prop="amount"
                      label=""
                      align="center"

                    >
                    </el-table-column>
                    <el-table-column
                      prop="payLogNums"
                      label=""
                      align="center"

                    >
                    </el-table-column>
                    <el-table-column
                      align="center"
                    >
                      <template slot-scope="scope">
                        <el-button type="primary" round size="mini" @click="openSettlementDialog(scope.row)">
                        </el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              prop="payDay"
              align="center"
            >
            </el-table-column>
            <el-table-column
              label=""
              prop="amount"
              align="center"
            >
            </el-table-column>
            <el-table-column
              label=""
              prop="payLogNums"
              align="center"
            >
            </el-table-column>
            <el-table-column
              label=""
              align="center"
            >
              <template slot-scope="scope">
                <el-button type="primary" round size="mini" @click="openSettlementDialog(scope.row)"
                           :loading="settling">
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>

    </el-drawer>
    <el-dialog
      title=""
      :visible.sync="settlementDialogVisible"
      width="30%"
      :before-close="handleClose">
      <table>
        <tr>
          <td style="width: 120px;height: 30px">:</td>
          <td>{{ settlementForm.appId }}</td>
        </tr>
        <tr>
          <td style="width: 120px;height: 30px">:</td>
          <td>{{ settlementForm.payDay }}</td>
        </tr>
        <tr v-if="settlementForm.payTypeId !== '' && settlementForm.payTypeId!== undefined">
          <td style="width: 120px;height: 30px">:</td>
          <td>{{ settlementForm.payTypeName }}</td>
        </tr>
        <tr>
          <td style="width: 120px;height: 30px">:</td>
          <td>{{ settlementForm.amount }}</td>
        </tr>
      </table>
      <span slot="footer" class="dialog-footer">
    <el-button @click="closeSettlementDialog"> </el-button>
    <el-button type="primary" @click="settlementPayLog"> </el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import {queryPayTypeSelectList} from "../../api/paytype";
import {queryWithdrawTypeSelectList} from "../../api/withdrawtype";
import {queryAgentSelectList} from "../../api/agent";
import {
  addCpInfo,
  addCpInfoRemain,
  changeCpInfoCanOver, changeCpInfoCanWithdraw,
  changeCpInfoConfirmMin,
  changeCpInfoConfirmStatus,
  changeCpInfoStatus, changeWhiteIps,
  deleteCpInfo, queryCpInfoByUserName,
  queryCpInfoPageList, queryCpInfoStats,
  resetGoogleSecret,
  subtractCpInfoRemain, updateCpInfo,
  updateCpInfoAgentId,
  updateCpInfoCredit,
  updateCpInfoDefaultType,
  updateCpInfoIp,
  updateCpInfoDailyAmount,
  frozenActual,
  unFrozenActual
} from "../../api/cpinfo";
import {queryCountryPageList, queryCurrencyPageList, queryUserTypePageList} from "../../api/dictionary";

import {toDecimalString} from "../../statics/custom";
import da from "element-ui/src/locale/lang/da";
import {exportCpInfo} from "../../api/cpinfo";
import {queryValidPayLogList, settlementPayLog} from "../../api/paylog";
import OneButton from "../../components/cButton";
import OneSwitch from "../../components/cSwitch";

export default {
  name: "cpInfoList",
  components: {OneSwitch, OneButton},
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.dialog.form.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.dialog.form.userPass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    let isNumber = (rule, value, callback) => {
      if (typeof value == 'number') {
        callback();
        return;
      }
      if (value == null || value.indexOf(' ') != -1) {
        callback(new Error('!'));
        return;
      }
      if (!isNaN(value)) {
        callback();
      } else {
        callback(new Error('!'));
      }
    }
    //
    let agentPayFeeFix = (rule, value, callback) => {
      if (value <= this.dialog.form.payFeeFix) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentPayFeeRate = (rule, value, callback) => {
      if (value <= this.dialog.form.payFeeRate) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentWithdrawFeeFix = (rule, value, callback) => {
      if (value <= this.dialog.form.withdrawFeeFix) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentWithdrawFeeRate = (rule, value, callback) => {
      if (value <= this.dialog.form.withdrawFeeRate) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let country = (rule, value, callback) => {
      if (this.dialog.form.country !== '') {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    var checkUserNameCanUse = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      if (this.dialog.isInsert) {
        queryCpInfoByUserName({
          userName: value
        }).then(function (response) {
          if (response.data.code === 0) {
            callback();
          } else {
            callback(new Error(''));
          }
        })
      } else {
        callback()
      }
    };
    return {
      fullscreenLoading: true,
      searchForm: {
        userName: '',
        agentId: '',
        status: '',
        canOver: '',
        agentOptions: [],
        platformOptions: [],
        country: '',
        type: ''
      },
      dialog: {
        isInsert: true,
        visible: false,
        formLabelWidth: '150px',
        form: {
          appId: 0,
          userName: '',
          userPass: '',
          checkPass: '',
          payRange: '',
          withdrawRange: '',
          subRatio: 0,
          remainDelayDay: '0',
          payFeeFix: '0',
          payFeeRate: '0',
          withdrawFeeFix: '0',
          withdrawFeeRate: '0',
          agentPayFeeFix: '0',
          agentPayFeeRate: '0',
          agentWithdrawFeeFix: '0',
          agentWithdrawFeeRate: '0',
          country: '',
          type: ''
        },
        rules: {
          userName: [
            {validator: checkUserNameCanUse, trigger: 'blur'}
          ],
          userPass: [
            {required: true, message: '', trigger: 'blur'},
            {validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {validator: validatePass2, trigger: 'blur'}
          ],
          subRatio: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remainDelayDay: [
            {required: true, message: '', trigger: 'blur'},
          ],
          agentPayFeeFix: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentPayFeeFix, trigger: 'blur'}
          ],
          agentPayFeeRate: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentPayFeeRate, trigger: 'blur'}
          ],
          agentWithdrawFeeFix: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentWithdrawFeeFix, trigger: 'blur'}
          ],
          agentWithdrawFeeRate: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentWithdrawFeeRate, trigger: 'blur'}
          ],
        }
      },
      settings: {
        visible: false,
        activeName: 'first',
        userName: '',
        appId: 0,
        payFix: 0,
        payRate: 0,
        withdrawFix: 0,
        withdrawRate: 0,
        balanceForm: {
          appId: 0,
          password: '',
          verifyCode: '',
          remain: 0,
          notes: ''
        },
        creditForm: {
          password: '',
          verifyCode: '',
          remain: '0',
        },
        agentForm: {
          agentId: ''
        },
        confirmForm: {
          confirmStatus: '',
          confirmMin: '',
          confirmMax: ''
        },
        typeForm: {
          defaultPayTypeId: [],
          defaultWithdrawTypeId: [],
        },
        ipForm: {
          ip: ''
        },
        dailyAmountForm: {
          payAmount: '',
          withdrawAmount: ''
        },
        frozenForm: {
          appId: 0,
          password: '',
          verifyCode: '',
          remain: 0
        },
        rules: {
          userName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
          ],
          verifyCode: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remain: [
            {required: true, message: '', trigger: 'blur'},
            {validator: isNumber, trigger: 'blur'}
          ],
          country: [
            {required: true, message: '', trigger: 'blur'},
            {validator: isNumber, trigger: 'blur'}
          ]
        },
        payTypeOptions: [],
        withdrawTypeOptions: [],
      },
      tableData: [],
      page: {
        pageSize: 10,
        total: 0,
        cur: 1
      },
      userTypeOptions: [],
      countryOptions: [],
      agentOptions: [],
      filterList: [],
      whiteIps: '',
      confirmRangeShow: '',
      settleRemainDrawer: false,
      direction: 'rtl',
      validDayList: [],
      settlementDialogVisible: false,
      settlementForm: {},
      settling: false,
      sumRemain: '',
      sumActualRemain: '',
      sumToBeSettleRemain: '',
      resetDialogForm: {}
    }

  },
  created() {
    this.refresh()
    this.findType()
    this.findAgent()
    this.queryCountryPageList()
    this.queryUserTypeOptions()
    this.resetDialogForm = JSON.parse(JSON.stringify(this.dialog.form))
  },
  watch: {
    settings: {
      handler(val, oldValue) {
        const confirmMin = this.settings.confirmForm.confirmMin
        const confirmMax = this.settings.confirmForm.confirmMax

        if (confirmMin !== '' && confirmMax !== '') {
          if (confirmMin === 'min' && confirmMax === 'max') {
            this.confirmRangeShow = ''
          } else if (confirmMin > confirmMax) {
            this.confirmRangeShow = ''
          } else {
            this.confirmRangeShow = confirmMin + ' < ' + '' + '<' + confirmMax + ''
          }
        } else if (confirmMin !== '' && confirmMax === '') {
          this.confirmRangeShow = confirmMin + ' < ' + '' + ''
        } else if (confirmMin === '' && confirmMax !== '') {
          this.confirmRangeShow = '' + ' < ' + confirmMax + ''
        } else {
          this.confirmRangeShow = ''
        }
      },
      deep: true
    }
  },
  methods: {
    getUserTypeLabel(value) {
      let label = ""
      for (let i = 0; i < this.userTypeOptions.length; i++) {
        if (value == this.userTypeOptions[i].value) {
          label = this.userTypeOptions[i].label
        }
      }
      return label
    },
    findType() {
      let that = this
      queryPayTypeSelectList().then(function (response) {
        if (response.data.code === 0) {
          let payTypeList = [];
          for (let i = 0; i < response.data.data.length; i++) {
            let payType = {}
            payType.id = response.data.data[i].id + ""
            payType.name = response.data.data[i].name + ""
            payType.status = response.data.data[i].status + ""
            payTypeList.push(payType)
          }
          that.settings.payTypeOptions = payTypeList
        }
      })
      queryWithdrawTypeSelectList().then(function (response) {
        if (response.data.code === 0) {
          let withdrawTypeList = [];
          for (let i = 0; i < response.data.data.length; i++) {
            let withdrawType = {}
            withdrawType.id = response.data.data[i].id + ""
            withdrawType.name = response.data.data[i].name + ""
            withdrawType.status = response.data.data[i].status + ""
            withdrawTypeList.push(withdrawType)
          }
          that.settings.withdrawTypeOptions = withdrawTypeList
        }
      })
    },
    findAgent() {
      let that = this
      queryAgentSelectList().then(function (response) {
        if (response.data.code === 0) {
          that.searchForm.agentOptions = response.data.data
        }
      })
    },
    refresh() {
      const that = this;
      that.fileList = []

      let userNames = []

      const re = /^,|| $/;
      const str = String(that.searchForm.userName);
      if (str !== '') {
        if (re.test(str)) {
          userNames = str.split(re)
        } else {
          userNames = []
          userNames.push(str)
        }
      } else {
        userNames = null
      }

      var params = {
        page: this.page.cur,
        limit: this.page.pageSize,
        userNames: userNames,
        agentId: this.searchForm.agentId,
        status: this.searchForm.status,
        canOver: this.searchForm.canOver,
        country: this.searchForm.country,
        type: this.searchForm.type
      }

      // 
      queryCpInfoStats(params).then(function (response) {
        const data = response.data.data

        if (response.data.code === 0) {
          const remain = !data.remain ? 0 : data.remain
          const actualRemain = !data.remain ? 0 : data.actualRemain
          const toBeSettleRemain = !data.remain ? 0 : remain - actualRemain

          that.sumRemain = toDecimalString(remain)
          that.sumActualRemain = toDecimalString(actualRemain)
          that.sumToBeSettleRemain = toDecimalString(toBeSettleRemain)
        }
      })

      queryCpInfoPageList(params).then(function (response) {
        that.fullscreenLoading = false

        const data = response.data.data

        if (response.data.code === 0) {
          for (const datum of data) {
            const remain = datum.remain
            const actualRemain = datum.actualRemain
            const frozenActualRemain = datum.frozenActualRemain
            const frozenRemain = datum.frozenRemain
            const toBeSettleRemain = remain - actualRemain - frozenActualRemain

            datum.remain = toDecimalString(remain)
            datum.actualRemain = toDecimalString(actualRemain)
            datum.frozenActualRemain = toDecimalString(frozenActualRemain)
            datum.toBeSettleRemain = toDecimalString(toBeSettleRemain)
            datum.frozenRemain = toDecimalString(frozenRemain)

            if (datum.payDailyLimit != -1) {
              datum.payDailyLimitDecimal = toDecimalString(datum.payDailyLimit)
            }

            if (datum.withdrawDailyLimit != -1) {
              datum.withdrawDailyLimitDecimal = toDecimalString(datum.withdrawDailyLimit)
            }

          }
          that.tableData = data
          that.page.total = response.data.count

          for (const datum of data) {
            that.filterList.push(
              {
                text: datum.appId,
                value: datum.appId
              }
            )
          }
        }
      })
    },
    pageChange(cur) {
      this.page.cur = cur
      this.refresh()
    },
    search() {
      this.page.cur = 1
      this.$nextTick(()=>{
        this.refresh()
      })
    },
    submitForm(formName) {
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'ruleForm') {
            that.saveCpInfo()
          } else if (formName === 'balanceForm') {
            that.saveBalance()
          } else if (formName === 'creditForm') {
            that.saveCredit()
          } else if (formName === 'agentForm') {
            that.saveAgent()
          } else if (formName === 'typeForm') {
            that.saveType()
          } else if (formName === 'confirmForm') {
            that.saveConfirmMin()
          } else if (formName === 'ipForm') {
            that.saveIp()
          } else if (formName === 'dailyAmountForm') {
            that.saveDailyAmount()
          } else if (formName === 'frozenForm') {
            that.saveFrozen()
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    saveCpInfo() {
      let str = this.dialog.isInsert ? '' : ''
      let that = this
      this.$confirm('' + str + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        (that.dialog.isInsert ? addCpInfo(that.dialog.form) : updateCpInfo(that.dialog.form)).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: str + '',
              type: 'success'
            })
          } else {
            str = str + '' + response.data.msg
            that.$message.error(str);
          }
          that.refresh()
          that.closeDialog()
        })
      })
    },
    saveBalance() {
      let that = this

      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = {
          appId: 0,
          password: '',
          verifyCode: '',
          remain: 0,
          notes: ''
        }

        data.appId = that.settings.appId
        data.password = that.settings.balanceForm.password
        data.verifyCode = that.settings.balanceForm.verifyCode
        data.remain = that.settings.balanceForm.remain
        data.notes = that.settings.balanceForm.notes
        if (data.remain < 0) {
          data.remain = Math.abs(data.remain)
        }
        (that.settings.balanceForm.remain >= 0 ? addCpInfoRemain(data) : subtractCpInfoRemain(data)).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveFrozen() {
      let that = this

      this.$confirm('/' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = {
          appId: 0,
          password: '',
          verifyCode: '',
          remain: 0
        }

        data.appId = that.settings.appId
        data.password = that.settings.frozenForm.password
        data.verifyCode = that.settings.frozenForm.verifyCode
        data.remain = that.settings.frozenForm.remain
        if (data.remain < 0) {
          data.remain = Math.abs(data.remain)
        }
        (that.settings.frozenForm.remain >= 0 ? frozenActual(data) : unFrozenActual(data)).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
            that.refresh()
            that.closeSettingsDialog()
          } else {
            that.$message.error(response.data.msg);
          }
        })
      })
    },
    saveCredit() {
      let that = this
      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = that.settings.creditForm
        data.appId = that.settings.appId
        updateCpInfoCredit(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveConfirmMin() {
      let that = this
      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // that.settings.confirmForm.confirmMin = that.settings.confirmForm.confirmMin === '' ? '' : that.settings.confirmForm.confirmMin
        // that.settings.confirmForm.confirmMax = that.settings.confirmForm.confirmMax === '' ? '' : that.settings.confirmForm.confirmMax


        let data = JSON.parse(JSON.stringify(that.settings.confirmForm))
        data.appId = that.settings.appId
        changeCpInfoConfirmMin(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveAgent() {
      let that = this
      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = that.settings.agentForm
        data.appId = that.settings.appId
        updateCpInfoAgentId(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveType() {
      let that = this
      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = that.settings.typeForm
        data.appId = that.settings.appId
        data.defaultPayTypeId = that.settings.typeForm.defaultPayTypeId.toString()
        data.defaultWithdrawTypeId = that.settings.typeForm.defaultWithdrawTypeId.toString()
        updateCpInfoDefaultType(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveIp() {
      let that = this
      this.$confirm('' + this.settings.userName + 'ip?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = that.settings.ipForm
        data.appId = that.settings.appId
        updateCpInfoIp(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    saveDailyAmount() {
      let that = this
      this.$confirm('' + this.settings.userName + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let data = that.settings.dailyAmountForm
        data.appId = that.settings.appId
        updateCpInfoDailyAmount(data).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
          that.closeSettingsDialog()
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true
      this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    preventClose() {
      return false
    },
    commonCloseDialog(obj, formName) {
      this.$nextTick(() => {
        this.$refs[formName].clearValidate()
        this.$refs[formName].resetFields()
      })
      Object.keys(obj).forEach(key => {
        let type = typeof obj[key]
        if (type === 'number') {
          obj[key] = 0
        }
        if (type === 'string') {
          obj[key] = ''
        }
      })
      this.dialog.form.platformId = ''
      // this.resetForm(formName)
    },
    handleEdit(index, row) {
      let that = this
      Object.keys(row).forEach(key => {
        if (that.dialog.form.hasOwnProperty(key)) {
          that.dialog.form[key] = row[key]
        }
      })
      this.dialog.form.userName = row.userName
      this.dialog.form.checkPass = row.userPass
      this.dialog.form.payFeeFix = row.payFeeFix / 100
      this.dialog.form.withdrawFeeFix = row.withdrawFeeFix / 100
      this.dialog.form.agentPayFeeFix = row.agentPayFeeFix / 100
      this.dialog.form.agentWithdrawFeeFix = row.agentWithdrawFeeFix / 100


      this.dialog.visible = true
      this.dialog.isInsert = false
    },
    handleDelete(index, row) {
      let that = this
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteCpInfo({
          appId: row.appId
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      })

    },
    changeStatus(row) {
      let that = this
      changeCpInfoStatus({
        appId: row.appId,
        status: row.status === 1 ? -1 : 1
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '',
            type: 'success'
          })
        } else {
          that.$message.error('');
        }
        that.refresh()
      })
    },
    changeConfirmStatus(row) {
      let that = this
      changeCpInfoConfirmStatus({
        appId: row.appId,
        confirmStatus: row.confirmStatus
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '',
            type: 'success'
          })
        } else {
          that.$message.error('');
        }
        that.refresh()
      })
    },
    changeConfirmMin(row) {
      let that = this
      changeCpInfoConfirmMin({
        appId: row.appId,
        confirmMin: row.status
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '',
            type: 'success'
          })
        } else {
          that.$message.error('');
        }
        that.refresh()
      })
    },
    changeCanWithdraw(row) {
      let that = this
      changeCpInfoCanWithdraw({
        appId: row.appId,
        canWithdraw: row.canWithdraw === 1 ? -1 : 1
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '',
            type: 'success'
          })
        } else {
          that.$message.error('');
        }
        that.refresh()
      })
    },
    changeCanOver(row) {
      let that = this
      changeCpInfoCanOver({
        appId: row.appId,
        canOver: row.canOver === 1 ? 0 : 1
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: response.data.msg,
            type: 'success'
          })
        } else {
          that.$message.error(response.data.msg);
        }
        that.refresh()
      })
    },
    changeSettings(row) {
      this.settings.visible = true
      if (row.payDailyLimit === -1) {
        this.settings.dailyAmountForm.payAmount = -1
      } else {
        this.settings.dailyAmountForm.payAmount = row.payDailyLimit / 100
      }
      if (row.withdrawDailyLimit === -1) {
        this.settings.dailyAmountForm.withdrawAmount = -1
      } else {
        this.settings.dailyAmountForm.withdrawAmount = row.withdrawDailyLimit / 100
      }
      if (row.defaultPayTypeId.length > 0 && row.defaultPayTypeId !== 0) {
        let defaultPayTypeId = row.defaultPayTypeId.split(",")
        let payTypes = []
        for (let i = 0; i < defaultPayTypeId.length; i++) {
          payTypes.push(defaultPayTypeId[i] + "")
        }
        this.settings.typeForm.defaultPayTypeId = payTypes
      } else {
        this.settings.typeForm.defaultPayTypeId = []
      }
      if (row.defaultWithdrawTypeId.length > 0 && row.defaultWithdrawTypeId !== 0) {
        let defaultWithdrawTypeId = row.defaultWithdrawTypeId.split(",")
        let withdrawTypes = []
        for (let i = 0; i < defaultWithdrawTypeId.length; i++) {
          withdrawTypes.push(defaultWithdrawTypeId[i] + "")
        }
        this.settings.typeForm.defaultWithdrawTypeId = withdrawTypes
      } else {
        this.settings.typeForm.defaultWithdrawTypeId = []
      }
      this.$nextTick(() => {
        this.settings.userName = row.userName
        this.settings.payFeeFix = row.payFeeFix
        this.settings.payFeeRate = row.payFeeRate
        this.settings.withdrawFeeFix = row.withdrawFeeFix
        this.settings.withdrawFeeRate = row.withdrawFeeRate
        this.settings.agentForm.agentId = row.agentId
        this.settings.appId = row.appId
        this.settings.confirmForm.confirmStatus = row.confirmStatus
        this.settings.confirmForm.confirmMin = row.confirmMin
        this.settings.confirmForm.confirmMax = row.confirmMax
        this.settings.ipForm.ip = row.whiteIps
      })
      this.settings.frozenForm = {}
    },
    closeSettingsDialog() {
      this.settings.activeName = 'first'
      this.settings.visible = false
      this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
      this.commonCloseDialog(this.settings.creditForm, 'creditForm')
      this.commonCloseDialog(this.settings.agentForm, 'agentForm')
      this.commonCloseDialog(this.settings.typeForm, 'typeForm')
    },
    queryCountryPageList() {
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    queryUserTypeOptions() {
      let that = this
      return new Promise(resolve => {
        queryUserTypePageList({type: 'user_type'}).then(function (res) {
          if (res.data.code === 0) {
            that.userTypeOptions = []
            res.data.data.forEach((item, index) => {
              that.userTypeOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    addCpinfo() {
      this.dialog.form = JSON.parse(JSON.stringify(this.resetDialogForm))
      this.dialog.visible = true
    },
    beforeCloseDialog() {
      this.$refs.ruleForm.resetFields()
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.refresh()
    },
    filterHandler(value, row, column) {
      const property = column['property'];
      return String(row[property]) === String(value);
    },
    resetGoogleSecret(row) {
      const that = this
      resetGoogleSecret({appId: row.appId}).then((response) => {
        if (response.data.code === 0) {
          that.$message(response.data.msg)
        }
      })
    },
    changeWhiteIps(row) {
      const that = this
      changeWhiteIps({appId: row.appId, whiteIps: row.whiteIps}).then((response) => {
        if (response.data.code === 0) {
          that.$message(response.data.msg)
        }
      })
    },
    setConfirmMin() {
      this.settings.confirmForm.confirmMin = 'min'
    },
    setConfirmMax() {
      this.settings.confirmForm.confirmMax = 'max'
    },
    statisticsCpInfo() {

    },
    getSummaries(param) {
      const {columns, data} = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index < 5 || index > 7) {
          return ''
        }
        const values = data.map(item => Number(item[column.property]));

        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index] = sums[index];
        } else {
          sums[index] = 'N/A';
        }
      });
      return sums;
    },
    exportCpInfo() {
      const that = this;
      that.fileList = []

      let userNames = []

      const re = /^,|| $/;
      const str = String(that.searchForm.userName);
      if (str !== '') {
        if (re.test(str)) {
          userNames = str.split(re)
        } else {
          userNames = []
          userNames.push(str)
        }
      }
      window.location.href = encodeURI("/cpinfo/exportCpInfo?" +
        "userNames=" + userNames +
        "&agentId=" + that.searchForm.agentId +
        "&status=" + that.searchForm.status +
        "&canOver=" + that.searchForm.canOver +
        "&country=" + that.searchForm.country)
    },

    queryValidPayLogList() {
      var that = this
      queryValidPayLogList({
        appId: that.settlementForm.appId
      }).then((resp) => {
        const code = resp.data.code
        const msg = resp.data.msg
        const data = resp.data.data

        if (code === 0) {

          for (const datum of data) {
            datum.amount = toDecimalString(datum.amount)
            for (const datumElement of datum.list
              ) {
              datumElement.amount = toDecimalString(datumElement.amount)
            }
          }
          that.validDayList = data
        }
      })
    },
    openSettlementRemainDrawer(index, row) {
      this.validDayList.appId = row.appId
      this.settlementForm.appId = row.appId
      this.queryValidPayLogList()
      this.settleRemainDrawer = true
    },
    closeSettlementRemainDrawer() {
      this.settleRemainDrawer = false
    },
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => {
        });
    },
    openSettlementDialog(row) {
      this.settlementDialogVisible = true
      this.settlementForm.payDay = row.payDay
      if (row.payTypeId !== undefined) {
        this.settlementForm.payTypeId = row.payTypeId
        this.settlementForm.payTypeName = row.payTypeName
      }
      this.settlementForm.amount = row.amount
    },
    closeSettlementDialog() {
      this.settlementForm = {}
      this.settlementDialogVisible = false
    },
    settlementPayLog() {
      this.settling = true

      const that = this
      settlementPayLog(this.settlementForm).then((resp) => {
        const code = resp.data.code
        const msg = resp.data.msg
        const data = resp.data.data

        if (code === 0) {
          that.$message(msg)
          that.queryValidPayLogList()
          that.settlementDialogVisible = false
        }
        that.$message(msg)
        that.settling = false
      })
    },
    handleClick(tab, event) {
      this.searchForm.country = this.countryOptions[tab.index].value
      this.refresh()
    },
    changeList(item) {
      this.country = item.value
    },
    queryCurrencyPageList() {
      let that = this
      return new Promise(resolve => {
        queryCurrencyPageList({type: 'currency'}).then(function (res) {
          if (res.data.code === 0) {
            that.currencyOptions = []
            res.data.data.forEach((item, index) => {
              that.currencyOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },

  },
}
</script>

<style>
.el-scrollbar__view {
  padding-bottom: 20px;
}
.cpInfoAddWin .el-form-item--mini.el-form-item {
  margin-bottom: 8px;
}
</style>



