import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function confirmRecharge(datas) {
    console.log(datas)
    return Axios({
        url: "/recharge/confirmRecharge",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //form
        },
        data: qs.stringify(datas)
    })
}

export function financeSuccess(datas) {
  console.log(datas)
  return Axios({
    url: "/recharge/financeSuccess",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function uploadProof(datas) {
  return Axios({
    url: "/recharge/uploadProof",
    method: "post",
    data: datas,
    headers: {
      "Content-Type": "multipart/form-data"
    }
  })
}

export function pageList(datas) {
    console.log(datas)
    return Axios({
        url: "/recharge/pageList",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //form
        },
        data: qs.stringify(datas)
    })
}




