<template>
  <el-switch
    v-model="switchValue"
    active-color="#13ce66"
    inactive-color="#ff4949"
    :active-value="activeValue"
    :inactive-value="inactiveValue"
    @change="handleChange()"
    v-if="hasPermission(perms)"
  >
  </el-switch>
<!--  <span>{{this.switchData}}</span>-->
</template>

<script>
export default {
  name: "oneSwitch",
  props: {
    value: {
      type: Number
    },
    perms: {  // 
      type: String,
      default: null
    },
    switchData: {
      type: Number,
    },
    activeValue: {
      type: Number,
    },
    inactiveValue: {
      type: Number,
    }
  },
  data() {
    return {
      switchValue: this.value
    }
  },
  created() {
    },
  methods: {
    // 
    handleChange() {
      this.$emit('changeSwitch')
    },
    hasPermission(perms) {
      let hasPermission = false
      let permissions = JSON.parse(window.sessionStorage.getItem('permissionList'))
      var permsArr = perms.split('-');
      var menuFather = permsArr[0]
      var menuSon = permsArr[1]
      var btn = permsArr[2]
      for (let i = 0; i < permissions.length; i++) {
        if (permissions[i].pValue === menuFather) {
          var children = permissions[i].children;
          for (let j = 0; j < children.length; j++) {
            if (children[j].pValue === menuSon) {
              var sons = children[j].children;
              for (let k = 0; k < sons.length; k++) {
                if (sons[k].pValue === perms) {
                  this.buttonName = sons[k].permissName
                  hasPermission = true;
                  break
                }
              }
            }
          }
        }
      }
      return hasPermission
    }

  }
}
</script>

<style scoped>

</style>



