<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="userName">
        <el-input v-model="searchForm.userName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <one-button type="primary" @click="search" perms="user-userList-search"></one-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <c-button plain @click="addUser()" perms="user-userList-add"></c-button>
    </el-row>
    <el-table
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.password }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="username"
        label="">
      </el-table-column>
      <el-table-column
        prop="roleNames"
        label="">
      </el-table-column>
      <el-table-column
        prop="loginTime"
        label="">
      </el-table-column>
      <el-table-column
        prop="createtime"
        label="">
      </el-table-column>

      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'':''" placement="top">
            <!--            <el-switch-->
            <!--              v-model="scope.row.status"-->
            <!--              active-color="#13ce66"-->
            <!--              inactive-color="#ff4949"-->
            <!--              :active-value="1"-->
            <!--              :inactive-value="-1"-->
            <!--              @change="changeStatus(scope.row)">-->
            <!--            </el-switch>-->
            <one-switch
              :key="new Date().getTime()"
              :value="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="0"
              @changeSwitch="changeStatus(scope.row)"
              perms="user-userList-on/off"
            >
            </one-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <c-button size="mini" @click="handleEdit(scope.$index, scope.row)" perms="user-userList-edit"></c-button>

          <one-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"
                      perms="user-userList-del"></one-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="total" :current-page="searchForm.page" @current-change="pageChange">
      </el-pagination>
    </div>
    <!-- Form -->
    <el-dialog :title="this.dialog.isInsert ? '' : ''" :visible.sync="dialog.visible" width="24rem"
               @closed="closeDialog">
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules" size="mini">
        <el-form-item label="" :label-width="dialog.formLabelWidth" prop="userName">
          <el-input v-model="dialog.form.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="dialog.formLabelWidth" prop="password">
          <el-input type="password" v-model="dialog.form.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="dialog.formLabelWidth" prop="passwordNew">
          <el-input type="password" v-model="dialog.form.passwordNew" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="dialog.formLabelWidth"
                      prop="roles">
          <el-select v-model="dialog.form.roles" multiple placeholder="" filterable>
            <el-option style="color: #8492a6; font-size: 13px" label="" value=""></el-option>
            <el-option
              v-for="(item) in roleOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {addUser, changeStatus, deleteUser, queryUserPageList, updateUser} from "../../api/user";
import {queryRoleSelectList} from "../../api/role";
import cButton from "../../components/cButton";
import OneButton from "../../components/cButton";
import OneSwitch from "../../components/cSwitch";

export default {
  name: "userList",
  components: {
    OneSwitch,
    OneButton,
    cButton
  },
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.dialog.form.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.dialog.form.UserPass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    let isNumber = (rule, value, callback) => {
      if (typeof value == 'number') {
        callback();
        return;
      }
      if (value == null || value.indexOf(' ') != -1) {
        callback(new Error('!'));
        return;
      }
      if (!isNaN(value)) {
        callback();
      } else {
        callback(new Error('!'));
      }
    }
    //
    let agentPayFeeFix = (rule, value, callback) => {
      if (value <= this.dialog.form.payFeeFix) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentPayFeeRate = (rule, value, callback) => {
      if (value <= this.dialog.form.payFeeRate) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentWithdrawFeeFix = (rule, value, callback) => {
      if (value <= this.dialog.form.withdrawFeeFix) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let agentWithdrawFeeRate = (rule, value, callback) => {
      if (value <= this.dialog.form.withdrawFeeRate) {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    let country = (rule, value, callback) => {
      if (this.dialog.form.country !== '') {
        callback()
      } else {
        callback(new Error('!'));
      }
    }
    return {
      fullscreenLoading: true,
      searchForm: {
        userName: '',
        page: 1,
        limit: 10
      },
      total: 0,
      dialog: {
        isInsert: true,
        visible: false,
        formLabelWidth: '150px',
        form: {
          id: '',
          userName: '',
          password: '',
          passwordNew: '',
          role: '',
          roles: []
        },
        rules: {
          UserName: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 50, message: ' 3  50 ', trigger: 'blur'}
          ],
          UserPass: [
            {required: true, message: '', trigger: 'blur'},
            {validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {validator: validatePass2, trigger: 'blur'}
          ],
          SubRatio: [
            {required: true, message: '', trigger: 'blur'},
          ],
          RemainDelayDay: [
            {required: true, message: '', trigger: 'blur'},
          ],
          AgentpayFeeFix: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentPayFeeFix, trigger: 'blur'}
          ],
          AgentpayFeeRate: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentPayFeeRate, trigger: 'blur'}
          ],
          AgentwithdrawFeeFix: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentWithdrawFeeFix, trigger: 'blur'}
          ],
          AgentwithdrawFeeRate: [
            {validator: isNumber, trigger: 'blur'},
            {validator: agentWithdrawFeeRate, trigger: 'blur'}
          ],
        }
      },
      settings: {
        visible: false,
        activeName: 'first',
        userName: '',
        appId: 0,
        payFix: 0,
        payRate: 0,
        withdrawFix: 0,
        withdrawRate: 0,
        balanceForm: {
          password: '',
          Verifycode: '',
          remain: 0,
          Notes: ''
        },
        creditForm: {
          password: '',
          Verifycode: '',
          remain: '0',
        },
        agentForm: {
          UserName: ''
        },
        typeForm: {
          payType: 0,
          withdrawType: 0,
        },

        rules: {
          UserName: [
            {required: true, message: '', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
          ],
          Verifycode: [
            {required: true, message: '', trigger: 'blur'},
          ],
          remain: [
            {required: true, message: '', trigger: 'blur'},
            {validator: isNumber, trigger: 'blur'}
          ],
          country: [
            {required: true, message: '', trigger: 'blur'},
            {validator: isNumber, trigger: 'blur'}
          ]
        },
        payTypeOptions: [],
        withdrawTypeOptions: [],
      },
      tableData: [],
      roleOptions: [],
      addFormTitle: ''

    }
  },
  created() {
    this.queryUserPageList()
    this.queryRoleSelectList()
  },
  methods: {
    queryUserPageList() {
      var that = this
      queryUserPageList(that.searchForm).then(function (response) {
        that.fullscreenLoading = false
        if (response.data.code === 0) {

          that.tableData = response.data.data
          that.total = response.data.count
        }
      })
    },
    refresh() {
    },
    pageChange(cur) {
      this.searchForm.page = cur
      this.queryUserPageList()
    },
    search() {
      this.searchForm.page = 1
      this.queryUserPageList()
    },
    submitForm(formName) {
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'ruleForm') {
            that.saveUser()
          }
        } else {
          return false;
        }
      });
    },
    saveUser() {
      let str = this.dialog.isInsert ? '' : ''
      this.addFormTitle = str + ''
      let that = this
      this.$confirm('' + str + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const userForm = {
          id: that.dialog.form.id,
          userName: that.dialog.form.userName,
          password: that.dialog.form.password,
          passwordNew: that.dialog.form.passwordNew,
          role: that.dialog.form.role,
          roles: that.dialog.form.roles.join(',')
        };
        (that.dialog.isInsert ? addUser(userForm) : updateUser(userForm)).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: str + '',
              type: 'success'
            })
            that.queryUserPageList()
          } else {
            str = str + '' + response.data.msg
            that.$message.error(str);
          }
          that.closeDialog()
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.dialog.form.roles = ''
    },
    beforeClose() {
      this.dialog.form.roles = []
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true

      this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    commonCloseDialog(obj, formName) {
      Object.keys(obj).forEach(key => {
        let type = typeof obj[key]
        if (type === 'number') {
          obj[key] = 0
        }
        if (type === 'string') {
          obj[key] = ''
        }
      })
      this.dialog.form.platformId = ''
      this.dialog.form.roles = []
      // this.resetForm(formName)
    },
    handleEdit(index, row) {
      let that = this
      Object.keys(row).forEach(key => {
        if (that.dialog.form.hasOwnProperty(key)) {
          that.dialog.form[key] = row[key]
        }
      })
      this.dialog.form.id = row.userId
      this.dialog.form.userName = row.username
      this.dialog.form.password = row.password
      this.dialog.form.passwordNew = row.password
      this.dialog.form.roles = row.roles.split(',')
      this.dialog.visible = true
      this.dialog.isInsert = false
    },
    handleDelete(index, row) {
      let that = this
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteUser({
          id: row.userId
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.queryUserPageList()
        })
      })

    },
    changeStatus(row) {
      let that = this
      changeStatus({
        id: row.userId,
        status: row.status === 0 ? 1 : 0
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '',
            type: 'success'
          })
        } else {
          that.$message.error('');
        }
        that.refresh()
      })
    },
    changeSettings(row) {
      this.settings.visible = true
      if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
        let DefaultPayTypeId = row.DefaultPayTypeId.split("")
        let payTypes = [];
        for (let i = 0; i < DefaultPayTypeId.length; i++) {
          payTypes.push(DefaultPayTypeId[i] + "")
        }
        this.settings.typeForm.payType = payTypes
      } else {
        this.settings.typeForm.payType = []
      }

      if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
        let defaultWithdrawTypeId = row.DefaultWithdrawTypeId.split("")
        let withdrawTypes = [];
        for (let i = 0; i < defaultWithdrawTypeId.length; i++) {
          withdrawTypes.push(defaultWithdrawTypeId[i] + "")
        }
        this.settings.typeForm.withdrawType = withdrawTypes
      } else {
        this.settings.typeForm.withdrawType = []
      }
      this.settings.userName = row.UserName
      this.settings.appId = row.appId
      this.settings.payFeeFix = row.payFeeFix
      this.settings.payFeeRate = row.payFeeRate
      this.settings.withdrawFeeFix = row.withdrawFeeFix
      this.settings.withdrawFeeRate = row.withdrawFeeRate
    },
    closeSettingsDialog() {
      this.settings.activeName = 'first'
      this.settings.visible = false
      this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
      this.commonCloseDialog(this.settings.creditForm, 'creditForm')
      this.commonCloseDialog(this.settings.agentForm, 'agentForm')
      this.commonCloseDialog(this.settings.typeForm, 'typeForm')
    },
    queryRoleSelectList() {
      let that = this
      return new Promise(resolve => {
        queryRoleSelectList().then(function (res) {
          if (res.data.code === 0) {
            that.roleOptions = []
            res.data.data.forEach((item, index) => {
              that.roleOptions.push(
                {
                  "label": item.rolename,
                  "value": item.id + ''
                }
              )

            })
          }
        })
      })
    },
    addUser() {
      this.dialog.isInsert = true
      let str = this.dialog.isInsert ? '' : ''
      this.dialog.visible = true

    }
  }
}
</script>

<style scoped>

</style>



