<template>
  <div id="app">
    <el-form :inline="true" :model="searchForm2" ref="searchForm2">
      <el-form-item label="" prop="id">
        <el-input v-model="searchForm2.id" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm2.status" placeholder="">
          <el-option
            v-for="(item) in this.statusOption"
            :key="item.label"
            :label="item.label"
            :value="item.value">
            <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker
            v-model="createTime"
            type="daterange"
            :clearable="true"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd"
            placeholder=""
            :picker-options="pickerOptionsStartCreate">
          </el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button size="medium" icon="el-icon-search" type="primary" @click="search2"></el-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm2')"></el-button>
        <el-button plain type="primary" @click="openMsg()"></el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData2" size="mini"
      style="width: 100%"
    >
      <el-table-column
        prop="id"
        label="22"
        column-key="id"
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="userName"
        width="120"
        label="">
      </el-table-column>
      <el-table-column
        prop="batchNo"
        width="140"
        label=""
        :formatter="dateFormat">
      </el-table-column>
      <el-table-column
        prop="content"
        label="" >
        <template slot-scope="scope">
          <el-avatar v-if="(scope.row.content).startsWith('img-file')" shape="square" :size="60" fit="contain" :src='imageUrl + scope.row.batchNo + "&imgName=" + scope.row.content'></el-avatar>
          <i v-else>{{scope.row.content}}</i>
        </template>
      </el-table-column>
      <el-table-column
        prop="status"
        width="90"
        label="">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium" :type="scope.row.status == 1 ? 'success': 'error' ">{{ scope.row.status == 1 ? '' :
              '' }}
            </el-tag>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="msg"
        width="250"
        :show-overflow-tooltip="true"
        label="">
      </el-table-column>
      <el-table-column
        prop="userId"
        label=""
        :show-overflow-tooltip="true"
        column-key="userId"
        width="350"
      >
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        @size-change="handleSizeChange2"
        @current-change="pageChange2"
        :current-page.sync="page2.cur"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page2.pageSize"
        layout="total, sizes, prev, pager, next"
        :total="page2.total">
      </el-pagination>
    </div>

    <!-- Form -->
    <el-dialog title="" :modal="false" :visible.sync="dialog.visible" width="80%" @closed="closeDialog" :close-on-click-modal="false"
    >
      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
      <div style="margin: 15px 0;"></div>
      <el-checkbox-group v-model="checkedCountry" @change="handleCheckedCitiesChange">
        <el-checkbox v-for="item in countryOptions" :label="item.value" :key="item.value">{{item.label}}</el-checkbox>
      </el-checkbox-group>
      <div style="margin: 15px 0;"></div>

      <template v-if="true">
        <el-checkbox :indeterminate="isIndeterminate2" v-model="checkAll2" @change="handleCheckAllTypeBoxChange"></el-checkbox>
        <div style="margin: 15px 0;"></div>
        <el-checkbox-group v-model="checkedUserType" @change="handleCheckedTypeBoxChange">
          <el-checkbox v-for="item in userTypeOptions" :label="item.value" :key="item.value">{{item.label}}</el-checkbox>
        </el-checkbox-group>
      </template>

      <div style="margin: 15px 0;"></div>
      <el-tag
        type="success"
        :key="tag"
        v-for="tag in cpIds"
        closable
        :disable-transitions="false"
        @close="handleCloseTag(tag)">
        {{tag}}
      </el-tag>
      <el-input
        class="input-new-tag"
        v-if="inputVisible"
        v-model="inputValue"
        ref="saveTagInput"
        size="small"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm"
      >
      </el-input>
      <el-button v-else class="button-new-tag" size="small" @click="showInput">+ </el-button>
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules">
        <div id="toolbar" class="quill-editor-toolbar">
          <select class="ql-size">
            <option value="small"></option>
            <option selected></option>
            <option value="large"></option>
            <option value="huge"></option>
          </select>
          <select class="ql-header">
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
            <option value="4"></option>
            <option value="5"></option>
            <option value="6"></option>
            <option selected></option>
          </select>
          <select class="ql-font"></select>
          <button class="ql-bold"></button>
          <button class="ql-italic"></button>
          <button class="ql-underline"></button>
          <button class="ql-strike"></button>
          <button class="ql-blockquote"></button>
          <button class="ql-code-block"></button>
          <select class="ql-color"></select>
          <select class="ql-background"></select>
          <select class="ql-align"></select>
          <button class="ql-script" value="sub"></button>
          <button class="ql-script" value="super"></button>
          <button class="ql-direction" value="rtl"></button>
          <button class="ql-indent" value="-1"></button>
          <button class="ql-indent" value="+1"></button>
          <button class="ql-list" value="ordered"></button>
          <button class="ql-list" value="bullet"></button>
          <button class="ql-header" value="1"></button>
          <button class="ql-header" value="2"></button>
          <button class="ql-clean"></button>
        </div>
        <el-form-item >
          <el-input v-model="dialog.form.content" autocomplete="new-off" type="textarea" :rows="20"></el-input>
        </el-form-item>
        <quill-editor class="quill-editor" v-show="false" ref="myQuillEditor" :options="editorOption" @ready="onEditorReady($event)">
        </quill-editor>
        <el-form-item label="(3):">
          <el-upload
            ref='uploadImgs'
            class="avatar-uploader"
            action="#"
            list-type="picture-card"
            :show-file-list="true"
            :auto-upload="false"
            :multiple="true"
            :on-change="fileChange2"
            :on-remove="fileRemove2"
            :drag="true"
            accept="image/jpeg,image/jpg,image/png"
            :limit="3"
          >
            <i class="el-icon-plus" v-if="this.fileList2.length < 3"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm2('ruleForm')" :disabled="submitButtonDisabled"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {queryPayTypeSelectList} from "../../api/paytype";
  import {queryWithdrawTypeSelectList} from "../../api/withdrawtype";
  import {queryAgentSelectList} from "../../api/agent";
  import {
    addCpInfo,
    addCpInfoRemain,
    changeCpInfoCanOver, changeCpInfoCanWithdraw,
    changeCpInfoConfirmMin,
    changeCpInfoConfirmStatus,
    changeCpInfoStatus, changeWhiteIps,
    deleteCpInfo, queryCpInfoByUserName,
    queryCpInfoPageList, resetGoogleSecret,
    subtractCpInfoRemain, updateCpInfo,
    updateCpInfoAgentId,
    updateCpInfoCredit,
    updateCpInfoDefaultType,
    updateCpInfoIp
  } from "../../api/cpinfo";
  import {queryCountryPageList, queryCurrencyPageList, queryUserTypePageList} from "../../api/dictionary";

  import {dateFormate, toDecimalString} from "../../statics/custom";
  import da from "element-ui/src/locale/lang/da";
  import {exportCpInfo} from "../../api/cpinfo";
  import {queryValidPayLogList, settlementPayLog} from "../../api/paylog";
  import {sendMsg, getAllRecord, queryCpIdsByCountry, getImg} from "../../api/massMsg";
  import { quillEditor } from 'vue-quill-editor'
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'

  export default {
    name: "massMsg",
    components: {
      quillEditor
    },
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.dialog.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.dialog.form.userPass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      let isNumber = (rule, value, callback) => {
        if (typeof value == 'number') {
          callback();
          return;
        }
        if (value == null || value.indexOf(' ') != -1) {
          callback(new Error('!'));
          return;
        }
        if (!isNaN(value)) {
          callback();
        } else {
          callback(new Error('!'));
        }
      }
      //
      let agentPayFeeFix = (rule, value, callback) => {
        if (value <= this.dialog.form.payFeeFix) {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      let agentPayFeeRate = (rule, value, callback) => {
        if (value <= this.dialog.form.payFeeRate) {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      let agentWithdrawFeeFix = (rule, value, callback) => {
        if (value <= this.dialog.form.withdrawFeeFix) {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      let agentWithdrawFeeRate = (rule, value, callback) => {
        if (value <= this.dialog.form.withdrawFeeRate) {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      let country = (rule, value, callback) => {
        if (this.dialog.form.country !== '') {
          callback()
        } else {
          callback(new Error('!'));
        }
      }
      var checkUserNameCanUse = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (this.dialog.isInsert) {
          queryCpInfoByUserName({
            userName: value
          }).then(function (response) {
            if (response.data.code === 0) {
              callback();
            } else {
              callback(new Error(''));
            }
          })
        } else {
          callback()
        }
      };
      return {
        typeboxVisible: false,
        inputVisible: false,
        inputValue: '',
        fileList2: [],
        imageUrl: 'https://13.200.37.118:8080/manage/telegrambot/getImg2?sendId=',
        startTime: '',
        endTime: '',
        createTime: [],
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.pickerOptionsStartCreate) {
              // 
              var startTime = new Date(this.pickerOptionsStartCreate).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        allType: ['','',''],
        cpIds: [],
        allCountry: [],
        checkAll: false,
        checkedCountry: [],
        isIndeterminate: true,
        //
        allUserType: [],
        checkedUserType: [],
        isIndeterminate2: true,
        checkAll2: false,
        statusOption: [
          {label: '', value: '1'},
          {label: '', value: '2'}
        ],
        editorOption: {
          modules: {
            toolbar: '#toolbar'
          },
          placeholder: ''
        },
        fullscreenLoading: true,
        searchForm: {
          userName: '',
          agentId: '',
          status: '',
          canOver: '',
          agentOptions: [],
          platformOptions: [],
          country: '',
        },
        searchForm2: {
          id: '',
          userId: '',
          status: '',
          groupName: '',
          startCreateTime: '',
          endCreateTime: ''
        },
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '80px',
          form: {
            appId: 0,
            userName: '',
            userPass: '',
            checkPass: '',
            payRange: '',
            withdrawRange: '',
            subRatio: 0,
            remainDelayDay: '0',
            payFeeFix: '0',
            payFeeRate: '0',
            withdrawFeeFix: '0',
            withdrawFeeRate: '0',
            agentPayFeeFix: '0',
            agentPayFeeRate: '0',
            agentWithdrawFeeFix: '0',
            agentWithdrawFeeRate: '0',
            country: '',
            content: ''
          },
          rules: {
            content: [
              {required: true, message: '', trigger: 'blur'},
            ],
          }
        },
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          appId: 0,
          payFix: 0,
          payRate: 0,
          withdrawFix: 0,
          withdrawRate: 0,
          balanceForm: {
            appId: 0,
            password: '',
            verifyCode: '',
            remain: 0,
            notes: ''
          },
          creditForm: {
            password: '',
            verifyCode: '',
            remain: '0',
          },
          agentForm: {
            agentId: ''
          },
          confirmForm: {
            confirmStatus: '',
            confirmMin: '',
            confirmMax: ''
          },
          typeForm: {
            defaultPayTypeId: [],
            defaultWithdrawTypeId: [],
          },
          ipForm: {
            ip: ''
          },
          rules: {
            userName: [
              {required: true, message: '', trigger: 'blur'},
            ],
            password: [
              {required: true, message: '', trigger: 'blur'},
            ],
            verifyCode: [
              {required: true, message: '', trigger: 'blur'},
            ],
            remain: [
              {required: true, message: '', trigger: 'blur'},
              {validator: isNumber, trigger: 'blur'}
            ],
            country: [
              {required: true, message: '', trigger: 'blur'},
              {validator: isNumber, trigger: 'blur'}
            ]
          },
          payTypeOptions: [],
          withdrawTypeOptions: [],
        },
        tableData: [],
        tableData2: [],
        page: {
          pageSize: 1000,
          total: 0,
          cur: 1
        },
        page2: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        countryOptions: [],
        userTypeOptions: [],
        agentOptions: [],
        filterList: [],
        whiteIps: '',
        confirmRangeShow: '',
        settleRemainDrawer: false,
        direction: 'rtl',
        validDayList: [],
        settlementDialogVisible: false,
        settlementForm: {},
        settling: false,
        //
        multipleSelection: [],
        submitButtonDisabled: false
      }

    },
    created() {
      this.refresh()
      this.findType()
      this.findAgent()
      this.queryCountryPageList()
      this.queryUserTypeOptions()
    },
    watch: {
      settings: {
        handler(val, oldValue) {
          const confirmMin = this.settings.confirmForm.confirmMin
          const confirmMax = this.settings.confirmForm.confirmMax

          if (confirmMin !== '' && confirmMax !== '') {
            if (confirmMin === 'min' && confirmMax === 'max') {
              this.confirmRangeShow = ''
            } else if (confirmMin > confirmMax) {
              this.confirmRangeShow = ''
            } else {
              this.confirmRangeShow = confirmMin + ' < ' + '' + '<' + confirmMax + ''
            }
          } else if (confirmMin !== '' && confirmMax === '') {
            this.confirmRangeShow = confirmMin + ' < ' + '' + ''
          } else if (confirmMin === '' && confirmMax !== '') {
            this.confirmRangeShow = '' + ' < ' + confirmMax + ''
          } else {
            this.confirmRangeShow = ''
          }
        },
        deep: true
      }
    },
    methods: {
      showInput() {
        this.inputVisible = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },
      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.cpIds.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = '';
      },
      contentFormat(row, column) {
        if (row.content.startsWith('img-file')) {
          getImg({'sendId': 213}).then(function (response) {
            if (response.data.code === 0) {
              const base64 = response.data.data;
              const img = "data:image/jpeg;base64," + base64;
              const result = '<el-avatar  shape="square" :size="80" fit="contain" src="' + img + '"'+ '></el-avatar>';
              return result
            } else {
              return ""
            }
          })
        } else {
          return row.content
        }
      },
      fileChange2(file, fileList) {
        this.fileList2 = []
        if (fileList.length > 3) {
          this.$message.warning('')
        }
        for (let i = 0; i < fileList.length; i++) {
          if (i < 3) {
            this.fileList2.push(fileList[i])
          }
        }
      },
      fileRemove2(file, fileList) {
        this.fileList2 = []
        for (let i = 0; i < fileList.length; i++) {
          if (i < 3) {
            this.fileList2.push(fileList[i])
          }
        }
      },
      handleCloseTag(tag) {
        this.cpIds.splice(this.cpIds.indexOf(tag), 1);
      },
      getCpIds() {
        let that = this
        queryCpIdsByCountry({
          'countrys': that.checkedCountry.length > 0 ? that.checkedCountry.join(',') : '',
          'type': that.checkedUserType.length > 0 ? that.checkedUserType.join(',') : ''
        }).then(function (response) {
          if (response.data.code === 0) {
            that.cpIds = response.data.data
          } else {
            that.cpIds = []
            str = str + '' + response.data.msg
            that.$message.error(str);
          }
        })
      },
      handleCheckAllChange(val) {
        this.checkedCountry = val ? this.allCountry : [];
        this.isIndeterminate = false;
        this.getCpIds()
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.countryOptions.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.countryOptions.length;
        this.getCpIds()
      },
      handleCheckAllTypeBoxChange(val) {
        this.checkedUserType = val ? this.allUserTypeOptions : [];
        this.isIndeterminate2 = false;
        this.getCpIds()
      },
      handleCheckedTypeBoxChange(value) {
        let checkedCount = value.length;
        this.checkAll2 = checkedCount === this.userTypeOptions.length;
        this.isIndeterminate2 = checkedCount > 0 && checkedCount < this.userTypeOptions.length;
        this.getCpIds()
      },
      dateFormat(row, column) {
        let date = row[column.property];
        if (date === undefined || date == null) {
          return ''
        }
        return this.dateFormate(date)
      },
      dateFormate(date) {
        if (date) {
          date = new Date(date)
        } else {
          return ''
        }
        var Y = date.getFullYear();
        var M = date.getMonth() + 1;
        M = M < 10 ? '0' + M : M;// 0
        var D = date.getDate();
        D = D < 10 ? '0' + D : D;
        var H = date.getHours();
        H = H < 10 ? '0' + H : H;
        var Mi = date.getMinutes();
        Mi = Mi < 10 ? '0' + Mi : Mi;
        var S = date.getSeconds();
        S = S < 10 ? '0' + S : S;
        // return Y + '-' + M + '-' + D + ' ' + H + ':' + Mi + ':' + S;
        return Y + '-' + M + '-' + D;
      },
      openMsg2() {
        this.settlementDialogVisible = true
        this.cpIds = []
        this.checkAll = false
        this.checkedCountry = []
        this.fileList2 = []
      },
      openRecord() {
        this.settings.visible = true
        this.search2();
      },
      onEditorReady(editor) {
      },
      //msg 
      openMsg() {
        this.dialog.visible = true
        this.cpIds = []
        this.checkAll = false
        this.checkedCountry = []
        this.checkAll2 = false
        this.checkedUserType = []
        this.fileList2 = []
        this.isIndeterminate = false
        this.isIndeterminate2 = false
        this.submitButtonDisabled = false
        this.$nextTick(()=>{
          this.$refs['uploadImgs'].clearFiles()
        })
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = []
        val.forEach((item) => {
          this.multipleSelection.push(item.appId)
        })
      },
      findType() {
        let that = this
        queryPayTypeSelectList().then(function (response) {
          if (response.data.code === 0) {
            let payTypeList = [];
            for (let i = 0; i < response.data.data.length; i++) {
              let payType = {}
              payType.id = response.data.data[i].id + ""
              payType.name = response.data.data[i].name + ""
              payType.status = response.data.data[i].status + ""
              payTypeList.push(payType)
            }
            that.settings.payTypeOptions = payTypeList
          }
        })
        queryWithdrawTypeSelectList().then(function (response) {
          if (response.data.code === 0) {
            let withdrawTypeList = [];
            for (let i = 0; i < response.data.data.length; i++) {
              let withdrawType = {}
              withdrawType.id = response.data.data[i].id + ""
              withdrawType.name = response.data.data[i].name + ""
              withdrawType.status = response.data.data[i].status + ""
              withdrawTypeList.push(withdrawType)
            }
            that.settings.withdrawTypeOptions = withdrawTypeList
          }
        })
      },
      findAgent() {
        let that = this
        queryAgentSelectList().then(function (response) {
          if (response.data.code === 0) {
            that.searchForm.agentOptions = response.data.data
          }
        })
      },
      refresh() {
        this.search2()
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
      pageChange2(cur) {
        this.page2.cur = cur
        this.refresh()
      },
      search() {
        this.page.cur = 1
        this.refresh()
      },
      search2() {
        let that = this
        that.searchForm2.startCreateTime = this.createTime === "" ? "" : this.createTime[0]
        that.searchForm2.endCreateTime = this.createTime === "" ? "" : this.createTime[1]
        getAllRecord({
          'page': that.page2.cur,
          'limit': that.page2.pageSize,
          'id': that.searchForm2.id,
          'status': that.searchForm2.status,
          'groupName': that.searchForm2.groupName,
          'startDate': that.searchForm2.startCreateTime,
          'endDate': that.searchForm2.endCreateTime
        }).then(function (response) {
          if (response.data.code === 0) {
            that.tableData2 = response.data.data
            that.page2.total = response.data.count
          } else {
            str = str + '' + response.data.msg
            that.$message.error(str);
          }
        })
      },
      submitForm2(formName) {
        if (this.cpIds.length < 1) {
          this.$message.warning("");
          return;
        }
        if (this.fileList2.length < 1 && !this.dialog.form.content) {
          this.$message.warning("");
          return;
        }
        this.$confirm('', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let that = this
          //
          that.submitButtonDisabled = true
          const formData = new FormData()
          if (that.fileList2.length > 0) {
            that.fileList2.forEach((file, index) => {
              formData.append('file', file.raw, file.newName)
            })
          }
          formData.append('dataList', that.cpIds)
          formData.append('content', that.dialog.form.content)
          sendMsg(formData).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.refresh()
            that.closeDialog()
          })
        })
      },
      submitForm(formName) {
        if (this.cpIds.length < 1) {
          this.$message.warning("");
          return;
        }
        this.$confirm('', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let that = this
          sendMsg({'dataList': that.cpIds, 'content': that.dialog.form.content}).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.refresh()
            that.closeDialog()
          })
        })
      },
      saveCpInfo() {
        let str = this.dialog.isInsert ? '' : ''
        let that = this
        this.$confirm('' + str + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          (that.dialog.isInsert ? addCpInfo(that.dialog.form) : updateCpInfo(that.dialog.form)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: str + '',
                type: 'success'
              })
            } else {
              str = str + '' + response.data.msg
              that.$message.error(str);
            }
            that.refresh()
            that.closeDialog()
          })
        })
      },
      saveBalance() {
        let that = this

        this.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = {
            appId: 0,
            password: '',
            verifyCode: '',
            remain: 0,
            notes: ''
          }

          data.appId = that.settings.appId
          data.password = that.settings.balanceForm.password
          data.verifyCode = that.settings.balanceForm.verifyCode
          data.remain = that.settings.balanceForm.remain
          data.notes = that.settings.balanceForm.notes
          if (data.remain < 0) {
            data.remain = Math.abs(data.remain)
          }
          (that.settings.balanceForm.remain >= 0 ? addCpInfoRemain(data) : subtractCpInfoRemain(data)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      saveCredit() {
        let that = this
        this.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.creditForm
          data.appId = that.settings.appId
          updateCpInfoCredit(data).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      saveConfirmMin() {
        let that = this
        this.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // that.settings.confirmForm.confirmMin = that.settings.confirmForm.confirmMin === '' ? '' : that.settings.confirmForm.confirmMin
          // that.settings.confirmForm.confirmMax = that.settings.confirmForm.confirmMax === '' ? '' : that.settings.confirmForm.confirmMax


          let data = JSON.parse(JSON.stringify(that.settings.confirmForm))
          data.appId = that.settings.appId
          changeCpInfoConfirmMin(data).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      saveAgent() {
        let that = this
        this.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.agentForm
          data.appId = that.settings.appId
          updateCpInfoAgentId(data).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      saveType() {
        let that = this
        this.$confirm('' + this.settings.userName + '?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.typeForm
          data.appId = that.settings.appId
          data.defaultPayTypeId = that.settings.typeForm.defaultPayTypeId.toString()
          data.defaultWithdrawTypeId = that.settings.typeForm.defaultWithdrawTypeId.toString()
          updateCpInfoDefaultType(data).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      saveIp() {
        let that = this
        this.$confirm('' + this.settings.userName + 'ip?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let data = that.settings.ipForm
          data.appId = that.settings.appId
          updateCpInfoIp(data).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: response.data.msg,
                type: 'success'
              })
            } else {
              that.$message.error(response.data.msg);
            }
            that.refresh()
            that.closeSettingsDialog()
          })
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.createTime = []
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
        this.fileList2 = []
      },
      commonCloseDialog(obj, formName) {
        this.$nextTick(() => {
          this.$refs[formName].clearValidate()
          this.$refs[formName].resetFields()
        })
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        this.dialog.form.platformId = ''
        // this.resetForm(formName)
      },
      handleEdit(index, row) {
        let that = this
        Object.keys(row).forEach(key => {
          if (that.dialog.form.hasOwnProperty(key)) {
            that.dialog.form[key] = row[key]
          }
        })
        this.dialog.form.userName = row.userName
        this.dialog.form.checkPass = row.userPass
        this.dialog.form.payFeeFix = row.payFeeFix / 100
        this.dialog.form.withdrawFeeFix = row.withdrawFeeFix / 100
        this.dialog.form.agentPayFeeFix = row.agentPayFeeFix / 100
        this.dialog.form.agentWithdrawFeeFix = row.agentWithdrawFeeFix / 100
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteCpInfo({
            appId: row.appId
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.refresh()
          })
        })

      },
      changeStatus(row) {
        let that = this
        changeCpInfoStatus({
          appId: row.appId,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeConfirmStatus(row) {
        let that = this
        changeCpInfoConfirmStatus({
          appId: row.appId,
          confirmStatus: row.confirmStatus
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeConfirmMin(row) {
        let that = this
        changeCpInfoConfirmMin({
          appId: row.appId,
          confirmMin: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeCanWithdraw(row) {
        let that = this
        changeCpInfoCanWithdraw({
          appId: row.appId,
          canWithdraw: row.canWithdraw
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
      changeCanOver(row) {
        let that = this
        changeCpInfoCanOver({
          appId: row.appId,
          canOver: row.canOver
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: response.data.msg,
              type: 'success'
            })
          } else {
            that.$message.error(response.data.msg);
          }
          that.refresh()
        })
      },
      changeSettings(row) {
        this.settings.visible = true
        if (row.defaultPayTypeId.length > 0 && row.defaultPayTypeId !== 0) {
          let defaultPayTypeId = row.defaultPayTypeId.split(",")
          let payTypes = []
          for (let i = 0; i < defaultPayTypeId.length; i++) {
            payTypes.push(defaultPayTypeId[i] + "")
          }
          this.settings.typeForm.defaultPayTypeId = payTypes
        } else {
          this.settings.typeForm.defaultPayTypeId = []
        }
        if (row.defaultWithdrawTypeId.length > 0 && row.defaultWithdrawTypeId !== 0) {
          let defaultWithdrawTypeId = row.defaultWithdrawTypeId.split(",")
          let withdrawTypes = []
          for (let i = 0; i < defaultWithdrawTypeId.length; i++) {
            withdrawTypes.push(defaultWithdrawTypeId[i] + "")
          }
          this.settings.typeForm.defaultWithdrawTypeId = withdrawTypes
        } else {
          this.settings.typeForm.defaultWithdrawTypeId = []
        }
        this.$nextTick(() => {
          this.settings.userName = row.userName
          this.settings.payFeeFix = row.payFeeFix
          this.settings.payFeeRate = row.payFeeRate
          this.settings.withdrawFeeFix = row.withdrawFeeFix
          this.settings.withdrawFeeRate = row.withdrawFeeRate
          this.settings.agentForm.agentId = row.agentId
          this.settings.appId = row.appId
          this.settings.confirmForm.confirmStatus = row.confirmStatus
          this.settings.confirmForm.confirmMin = Number(row.confirmMin).toFixed(0)
          this.settings.confirmForm.confirmMax = Number(row.confirmMax).toFixed(0)
          this.settings.ipForm.ip = row.whiteIps
        })
      },
      closeSettingsDialog() {
        this.settings.activeName = 'first'
        this.settings.visible = false
        this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
        this.commonCloseDialog(this.settings.creditForm, 'creditForm')
        this.commonCloseDialog(this.settings.agentForm, 'agentForm')
        this.commonCloseDialog(this.settings.typeForm, 'typeForm')
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
                that.allCountry.push(item.value)
              })
            }
          })
        })
      },
      queryUserTypeOptions() {
        let that = this
        return new Promise(resolve => {
          queryUserTypePageList({type: 'user_type'}).then(function (res) {
            if (res.data.code === 0) {
              that.allUserTypeOptions = []
              res.data.data.forEach((item, index) => {
                that.userTypeOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
                that.allUserTypeOptions.push(item.value)
              })
            }
          })
        })
      },
      addCpinfo() {
        this.dialog.visible = true
      },
      beforeCloseDialog() {
        this.$refs.ruleForm.resetFields()
      },
      handleSizeChange(val) {
        this.page.pageSize = val
        this.refresh()
      },
      handleSizeChange2(val) {
        this.page2.pageSize = val
        this.search2();
      },
      filterHandler(value, row, column) {
        // const property = column['property'];
        // return String(row[property]) === String(value);
        return value;
      },
      resetGoogleSecret(row) {
        const that = this
        resetGoogleSecret({appId: row.appId}).then((response) => {
          if (response.data.code === 0) {
            that.$message(response.data.msg)
          }
        })
      },
      changeWhiteIps(row) {
        const that = this
        changeWhiteIps({appId: row.appId, whiteIps: row.whiteIps}).then((response) => {
          if (response.data.code === 0) {
            that.$message(response.data.msg)
          }
        })
      }, setConfirmMin() {
        this.settings.confirmForm.confirmMin = 'min'
      },
      setConfirmMax() {
        this.settings.confirmForm.confirmMax = 'max'
      },
      statisticsCpInfo() {

      },
      getSummaries(param) {
        const {columns, data} = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index < 5 || index > 7) {
            return ''
          }
          const values = data.map(item => Number(item[column.property]));

          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] = sums[index];
          } else {
            sums[index] = 'N/A';
          }
        });
        return sums;
      },
      exportCpInfo() {
        const that = this;
        that.fileList = []

        let userNames = []

        const re = /^,|| $/;
        const str = String(that.searchForm.userName);
        if (str !== '') {
          if (re.test(str)) {
            userNames = str.split(re)
          } else {
            userNames = []
            userNames.push(str)
          }
        }
        window.location.href = encodeURI("/cpinfo/exportCpInfo?" +
          "userNames=" + userNames +
          "&agentId=" + that.searchForm.agentId +
          "&status=" + that.searchForm.status +
          "&canOver=" + that.searchForm.canOver +
          "&country=" + that.searchForm.country)
      },

      queryValidPayLogList() {
        var that = this
        queryValidPayLogList({
          appId: that.settlementForm.appId
        }).then((resp) => {
          const code = resp.data.code
          const msg = resp.data.msg
          const data = resp.data.data

          if (code === 0) {

            for (const datum of data) {
              datum.amount = toDecimalString(datum.amount)
              for (const datumElement of datum.list
                ) {
                datumElement.amount = toDecimalString(datumElement.amount)
              }
            }
            that.validDayList = data
          }
        })
      },
      openSettlementRemainDrawer(index, row) {
        this.validDayList.appId = row.appId
        this.settlementForm.appId = row.appId
        this.queryValidPayLogList()
        this.settleRemainDrawer = true
      },
      closeSettlementRemainDrawer() {
        this.settleRemainDrawer = false
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      openSettlementDialog(row) {
        this.settlementDialogVisible = true
        this.settlementForm.payDay = row.payDay
        if (row.payTypeId !== undefined) {
          this.settlementForm.payTypeId = row.payTypeId
          this.settlementForm.payTypeName = row.payTypeName
        }
        this.settlementForm.amount = row.amount
      },
      closeSettlementDialog() {
        this.settlementForm = {}
        this.settlementDialogVisible = false
      },
      settlementPayLog() {
        this.settling = true

        const that = this
        settlementPayLog(this.settlementForm).then((resp) => {
          const code = resp.data.code
          const msg = resp.data.msg
          const data = resp.data.data

          if (code === 0) {
            that.$message(msg)
            that.queryValidPayLogList()
            that.settlementDialogVisible = false
          }
          that.$message(msg)
          that.settling = false
        })
      },
      handleClick(tab, event) {
        this.searchForm.country = this.countryOptions[tab.index].value
        this.refresh()
      },
      changeList(item) {
        this.country = item.value
      },
      queryCurrencyPageList() {
        let that = this
        return new Promise(resolve => {
          queryCurrencyPageList({type: 'currency'}).then(function (res) {
            if (res.data.code === 0) {
              that.currencyOptions = []
              res.data.data.forEach((item, index) => {
                that.currencyOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
    },
  }
</script>

<style>

  .button-new-tag {
    height: 32px;
    line-height: 30px;
    margin: 5px 0px;
    padding-top: 0;
    padding-bottom: 0;
  }

  .input-new-tag {
    width: 90px;
    margin: 5px 10px;
    vertical-align: bottom;
  }

  .el-dialog__body {
    padding: 5px 20px;
  }

  .el-upload-dragger {
    width: 146px;
    height: 146px;
  }

  .el-tooltip__popper{
    max-width:20%
  }

  .el-tag {
    margin-bottom: 10px;
  }

  .el-scrollbar__view {
    padding-bottom: 20px;
  }

  .quill-editor-toolbar {
    width: 100%;
  }

  .quill-editor {
    height: 150px;
    width: 100%;
  }
</style>




