import Axios from "@/utils/request"  // axios
import qs from  "qs"

export function sendNotifyMsg(datas) {
  return Axios({
    url: "/withdrawlog/sendNotifyMsg",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}


export function queryWithdrawLogPageList(datas) {
  return Axios({
    url: "/withdrawlog/queryWithdrawLogPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function changeWithdrawLogStatus(datas) {
  return Axios({
    url: "/withdrawlog/changeWithdrawLogStatus",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function addAdminWithdrawLogColombia(header,datas) {
  return Axios({
    url: "/withdrawlog/addAdminWithdrawLogColombia",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded', //form,
      'withdrawToken': header
    },
    data: qs.stringify(datas)
  })
}

export function updateWithdrawLogConfirmStatusPass(datas) {
  return Axios({
    url: "/withdrawlog/updateWithdrawLogConfirmStatusPass",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function updateWithdrawLogConfirmStatusReject(datas) {
  return Axios({
    url: "/withdrawlog/updateWithdrawLogConfirmStatusReject",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function getWithdrawLogToken(datas) {
  return Axios({
    url: "/withdrawlog/getWithdrawLogToken",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

export function manualUploadCallback(datas) {
  return Axios({
    url: "/withdrawlog/manualUploadCallback",
    method: "post",
    data: datas
  })
}

// yesbank 
export function yesmanualUploadCallback(datas) {
  return Axios({
    url: "/withdrawlog/yesmanualUploadCallback",
    method: "post",
    data: datas
  })
}

export function yesmanualSubmit(datas) {
  return Axios({
    url: "/withdrawlog/yesmanualSubmit",
    method: "post",
    headers: {
      'Content-Type': 'application/json' //form
    },
    data: JSON.stringify(datas)
  })
}



