<template>
  <div id="app" >
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="Id" prop="appId">
              <el-input v-model="searchForm.appId" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <el-button size="medium" icon="el-icon-search" type="primary" @click="search"></el-button>
          <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
        </el-form-item>

      </el-form>
      <div id="grid" style="width: 100%">
        <el-table
          :data="tableData"
          style="width: 100%"
          size="mini"
        >
          <el-table-column
            prop="appId"
            label="Id"
            align="center">
          </el-table-column>

          <el-table-column
            prop="totalPay"
            label=""
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="totalPayFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="totalPayNum"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="totalWithdraw"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="totalWithdrawFee"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="totalWithdrawNum"
            label=""
            align="center">
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total" :current-page="searchForm.page" @current-change="pageChange">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
    import {queryCpInfoStatisticsPageList} from "../../api/cpinfo";
    import {formatAmount} from "../../statics/custom";

    export default {
        name: "cpInfoTotalStatistics",
      data() {
        return {
          fullscreenLoading: false,
          createTime: [],
          searchForm: {
            id: null,
            appId: '',
            statDay: '',
            page: 1,
            limit: 10,
            rowIndex: 0
          },
          tableData: [],
          total: 0,
          pickerOptionsStartCreate: {},
          pickerOptionsEndCreate: {
            disabledDate(time) {
              let startTime;
              if (this.pickerOptionsStartCreate) {
                // 
                startTime = new Date(this.pickerOptionsStartCreate).valueOf() - 8.64e7 + 1;
              } else {
                startTime = Date.now() - 8.64e7;
              }
              return time && time.valueOf() < startTime
            }
          },
          notifyBtnSwitch: 'disabled',
          formLabelWidth: 120,
          sending: false
        }
      },
      created() {
        this.queryCpInfoStatisticsPageList()
      },
      methods: {
        queryCpInfoStatisticsPageList() {
          var that = this
          this.fullscreenLoading = true
          return new Promise(resolve => {
            queryCpInfoStatisticsPageList(that.searchForm).then(function (response) {
              if (response.data.code === 0) {

                var list = response.data.data
                that.total = response.data.count

                var tableData = []
                for (var cpinfo of list) {
                  cpinfo.totalPay = formatAmount(cpinfo.totalPay)
                  cpinfo.totalPayFee = formatAmount(cpinfo.totalPayFee)
                  cpinfo.totalWithdraw= formatAmount(cpinfo.totalWithdraw)
                  cpinfo.totalWithdrawFee = formatAmount(cpinfo.totalWithdrawFee)

                  tableData.push(cpinfo)
                }
                that.tableData = tableData
                that.fullscreenLoading = false
              }
            })

          });
        },
        search() {
          this.searchForm.page = 1
          this.queryCpInfoStatisticsPageList()
        },
        resetForm(formName) {
          this.$refs[formName].resetFields()
          this.createTime = []
        },
        pageChange(cur) {
          this.searchForm.page = cur
          this.queryCpInfoStatisticsPageList()
        }
      }
    }
</script>

<style scoped>

</style>



