<template>
  <button type="button" class="one-button " :class="[`one-button-${type}`,{
    'is-plain':plain,
    'is-round':round,
    'is-circle':circle,
    'is-disabled':disabled,
    'el-button--mini': size === 'mini',
    'el-button--medium': size === 'medium',
  }]"
          v-if="hasPermission(perms)"
          @click="handleClick"
          :disabled="disabled"
  >
    <i v-if="icon" :class="`${icon}`"></i>
    <!-- span -->
    <span v-if="buttonName !== ''"><slot>{{buttonName}}</slot></span>
  </button>
</template>

<script>

export default {
  name: 'oneButton',
  // propsstringtype
  props: {
    type: {
      type: String,
      // default
      default: 'defalut'
    },
    plain: {
      type: Boolean,
      default: false
    },
    round: {
      type: Boolean,
      default: false
    },
    circle: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    perms: {  // 
      type: String,
      default: null
    },
    size: {  // 
      type: String,
      default: null
    },
  },
  data(){
    return{
      buttonName: ''
    }
  },
  created () {
    // 
  },
  methods: {
    // 
    handleClick (e) {
      this.$emit('click', e)
    },
    hasPermission (perms) {
      let hasPermission = false
      let permissions = JSON.parse(window.sessionStorage.getItem('permissionList'))
      var permsArr = perms.split('-');
      var menuFather = permsArr[0]
      var menuSon = permsArr[1]
      var btn = permsArr[2]
      for(let i=0; i<permissions.length; i++) {
        if(permissions[i].pValue === menuFather) {
          var children = permissions[i].children;
          for (let j = 0; j < children.length; j++) {
            if (children[j].pValue === menuSon){
              var sons = children[j].children;
              for (let k = 0; k < sons.length; k++) {
                if (sons[k].pValue === perms){
                  this.buttonName = sons[k].permissName
                  hasPermission = true;
                  break
                }
              }
            }
          }
        }
      }
      return hasPermission
    }
  }
}
</script>

<style lang="scss" scoped>
.one-button{
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #ffffff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  //
  -moz-user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  padding: 10px 20px;
  font-size: 14px;
  border-radius: 4px;
  &:hover,
  &:focus{
    color: #409eff;
    border-color: #c6e2ff;
    background-color: #ecf5ff;
  }
}
.one-button-primary{
  color:#fff;
  background-color: #409eff;
  border-color: #409eff;
  &:hover,
  &:focus{
    background: #66b1ff;
    background-color: #66b1ff;
    color: #fff;
  }
}
.one-button-success{
  color:#fff;
  background-color: #67c23a;
  border-color: #67c23a;
  &:hover,
  &:focus{
    background: #85ce61;
    background-color: #85ce61;
    color: #fff;
  }
}
.one-button-info{
  color:#fff;
  background-color: #909399;
  border-color: #909399;
  &:hover,
  &:focus{
    background: #a6a9ad;
    background-color: #a6a9ad;
    color: #fff;
  }
}
.one-button-warning{
  color:#fff;
  background-color: #e6a23c;
  border-color: #e6a23c;
  &:hover,
  &:focus{
    background: #ebb563;
    background-color: #ebb563;
    color: #fff;
  }
}
.one-button-danger{
  color:#fff;
  background-color: #f56c6c;
  border-color: #f56c6c;
  &:hover,
  &:focus{
    background: #f78989;
    background-color: #f78989;
    color: #fff;
  }
}
// 
.one-button.is-plain{
  &:hover,
  &:focus{
    background: #fff;
    border-color: #489eff;
    color: #409eff;
  }
}
.one-button-primary.is-plain{
  color: #409eff;
  background: #ecf5ff;
  &:hover,
  &:focus{
    background: #409eff;
    border-color: #409eff;
    color: #fff;
  }
}
.one-button-success.is-plain{
  color: #67c23a;
  background: #c2e7b0;
  &:hover,
  &:focus{
    background: #67c23a;
    border-color: #67c23a;
    color: #fff;
  }
}
.one-button-info.is-plain{
  color: #909399;
  background: #d3d4d6;
  &:hover,
  &:focus{
    background: #909399;
    border-color: #909399;
    color: #fff;
  }
}
.one-button-warning.is-plain{
  color: #e6a23c;
  background: #f5dab1;
  &:hover,
  &:focus{
    background: #e6a23c;
    border-color: #e6a23c;
    color: #fff;
  }
}
.one-button-danger.is-plain{
  color: #f56c6c;
  background: #fbc4c4;
  &:hover,
  &:focus{
    background: #f56c6c;
    border-color: #f56c6c;
    color: #fff;
  }
}
// round
.one-button.is-round{
  border-radius: 20px;
  padding: 12px 23px;
}
// circle
.one-button.is-circle{
  border-radius: 50%;
  padding: 12px;
}
// icon
.one-button [class*=one-icon-]+span{
  margin-left: 5px;
}
// disabled
.one-button.is-disabled{
  cursor: no-drop;
}

.el-button--mini, .el-button--mini.is-round {
  padding: 7px 15px;
}

.el-button--mini, .el-button--small {
  font-size: 12px;
  border-radius: 3px;
}

</style>




