import Axios from "@/utils/request"  // axios
import qs from "qs"

export function addWithdrawType(datas) {
  return Axios({
    url: "/withdrawtype/addWithdrawType",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //form
    },
    data: qs.stringify(datas)
  })
}

