<template>
  <div id="app">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="id">
              <el-input v-model="searchForm.id" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="Id" prop="appId">
              <el-input v-model="searchForm.appId" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="orderId">
              <el-input v-model="searchForm.orderId" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="type">
              <el-select v-model="searchForm.type" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="sceneInfo">
              <el-select v-model="searchForm.sceneInfo" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
            <el-form-item label="" prop="money">
              <el-input v-model="searchForm.money" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :xl="4" :lg="8" :md="8" :sm="12" :xs="24" class="form-search">
            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker
                  v-model="early"
                  type="datetimerange"
                  :clearable="true"
                  :default-time="['00:00:00', '23:59:59']"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder=""
                  :picker-options="pickerOptionsStart">
                </el-date-picker>
              </el-col>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <one-button  icon="el-icon-search" type="primary" @click="search" perms="log-moneyLogList-search"></one-button>
          <el-button  icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
        </el-form-item>

      </el-form>
      <!--            <div style="margin-left: 30px">-->
      <!--                <el-row :gutter="20">-->
      <!--                    <el-col :span="3"><div class="grid-content bg-purple">-->
      <!--                        <span class="show-statictics-span">-->
      <!--                            {{}}-->
      <!--                        </span>-->
      <!--                    </div></el-col>-->
      <!--                    <el-col :span="3"><div class="grid-content bg-purple">-->
      <!--                        <span class="show-statictics-span">-->
      <!--                            {{}}-->
      <!--                        </span>-->
      <!--                    </div></el-col>-->
      <!--                    <el-col :span="3"><div class="grid-content bg-purple">-->
      <!--                        <span class="show-statictics-span">-->
      <!--                            {{}}-->
      <!--                        </span>-->
      <!--                    </div></el-col>-->
      <!--                    <el-col :span="3"><div class="grid-content bg-purple">-->
      <!--                        <span class="show-statictics-span">-->
      <!--                            {{}}-->
      <!--                        </span>-->
      <!--                    </div></el-col>-->
      <!--                </el-row>-->
      <!--            </div>-->
      <div id="grid" style="width: 100%">
        <el-table
          :data="tableData"
          style="width: 100%"
          size="mini"
        >
<!--          <el-table-column type="expand">-->
<!--            <template slot-scope="props">-->
<!--              <el-form label-position="left" inline class="demo-table-expand">-->
<!--                &lt;!&ndash;                            <el-form-item label="">&ndash;&gt;-->
<!--                &lt;!&ndash;                                <span>{{ props.row.callbackUrl }}</span>&ndash;&gt;-->
<!--                &lt;!&ndash;                            </el-form-item>&ndash;&gt;-->
<!--                &lt;!&ndash;                            <el-form-item label="">&ndash;&gt;-->
<!--                &lt;!&ndash;                                <span>{{ props.row.notifyUrl }}</span>&ndash;&gt;-->
<!--                &lt;!&ndash;                            </el-form-item>&ndash;&gt;-->
<!--                &lt;!&ndash;                            <el-form-item label="">&ndash;&gt;-->
<!--                &lt;!&ndash;                                <span>{{ props.row.description }}</span>&ndash;&gt;-->
<!--                &lt;!&ndash;                            </el-form-item>&ndash;&gt;-->
<!--              </el-form>-->
<!--            </template>-->
<!--          </el-table-column>-->
          <el-table-column
            prop="id"
            label="">
          </el-table-column>

          <el-table-column
            prop="appId"
            label="Id">
          </el-table-column>
          <el-table-column
            prop="type"
            label="">
          </el-table-column>
          <el-table-column
            prop="sceneInfo"
            label="">
          </el-table-column>
          <el-table-column
            prop="frontMoney"
            label="">
          </el-table-column>
          <el-table-column
            prop="money"
            label="">
          </el-table-column>
          <el-table-column
            prop="queenMoney"
            label="">
          </el-table-column>

          <el-table-column
            prop="details"
            label="">
          </el-table-column>
          <el-table-column
            prop="notes"
            label="">
          </el-table-column>
          <el-table-column
            prop="orderId"
            label="">
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="">
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total" :current-page="searchForm.page" @current-change="pageChange">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
  import {queryMoneyLogPageList} from "../../api/moneylog";
  import {formatPayType, formatAmount, dateFormate} from "../../statics/custom"
  import OneButton from "../../components/cButton";
5
  export default {
    name: "moneyLogList",
    components: {OneButton},
    data() {
      return {
        fullscreenLoading: false,
        centerDialogVisible: false,
        innerVisible: false,
        sendNotifyDrawerVisable: false,
        direction: 'rtl',
        password: '',
        verifyCode: '',
        notifyId: '',
        early: [],
        countryOptions: [],
        payTypeOptions: [],
        searchFormOri: {},
        searchForm: {
          id: null,
          appId: '',
          orderId: '',
          type: '',
          sceneInfo: '',
          money: '',
          startCreateTime: '',
          endCreateTime: '',
          page: 1,
          limit: 10,
          rowIndex: 0
        },
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {},
          settings: {
            visible: false,
            activeName: 'first',
            userName: '',
            appId: 0,
            payFix: 0,
            payRate: 0,
            withdrawFix: 0,
            withdrawRate: 0,
            balanceForm: {
              password: '',
              Verifycode: '',
              remain: 0,
              Notes: ''
            }
          },
          rules: {
            UserName: [
              {required: true, message: '', trigger: 'blur'},
              {min: 3, max: 50, message: ' 3  50 ', trigger: 'blur'}
            ]
          },
        },
        tableData: [],
        total: 0,
        dateStart: '', // 
        dateEnd: '', // 
        pickerOptionsStart: {},
        pickerOptionsEnd: {
          disabledDate(time) {
            if (this.dateStart) {
              // 
              var startTime = new Date(this.dateStart).valueOf() - 8.64e7 + 1;
            } else {
              var startTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startTime
          }
        },
        notifyBtnSwitch: 'disabled',
        sendNotifyMsgForm: {
          id: '',
          url: '',
          notifyData: '',
          notifyResp: '',
          notifyAnalyze: ''
        },
        formLabelWidth: 120,
        sending: false
      }
    },
    created() {
      this.queryMoneyLogPageList()
    },
    methods: {
      queryMoneyLogPageList() {
        var that = this
        this.fullscreenLoading = true
        this.searchForm.startCreateTime = this.early === "" ? "" : this.early[0]
        this.searchForm.endCreateTime = this.early === "" ? "" : this.early[1]
        return new Promise(resolve => {
          queryMoneyLogPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {

              var list = response.data.data
              that.total = response.data.count

              var tableData = []
              for (var money of list) {

                var type = money.type
                type = type === 1 ? '' : ''
                money.type = type

                var sceneInfo = money.sceneInfo
                sceneInfo = sceneInfo === 1 ? '' : sceneInfo === 2 ? '' : sceneInfo === 3 ? '' : ''
                money.sceneInfo = sceneInfo

                money.frontMoney = formatAmount(money.frontMoney)
                money.money = formatAmount(money.money)
                money.queenMoney = formatAmount(money.queenMoney)

                money.createTime = dateFormate(money.createTime)

                tableData.push(money)
              }
              that.tableData = tableData
              that.fullscreenLoading = false
            }
          })

        });
      },
      search() {
        this.searchForm.page = 1
        this.searchForm.limit = 10
        this.queryMoneyLogPageList()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.dateStart = ''
        this.dateEnd = ''
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryMoneyLogPageList()
      }
    }
  }
</script>

<style scoped>

</style>



