<template>
  <div id="app">

    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row>

      </el-row>
      <el-row>
        <one-button type="primary" @click="search" perms="system-permissionList-search"></one-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-row>
    </el-form>
    <div id="grid" style="width: 100%">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="small"
        row-key="id"
      >
        <el-table-column
          align="center"
          width="60"
        >
        </el-table-column>
        <el-table-column
          prop="permissName"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          align="center">
          <template slot-scope="scope">
            <span class="settlement-status settlement-status-success" v-if="scope.row.pType === '1'"></span>
            <span class="settlement-status settlement-status-initial" v-if="scope.row.pType === '2'"></span>
            <span class="settlement-status settlement-status-reject" v-if="scope.row.pType === '3'"></span>
          </template>
        </el-table-column>
        <el-table-column
          prop="pValue"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="sort"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          prop="upName"
          label=""
          align="center">
        </el-table-column>
        <el-table-column
          label=""
          min-width="120px">
          <template slot-scope="scope">
            <one-button v-if="scope.row.pType === '1' || scope.row.pType === '2'" size="mini"
                        @click="addPermission(scope.$index, scope.row)" perms="system-permissionList-add"></one-button>
            <one-button size="mini" @click="handleEdit(scope.$index, scope.row)"
                        perms="system-permissionList-edit"></one-button>
            <one-button size="mini" type="danger" @click="deletePermission(scope.$index, scope.row)"
                        perms="system-permissionList-del"></one-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="this.dialog.isInsert ? '' : ''" :visible.sync="dialog.visible" width="30%"
               @closed="closeDialog"
    >
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules" size="mini">
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="permissName">
          <el-input v-model="dialog.form.permissName"></el-input>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="pValue">
          <el-input v-model="dialog.form.pValue"></el-input>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="sort">
          <el-input v-model="dialog.form.sort"></el-input>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="pType">
          <el-select v-model="dialog.form.pType" placeholder="">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="upId">
          <el-input disabled v-model="dialog.form.upId"></el-input>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="pIcon">
          <el-input v-model="dialog.form.pIcon"></el-input>
        </el-form-item>
        <el-form-item class="is-required" label="" :label-width="dialog.formLabelWidth" prop="pLevel">
          <el-input v-model="dialog.form.pLevel"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {queryPayLogPageList} from "../../api/paylog";
import {dateFormate, formatPayType, toDecimalString} from "../../statics/custom";
import {
  addPermission,
  deletePermission,
  pageList,
  queryPermissionTreeList,
  updatePermission
} from "../../api/permission";
import OneButton from "../../components/cButton";
import {addCpInfo, updateCpInfo} from "../../api/cpinfo";

export default {
  name: "permissionList",
  components: {OneButton},
  data() {
    return {
      searchForm: {
        page: 0,
        limit: 10,
      },
      tableData: [],
      dialog: {
        isInsert: true,
        visible: false,
        formLabelWidth: '150px',
        form: {
          id: null,
          permissName: '',
          pValue: '',
          sort: null,
          upId: null,
          pType: '',
          pIcon: '',
          pLevel: ''
        },
        rules: {}
      },
      typeOptions: [
        {
          label: '',
          value: '1'
        },
        {
          label: '',
          value: '2'
        },
        {
          label: '',
          value: '3'
        },
      ]
    }
  },
  created() {
    this.pageList()
  },
  methods: {
    pageList() {
      const that = this;
      that.fullscreenLoading = true
      return new Promise(resolve => {
        pageList(that.searchForm).then(function (response) {
          if (response.data.code === 0) {
            const tableData = [];

            const statistics = response.data.data.statistics;
            const list = response.data.data.list;

            for (let i = 0; i < list.length; i++) {
              var listElement = list[i];
              if (listElement.length > 0) {
                listElement.hasChildren = true

                for (let j = 0; j < listElement.length; j++) {
                  var listElementElement = listElement[j];
                  if (listElementElement.length > 0) {
                    listElementElement.hasChildren = true
                  }
                }
              }
            }
            that.tableData = list
            that.total = statistics.totalAllNum
            that.fullscreenLoading = false
          }
        })
      });
    },
    search() {
      this.pageList()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    commonCloseDialog(obj, formName) {
      // this.$nextTick(() => {
      //   // this.$refs.formName.clearValidate()
      //   this.$refs[formName].resetFields()
      // })
      Object.keys(obj).forEach(key => {
        let type = typeof obj[key]
        if (type === 'number') {
          obj[key] = 0
        }
        if (type === 'string') {
          obj[key] = ''
        }
      })
      // this.resetForm(formName)
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true
      this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    addPermission(index, row) {
      console.log(row)
      this.dialog.form.upId = row.id
      this.dialog.visible = true
    },
    deletePermission(index, row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const that = this
        deletePermission({id: row.id}).then((resp) => {
          const code = resp.data.code
          const msg = resp.data.msg
          const data = resp.data.data

          if (code === 0) {
            that.pageList()
          }
          that.$message(msg)
        })
      })
    },
    handleEdit(index, row) {
      let that = this
      Object.keys(row).forEach(key => {
        if (that.dialog.form.hasOwnProperty(key)) {
          that.dialog.form[key] = row[key]
        }
      })
      this.dialog.visible = true
      this.dialog.isInsert = false
    },
    submitForm(formName) {
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (formName === 'ruleForm') {
            that.savePermission()
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    savePermission() {
      let str = this.dialog.isInsert ? '' : ''
      let that = this
      this.$confirm('' + str + '?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        (that.dialog.isInsert ? addPermission(that.dialog.form) : updatePermission(that.dialog.form)).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: str + '',
              type: 'success'
            })
          } else {
            str = str + '' + response.data.msg
            that.$message.error(str);
          }
          that.pageList()
          that.closeDialog()
        })
      })
    },
    queryRoleMenuTreeList() {
      const that = this
      return new Promise(resolve => {
        queryPermissionTreeList().then(function (response) {
          if (response.data.code === 0) {
            const data = response.data.data

          }
        })

      });
    },
  }
}
</script>

<style scoped>

</style>



