<template>
<div id="app">
  <div id="grid" style="width: 100%">
    <el-table
      :data="tableData"
      style="width: 100%"
      size="small"
      ref="filterTable">
      <el-table-column
        prop="id"
        label="id"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="tranId"
        label="tranId"
        align="center">
      </el-table-column>
      <el-table-column
        prop="accountId"
        label="accountId"
        align="center">
      </el-table-column>
      <el-table-column
        prop="operator"
        label="operator"
        align="center"
        :filters="[{text: '1', value: 1}, {text: '-1', value: -1},{}]"
        :filter-method="filterHandler"
      >
      </el-table-column>
      <el-table-column
        prop="Amount"
        label="Amount"
        align="center">
      </el-table-column>
      <el-table-column
        prop="detail0"
        label="detail0"
        align="center">
      </el-table-column>
      <el-table-column
        prop="detail1"
        label="detail1"
        align="center">
      </el-table-column>
      <el-table-column
        prop="detail2"
        label="detail2"
        align="center">
      </el-table-column>
    </el-table>
  </div>
</div>
</template>

<script>




import axios from "axios";
import {alquimia} from "../../api/alquimia";
import {toDecimalString} from "../../statics/custom";

export default {
  name: "AlquimiaPayLog",
  created() {
      this.queryAlquimiaPayLog()
    },
  data(){
    return{
      tableData: []
    }
  },
  methods:{
    queryAlquimiaPayLog(){
      const that = this
      alquimia().then((resp)=>{
        console.log(resp.data)
        const data = resp.data
        for (const datum of data) {
          datum.Amount = toDecimalString(datum.Amount)


        }



        that.tableData  =resp.data
      })

    },
    resetDateFilter() {
      this.$refs.filterTable.clearFilter('date');
    },
    clearFilter() {
      this.$refs.filterTable.clearFilter();
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    }
  }
}
</script>

<style scoped>

</style>



