<template>
  <div id="app">
    <template>
      <el-form :model="searchForm" ref="searchForm" label-width="90px">
        <el-row>
          <el-col :span="4" class="form-search">
            <el-form-item label="" prop="payTypeId">
              <el-select v-model="searchForm.payTypeId" placeholder="" filterable>
                <el-option
                  v-for="(item) in this.payTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                  <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item class="search-button">
          <el-button type="primary" @click="search"></el-button>
          <el-button @click="resetForm('searchForm')"></el-button>
        </el-form-item>
      </el-form>
      <div id="grid" style="width: 100%">
        <el-table
          :data="payTypeStaticticsTableData "
          style="width: 100%"
          size="mini">
          <!--                <el-table-column type="expand">-->
          <!--                    <template slot-scope="props">-->
          <!--                        <el-form label-position="left" inline class="demo-table-expand">-->
          <!--                            <el-form-item label="">-->
          <!--                                <span>{{ props.row.callbackUrl }}</span>-->
          <!--                            </el-form-item>-->
          <!--                        </el-form>-->
          <!--                    </template>-->
          <!--                </el-table-column>-->
          <el-table-column
            prop="payTypeId"
            label="id">
          </el-table-column>
          <el-table-column
            prop="name"
            label="">
          </el-table-column>
          <el-table-column
            prop="payAmount"
            label="">
          </el-table-column>
          <el-table-column
            prop="payFee"
            label="">
          </el-table-column>
          <el-table-column
            prop="payNum"
            label="">
          </el-table-column>
          <el-table-column
            prop="withdrawAmount"
            label="">
          </el-table-column>
          <el-table-column
            prop="withdrawFee"
            label="">
          </el-table-column>
          <el-table-column
            prop="withdrawNum"
            label="">
          </el-table-column>
          <el-table-column
            prop="remain"
            label="">
          </el-table-column>
        </el-table>
      </div>
      <div style="text-align: center;margin-top:5px;">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :total="total" :current-page="searchForm.page" @current-change="pageChange">
        </el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
  import {formatAmount} from "../../statics/custom";
  import {payTypeStatictics, queryPayTypeSelectList} from "../../api/paytype";

  export default {
        name: "channelTotalStatistics",
      data() {
        return {
          fullscreenLoading: true,
          searchForm: {
            payTypeId: '',
            page: 1,
            limit: 10
          },
          payTypeStaticticsTableData: [],
          total: 0,
          payTypeOptions: []
        }
      },
      async created() {
        this.queryPayLogStaticticsByPayType()
        this.queryAllPaytypeList()
      },
      methods: {
        queryPayLogStaticticsByPayType(){
          var that = this
          payTypeStatictics(that.searchForm).then(function (response) {
            if (response.data.code === 0) {
              const data = response.data.data;
              that.total = response.data.count

              that.payTypeStaticticsTableData = []
              for (var payTypeStatictics of data) {
                payTypeStatictics.payAmount = formatAmount(payTypeStatictics.payAmount)
                payTypeStatictics.payFee = formatAmount(payTypeStatictics.payFee)
                payTypeStatictics.withdrawAmount = formatAmount(payTypeStatictics.withdrawAmount)
                payTypeStatictics.withdrawFee = formatAmount(payTypeStatictics.withdrawFee)
                payTypeStatictics.remain = formatAmount(payTypeStatictics.remain)
                that.payTypeStaticticsTableData.push(payTypeStatictics)
              }
            }
          })

        },
        pageChange(cur){
          this.searchForm.page = cur
          this.queryPayLogStaticticsByPayType()
        },
        queryAllPaytypeList() {
          var that = this
          queryPayTypeSelectList().then(function (res) {
            if (res.data.code === 0) {
              that.payTypeOptions = []
              var data = res.data.data
              for (var paytype of data) {
                that.payTypeOptions.push({
                  label: paytype.name,
                  value: paytype.id
                })
              }
            }
          })
        },
        search() {
          this.queryPayLogStaticticsByPayType()
        },
        resetForm(formName){
          this.$refs[formName].resetFields()
        }
      }
    }
</script>

<style scoped>

</style>



